import React, { useState } from 'react';
import { toast } from 'react-toastify';
import Modal from 'react-modal';
import api from '../../../../util/api';
import {Row} from 'react-bootstrap';
import {
    ContainNav,
    ContainContent,
    ColContent,
    ButtonEnviar,
    FormGrup,
    FormLabel,
    FormControl,
    ClosedModalButton,
} from './styles';

function RegisterContract({ openModalRegisterContract, setModalRegisterContract, reload, setReload }) {

    const [contractorName, setContractorName] = useState('');

    const [contractor, setContractor] = useState('');

    const [hired, setHired] = useState('');

    const [signDate, setSignDate] = useState('');

    const [description, setDescription] = useState('');

    const handleInputContractorNameChange = data => {
        setContractorName(data.target.value);
    };

    const handleInputContractorChange = data => {
        setContractor(data.target.value);
    };

    const handleInputHiredChange = data => {
        setHired(data.target.value);
    };

    const handleInputSignDateChange = data => {
        setSignDate(data.target.value);

        console.log(data.target.value);
        console.log(signDate);
    };

    const handleInputDescriptionChange = data => {
        setDescription(data.target.value);
    };

    const clearAll = () => {
        setContractorName('');
        setContractor('');
        setHired('');
        setSignDate('');
        setDescription('');
    };


    const handleSubmit = async () => {
        try {
            if (!contractor || !hired || !signDate) {
                return toast.error('Preencha os campos obrigatórios!');
            };

            const fisrtP = signDate[0];
            if (fisrtP === '0' || signDate.length < 10) {
                return toast.error("Preencha a data corretamente");
            };

            await api.post('contract/register', {
                name: contractorName,
                contractor,
                hired,
                date: signDate,
                description,
            });

            clearAll();

            setReload(reload + 1);

            toast.success('Contrato enviado com sucesso');
        } catch{
            toast.error('Erro ao solicitar o cadastro, por favor contate um administrador');
        }
    };

    return (
        <Modal
            style={
                {
                    overlay: {
                        backgroundColor: 'rgba(25,25,25, .8)',
                    },
                    content: {
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                        padding: '0',
                        top: '0',
                        bottom: '0',
                        left: '0',
                        right: '0',
                        border: 'none',
                        backgroundColor: 'rgba(25,25,25, .0)',
                        margin: '0',
                        borderRadius: '10px',
                        height: '100vh',
                        width: '100vw',
                    },
                }
            }

            isOpen={openModalRegisterContract} onRequestClose={() => {
                setModalRegisterContract(false)
            }}
        >
            <ContainNav>
                <ContainContent>
                    <ColContent className="col-md-7 col-sm-7 col--12" >
                        <Row style={{width:'100%'}}>
                            <div className="py-2 px-4 col-md-10 col-sm-10 col-10 d-flex justify-content-start">
                                <h2>Cadastrar Contrato</h2>
                            </div>
                            <div className="p-2 col-md-2 col-sm-2 col-2 d-flex justify-content-end">
                                <ClosedModalButton onClick={() => setModalRegisterContract(false)}>X</ClosedModalButton>
                            </div>
                        </Row>
                        <ContainContent>
                            <FormGrup className="col-md-12 col-12" >
                                <FormLabel>Nome do contrato: *</FormLabel>
                                <FormControl
                                    value={contractorName}
                                    onChange={handleInputContractorNameChange}
                                    placeholder="Digite o nome do contrato"
                                    type="text"
                                />
                            </FormGrup>
                            <FormGrup className="col-md-6 col-12" >
                                <FormLabel>Contratante: *</FormLabel>
                                <FormControl
                                    value={contractor}
                                    onChange={handleInputContractorChange}
                                    placeholder="Digite o nome"
                                    type="text"
                                />
                            </FormGrup>
                            <FormGrup className="col-md-6 col-12"  >
                                <FormLabel>Contratada: *</FormLabel>
                                <FormControl
                                    value={hired}
                                    onChange={handleInputHiredChange}
                                    placeholder="Digite o nome"
                                    type="text"
                                />
                            </FormGrup>
                        </ContainContent>
                        <ContainContent>
                            <FormGrup className="col-md-12 col-12" >
                                <FormLabel className="col-lg-6 col-md-7" >Data de assinatura:</FormLabel>
                                <FormControl
                                    value={signDate}
                                    onChange={handleInputSignDateChange}
                                    className="col-lg-12 col-md-12 col-12"
                                    type="date"
                                />
                            </FormGrup>
                        </ContainContent>
                        {/* <ContainContent style={{ justifyContent: 'start' }}>
                            <FormGrup className="col-md-9 col-12" >
                                <FormLabel>Arquivo: *</FormLabel>
                                <div style={{ display: 'flex' }}>
                                    <FormFile
                                        onChange={handleInputFileChange}
                                        lassName="position-relative"
                                        name="file"
                                        feedbackTooltip
                                        id="validationFormik107"
                                    />
                                    {loadFile && <img width="30" height="30" style={{ marginLeft: '5%' }} src={icoLoading} />}
                                </div>
                            </FormGrup>
                        </ContainContent> */}
                        <ContainContent>
                            <FormGrup className="col-md-12 col-12" >
                                <FormLabel >Objeto do contrato:</FormLabel>
                                <FormControl
                                    placeholder="Digite o objeto do contrato"
                                    value={description}
                                    onChange={handleInputDescriptionChange}
                                    className="col-md-12 col-12"
                                    as="textarea"
                                    rows="5"
                                />
                            </FormGrup>
                        </ContainContent>
                        <ContainContent>
                            <ButtonEnviar
                                id="SubForm"
                                name="btForm"
                                type="button"
                                onClick={handleSubmit}
                            >Enviar</ButtonEnviar>
                        </ContainContent>
                    </ColContent>
                </ContainContent>
            </ContainNav>
        </Modal>
    );
}

export default RegisterContract;
