import Styled from 'styled-components';
import { Container, Button } from 'react-bootstrap';

export const ContBody = Styled(Container)`
margin:0;
height:100vh;
display:flex;
flex-direction:column;
justify-content:center;
align-items:center;
`;
export const TextInfo = Styled.h2`
color:white;
`;

export const BtnGoHome = Styled(Button)`
transtion:0.3s;
width:10%;
margin:0px 15px;
background-color:#00CC5C;
color:white;
font-weight:bold;
border:none;
    &:hover{
        transtion:0.3s;
    background-color:#009945;
    color:white;
    }
    &:focus{
        transtion:0.3s;
        background-color:#909090!important;
        color:white;
        border:none;
        box-shadow:none;
    }
	
`;