import React, { useState } from 'react';
import { useDispatch } from 'react-redux';
import { signInRequest } from '../../redux/modules/auth/actions'

import { RowMain } from '../../styles/bootstrap';
import { WindowsC1, WindowsC2, InputTxt, IcoUser, BtnLogin, Div, Label } from './styles';
import userImg from '../../assets/images/do-utilizador.png';

function Login() {
  const dispatch = useDispatch();

  const [user, setUser] = useState("");
  const [password, setPassword] = useState("");

  const handleUserChange = data => {
    setUser(data.target.value);
  }

  const handlePasswordChange = data => {
    setPassword(data.target.value);
  }

  const handleSubmit = () => {
    dispatch(signInRequest(user, password));
  }

  return (
    <>
      <RowMain>
        <WindowsC1>
          <WindowsC2>
            <IcoUser src={userImg} />
            <Div>
              <Label for="login">LOGIN:</Label>
              <InputTxt 
                name="login" 
                type="text" 
                value={user}
                onChange={handleUserChange}
              />
              <Label for="senha">SENHA:</Label>
              <InputTxt 
                name="senha" 
                type="password" 
                value={password} 
                onChange={handlePasswordChange}
              />
            </Div>
            <BtnLogin onClick={handleSubmit}>Entrar</BtnLogin>
          </WindowsC2>
        </WindowsC1>
      </RowMain>
    </>
  );
}

export default Login;
