import { Container, Row, Col, Form, Button } from 'react-bootstrap';
import Styled from 'styled-components';
import PerfectScrollbar from 'react-perfect-scrollbar';

export const DivScroll = Styled(PerfectScrollbar)`
    max-height:350px;
    padding:20px;
`;


export const ContainContent = Styled(Row)`
    margin:0;
    justify-content:center;
    align-items:center;
    display:flex;
    width:100%;
    border-radius:10px;
`;

export const ColContentMain = Styled(Row)`
    margin:0;
    justify-content:center;
    align-items:center;
    display:flex;
    width:100%;
    border-radius:10px;
`;

export const ContainContentBody = Styled(Col)`
    margin-top:20px;
    -webkit-box-shadow: -1px -4px 35px 7px rgba(0,0,0,0.16);
    -moz-box-shadow: -1px -4px 35px 7px rgba(0,0,0,0.16);
    box-shadow: -1px -4px 35px 7px rgba(0,0,0,0.16);
    padding:10px;
    margin-left:30px;
    background-color:white;
    border-radius:10px;

    @media (max-width: 620px) { 
        margin-left:0px;
    }

`;


export const ListTitle = Styled.h2`
width:100%;
text-align:center;
font-size:24px;
padding:10px;
margin-bottom:20px;
border-bottom:solid #45E68D 3px;
`;
export const NameArchive = Styled.h2`
    font-size:14px;
    max-width: 15ch;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
`;

export const ListTopBar = Styled.div`
    font-size:18px;
    width:100%;
    background-color:#2F4F4F;
    padding:20px;
    color:white;
    border-radius:10px;
`;




