export default function filterForm(documentList, fileName, contractor, hired,  description)
{
    var array = [];

    //Condicionais com a propriedade FIleName
    if(fileName && !contractor && !hired && !description)
    {
        array = documentList.filter(
            index => index.name.toLowerCase().indexOf(fileName.toLowerCase()) > -1
        );
    }
    else if(fileName && contractor && !hired && !description)
    {
        array = documentList.filter(
            index => 
            (index.name.toLowerCase().indexOf(fileName.toLowerCase()) > -1)
            &&
            (index.contractor.toLowerCase().indexOf(contractor.toLowerCase()) > -1)
        );
    }
    else if(fileName && !contractor && hired && !description)
    {
        array = documentList.filter(
            (index => index.name.toLowerCase().indexOf(fileName.toLowerCase()) > -1)
            &&
            (index => index.hired.toLowerCase().indexOf(hired.toLowerCase()) > -1)
        );
    }
    else if(fileName && contractor && hired && !description)
    {
        array = documentList.filter(
            index => 
            (index.name.toLowerCase().indexOf(fileName.toLowerCase()) > -1)
            &&
            (index.contractor.toLowerCase().indexOf(contractor.toLowerCase()) > -1)
            &&
            (index.hired.toLowerCase().indexOf(hired.toLowerCase()) > -1)
        );
    }
    else if(fileName && contractor && hired && description)
    {
        array = documentList.filter(
            index => 
            (index.name.toLowerCase().indexOf(fileName.toLowerCase()) > -1)
            &&
            (index.contractor.toLowerCase().indexOf(contractor.toLowerCase()) > -1)
            &&
            (index.hired.toLowerCase().indexOf(hired.toLowerCase()) > -1)
            &&
            (index.description.toLowerCase().indexOf(description.toLowerCase()) > -1)
        );
    }
    //
    else if(!fileName && contractor && !hired && !description)
    {
        array = documentList.filter(
            index => index.contractor.toLowerCase().indexOf(contractor.toLowerCase()) > -1
        );
    }
    else if(!fileName && !contractor && hired && !description)
    {
        array = documentList.filter(
            index => index.hired.toLowerCase().indexOf(hired.toLowerCase()) > -1
        );
    }
    else if(!fileName && contractor && hired && !description)
    {
        array = documentList.filter(
            index => index.hired.toLowerCase().indexOf(hired.toLowerCase()) > -1
            &&
            index.contractor.toLowerCase().indexOf(contractor.toLowerCase()) > -1
        );
    }
    else if(!fileName && !contractor && !hired && description)
    {
        array = documentList.filter(
            index => index.description.toLowerCase().indexOf(description.toLowerCase()) > -1
        );
    } else {
        array = documentList;
    }


    return array;
}