import { isWithinInterval, parseISO, addDays } from "date-fns";

export default function filterDate(documents, dateInitial, dateFinal) {
  documents = documents.filter((index) =>
    isWithinInterval(new Date(index.date), {
      start: parseISO(dateInitial),
      end: addDays(parseISO(dateFinal), 1),
    })
  );

  return documents;
}
