import { Row, Col, Button, Dropdown, Form } from "react-bootstrap";
import Styled from "styled-components";
import Scroll from "react-perfect-scrollbar";

export const ScrollLimiter = Styled(Scroll)`
max-height:300px;
`;
export const FormGrup = Styled(Form.Group)`

`;

export const FormControl = Styled(Form.Control)`
margin-top:10px;
margin-bottom:20px;
`;

export const FormLabel = Styled(Form.Label)`
    padding:0;
    margin:0;
    font-size:14px;
`;

export const BtnAdd = Styled(Button)`
transition:0.3s;
width:50%;
margin:0px 15px;
background-color:#00CC5C;
color:white;
font-weight:bold;
border:none;
    &:hover{
        transition:0.3s;
    background-color:#009945;
    color:white;
    }
    &:focus{
        transition:0.3s;
        background-color:#909090!important;
        color:white;
        border:none;
        box-shadow:none;
    }
`;
export const BtnRemove = Styled(Button)`

transition:0.3s;
margin:10px 15px;
background-color:#990000;
color:white;
font-weight:bold;
border:none;
    &:hover{
        transition:0.3s;
    background-color:#770000;
    color:white;
    }
    &:focus{
        transition:0.3s;
        background-color:#550000!important;
        color:white;
        border:none;
        box-shadow:none;
    }
`;

export const RowModal = Styled(Row)`
margin:0;
width:100%;
justify-content:center;
`;

export const ColModal = Styled(Col)`
background-color:white;
width:50%;
min-height:30vh;
padding:20px;
`;

export const SeachContractInput = Styled(Dropdown)``;
export const SeachContractToogle = Styled(Dropdown.Toggle)`
background-color:white!important;
color:black!important;
height:40px;
text-align:start;
display:flex;
justify-content:space-between;
align-items:center;
    &:active{
        background-color:white!important;
        color:black!important;
    }
    &:hover{
    transtion:0.3s;
    background-color:white!important;
    color:black!important;
    }
    &:focus{
        transtion:0.3s;
        background-color:white!important;
    }
`;
export const SeachContractMenu = Styled(Dropdown.Menu)``;
export const SeachContractItem = Styled(Dropdown.Item)``;
