import React, { useState, useEffect } from "react";
import Modal from "react-modal";
import { toast } from "react-toastify";
import api from "../../../../util/api";

import {
  ContainNav,
  ContainContent,
  ColContent,
  ButtonEnviar,
  FormGrup,
  FormLabel,
  FormCheck,
  FormControl,
  FormFile,
  ClosedModalButton,
  DivScrollSubject,
  ButtonGrupReg,
  ButtonGrupClose,
  SeachContractInput,
  SeachContractToogle,
  SeachContractMenu,
  SeachContractItem,
  TagName,
  ExcludeBtn,
} from "./styles";

import ModalSelectThemes from "../selectthemes";

import icoLoading from "../../../../assets/images/icoLoading.gif";

function Correspondencia({
  openModalCorrespondence,
  setOpenModalCorrespondence,
  contractId,
  reloadThemes,
  setReloadList,
  reloadList
}) {
  const [openModalSelectThemes, setOpenModalSelectThemes] = useState(false);

  const [themes, setThemes] = useState([]);
  const [themesSelecteds, setThemesSelecteds] = useState([]);

  const [sender, setSender] = useState("");
  const [recep, setRecep] = useState("");
  const [subject, setSubject] = useState("");
  const [code, setCode] = useState("");

  const [sendDate, setSendDate] = useState("");
  const [type, setType] = useState("email");

  const [file, setFile] = useState(0);
  const [files, setFiles] = useState([]);
  const [loadFile, setLoadFile] = useState(false);

  const [description, setDescription] = useState("");
  const [impact, setImpact] = useState(false);

  const [textFilterRelation, setFilterTextRelation] = useState('');
  const [relationSelected, setRelationSelected] = useState({
    name: '',
    id: 0
  });
  const [correspondenceList, setCorrespondenceList] = useState([]);
  const [conrrespondenceRender, setCorrespondenceRender] = useState([]);
  const [reloadCorrespondence, setReloadCorrespondence] = useState(0);

  const [many, setMany] = useState(false);

  useEffect(() => {
    async function loadThemes() {
      const { data } = await api.get(`themes/list/${contractId}`);

      setThemes(data);
    }

    loadThemes();
  }, [reloadThemes]);

  useEffect(() => {
    async function loadCorrespondences()
    {
      const { data } = await api.get(`correspondences/list/${contractId}`);

      const formattedData = data.map(index => {
        const { id, subject, code } = index;

        const obj = {
            id,
            name: subject,
            code
        };

        return obj;
      });

      setCorrespondenceList(formattedData);
      setCorrespondenceRender(formattedData);
    };

    loadCorrespondences();
  }, [reloadCorrespondence])



  const handleInputSenderChange = (data) => {
    setSender(data.target.value);
  };

  const handleInputRecepChange = (data) => {
    setRecep(data.target.value);
  };

  const handleInputSubjectChange = (data) => {
    setSubject(data.target.value);
  };

  const handleInputImpactChange = (data) => {
    setImpact(!impact);
  };

  const handleInputCodeChange = (data) => {
    setCode(data.target.value);
  };

  const handleInputSendDateChange = (data) => {
    setSendDate(data.target.value);
  };

  const handleInputFilterRelationChange = (data) => {
    const { target: { value } } = data;

    setFilterTextRelation(value); 

    console.log(correspondenceList);

    const correspondenceFilter = correspondenceList.filter(
      index => index.name.toLowerCase().indexOf(value.toLowerCase()) > -1
    );

    setCorrespondenceRender(correspondenceFilter);
  };

  const handleInputFileChange = async (data) => {
    try {
      const { files } = data.target;
      const form = new FormData();
      form.append("file", files[0]);

      setLoadFile(true);
      const answear = await api.post("files/upload", form);

      const { id } = answear.data;

      setFile(id);
      setLoadFile(false);
    } catch (err) {
      toast.error(
        "Não foi possível carregar o PDF, por favor contate um administrador"
      );
    }
  };

  const handleInputFilesChange = async (data) => {
    const { files } = data.target;
    const filesId = [];

    try {
      setLoadFile(true);

      for (var i = 0; i < files.length; i++) {
        const form = new FormData();
        form.append("file", files[i]);

        const {
          data: { id },
        } = await api.post("files/upload", form);

        filesId.push(id);
      }

      setLoadFile(false);
    } catch (err) {
      toast.error(`Erro ao carregar, contate um administrador`);
    }

    setFiles(filesId);
  };

  const handleInputTypeChange = (data) => {
    setType(data.target.value);

    console.log(data.target.value);
  };

  const handleInputDescriptionChange = (data) => {
    setDescription(data.target.value);
  };

  const clearAll = () => {
    setSender("");
    setRecep("");
    setSubject("");
    setCode("");
    setSendDate("");
    setFile(0);
    setFiles([]);
    setThemesSelecteds([]);
    setDescription("");
    setRelationSelected({
      name: null,
      id: null,
    })
    if (!many) {
      document.getElementById("validationFormik107").value = "";
    } else {
      document.getElementById("validationFormik108").value = "";
    }
  };

  const handleButtonDeleteTheme = (id) => {
    var themesSelectedsCopy = themesSelecteds;
    const index = themesSelecteds.findIndex((index) => index.id === id);
    themesSelectedsCopy.splice(index, 1);

    setThemesSelecteds([]);

    setTimeout(() => {
      setThemesSelecteds(themesSelectedsCopy);
    }, 10);
  };

  const handleButtonSelectRelation = correspondence => {
    setRelationSelected(correspondence);
  };

  const handleSubmit = async () => {
    if (!many) {
      try {
    
        if (loadFile) {
          return toast.error("Aguarde até que o PDF seja enviado");
        }

        if (file === 0) {
          return toast.error("Você precisa carregar um arquivo");
        }

        const obj = {
          date: sendDate,
          subject,
          sender,
          impact,
          recipient: recep,
          type,
          file_id: file,
          contract_id: contractId,
        };

        if(themesSelecteds[0]) {
            const FormattedThemesSelecteds = themesSelecteds.map((index) => {
              const { value } = index;
  
              return value;
            });
  
            obj.themes = FormattedThemesSelecteds;
        }

        if(code) {
          obj.code = code;
        };

        if(relationSelected.name)
        {
          obj.correspondence_id = relationSelected.id;
        };

        const { data } = await api.post("correspondences/register", obj);

        const { Error } = data;
        if (Error) {
          const { Message } = data;
          return toast.error(Message);
        }

        

        clearAll();

        setReloadCorrespondence(reloadCorrespondence + 1);
        setReloadList(reloadList + 1);

        toast.success("Documento enviado!");

      } catch (err) {

        if (sendDate.length < 10) {
          return toast.error("Preencha a data corretamente");
        }

        if (loadFile) {
          return toast.error("Aguarde até que os PDF's sejam enviados");
        }

        if (!files[0]) {
          return toast.error("Você precisa carregar ao menos um arquivo");
        }
        
        toast.error(
           "Erro ao efetuar o cadastro, entre em contato com um admnistrador"
        );
      }
    } else {
      try {
        
        if (sendDate.length < 10) {
          return toast.error("Preencha a data corretamente");
        }

        if (loadFile) {
          return toast.error("Aguarde até que os PDF's sejam enviados");
        }

        if (!files[0]) {
          return toast.error("Você precisa carregar ao menos um arquivo");
        }

        await api.post("correspondences/register/many", {
          date: sendDate,
          type,
          files_id: files,
          contract_id: contractId,
        });

        clearAll();
        setReloadList(reloadList + 1);

        toast.success("Documentos enviados!");
      } catch (err) {
        toast.error("Falha ao registar, contate um administrador");
      }
    }
  };

  return (
    <Modal
      style={{
        overlay: {
          backgroundColor: "rgba(25,25,25, .8)",
        },
        content: {
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
          padding: "0",
          top: "0",
          bottom: "0",
          left: "0",
          right: "0",
          border: "none",
          backgroundColor: "rgba(25,25,25, .0)",
          margin: "0",
          borderRadius: "10px",
          maxHeight: "100vh",
          width: "100vw",
        },
      }}
      isOpen={openModalCorrespondence}
      onRequestClose={() => {
        if (loadFile) return;
        setOpenModalCorrespondence(false);
      }}
    >
      <ContainNav>
        <ModalSelectThemes
          openModalSelectThemes={openModalSelectThemes}
          setOpenModalSelectThemes={setOpenModalSelectThemes}
          contractId={contractId}
          themesSelecteds={themesSelecteds}
          setThemesSelecteds={setThemesSelecteds}
        />
        <ColContent className="col-md-8 col-sm-7 col--12">
          <ContainContent>
          <FormGrup className="col-md-8 col-12">
              <FormLabel>Assuntos destaque:</FormLabel>
              <DivScrollSubject>
                <ContainContent className="align-items:center">
                  {themesSelecteds.map((index) => (
                      <FormGrup className="d-flex col-md-6 col-6">
                        <TagName className="col-md-9 col-9" id={index.id}>
                          ! {index.value}
                        </TagName>
                        <ExcludeBtn
                          className="col-md-3 col-3"
                          onClick={() => handleButtonDeleteTheme(index.id)}
                        >
                          X
                        </ExcludeBtn>
                    </FormGrup>
                  ))}
                </ContainContent>
              </DivScrollSubject>
          </FormGrup>
       
            <FormGrup className="col-md-3 col-4">
              <ButtonGrupReg onClick={() => setOpenModalSelectThemes(true)}>
                Adicionar assunto
              </ButtonGrupReg>
            </FormGrup>
            <FormGrup className="col-md-1 col-2 d-flex justify-content-center align-items-center">
              <ClosedModalButton
                onClick={() => {
                  if (loadFile) return;
                  setOpenModalCorrespondence(false);
                }}
              >
                X
              </ClosedModalButton>
            </FormGrup>
          </ContainContent>
          <ContainContent>
            <FormGrup className="col-md-4 col-12">
              <FormLabel>Remetente:</FormLabel>
              <FormControl
                onChange={handleInputSenderChange}
                value={sender}
                disabled={many ? true : false}
                required
                placeholder="Digite o remetente"
                type="text"
              />
            </FormGrup>
            <FormGrup className="col-md-4 col-12">
              <FormLabel>Destinatario:</FormLabel>
              <FormControl
                onChange={handleInputRecepChange}
                value={recep}
                disabled={many ? true : false}
                required
                placeholder="Digite o destinatario"
                type="text"
              />
            </FormGrup>
            <FormGrup className="col-md-4 col-12">
              <FormLabel>Código:</FormLabel>
              <FormControl
                onChange={handleInputCodeChange}
                value={code}
                disabled={many ? true : false}
                required
                placeholder="Digite o codigo"
                type="text"
              />
            </FormGrup>
            <FormGrup className="col-md-8 col-12">
              <FormLabel>Assunto:</FormLabel>
              <FormControl
                onChange={handleInputSubjectChange}
                value={subject}
                disabled={many ? true : false}
                required
                placeholder="Digite o assunto"
                type="text"
              />
            </FormGrup>
            <FormGrup className="col-md-4 col-12">
              <FormLabel>Data de Envio:</FormLabel>
              <FormControl
                onChange={handleInputSendDateChange}
                value={sendDate}
                required
                type="date"
              ></FormControl>
            </FormGrup>
            <FormGrup className="col-md-6 col-12">
              <FormLabel>Tipo:</FormLabel>
              <FormControl
                onChange={handleInputTypeChange}
                value={type}
                className="col-md-12 col-12"
                as="select"
              >
                <option value="email">E-mail</option>
                <option value="enviada">Correspondência enviada</option>
                <option value="recebida">Correspondência recebida</option>
              </FormControl>
            </FormGrup>
            <FormGrup className="col-md-6 col-12">
              <FormLabel>Selecione o arquivo:</FormLabel>
              {!many ? (
                <FormFile
                  onChange={handleInputFileChange}
                  className="position-relative"
                  required
                  name="file"
                  id="validationFormik107"
                  feedbackTooltip
                />
              ) : (
                <FormFile
                  onChange={handleInputFilesChange}
                  className="position-relative"
                  required
                  name="file"
                  id="validationFormik108"
                  feedbackTooltip
                  multiple
                />
              )}
              {loadFile && (
                <img
                  width="30"
                  height="30"
                  style={{ marginLeft: "40%", marginTop: "5%" }}
                  src={icoLoading}
                />
              )}
            </FormGrup>
          </ContainContent>
          <FormGrup className="col-md-11 col-12">
              <FormLabel className="col-md-11 col-12">
                Relação:
              </FormLabel>
              <SeachContractInput>
                <SeachContractToogle
                  className="col-md-11 col-12"
                  id="dropdown-custom-components"
                  disabled={many ? true : false}
                >
                  {relationSelected.name}
                </SeachContractToogle>
                <SeachContractMenu>
                  <FormControl
                    autoFocus
                    className="mx-3 my-2 w-auto"
                    placeholder="Consultar..."
                    value={textFilterRelation}
                    onChange={handleInputFilterRelationChange}
                  />
                   
                    {conrrespondenceRender.map(index => (    
                        <SeachContractItem className={index.name}
                           onClick={() => handleButtonSelectRelation(index)}
                        >
                          {index.name}{index.code && `, código: ${index.code}`}
                        </SeachContractItem>
                    ))}
                  </SeachContractMenu>
                </SeachContractInput>
          </FormGrup>
          <FormGrup className="col-md-4 col-12">
            <FormCheck
              disabled={!many ? false : true}
              type="switch"
              id="custom-switch"
              label="Ocorrência importante"
              onChange={handleInputImpactChange}
            />
          </FormGrup>
          <ContainContent>
            <FormGrup className="col-md-12 col-12">
              <FormLabel>Descrição:</FormLabel>
              <FormControl
                onChange={handleInputDescriptionChange}
                disabled={many ? true : false}
                value={description}
                className="col-md-12 col-12"
                as="textarea"
                rows="5"
              />
            </FormGrup>
          </ContainContent>
          <ContainContent>
            <ButtonEnviar
              onClick={handleSubmit}
              id="SubForm"
              name="btForm"
              type="button"
            >
              Enviar
            </ButtonEnviar>
          </ContainContent>
          <FormCheck
            type="switch"
            id="custom-switch-massive"
            label="Em massa"
            onChange={() => setMany(!many)}
          />
        </ColContent>
      </ContainNav>
    </Modal>
  );
}

export default Correspondencia;
