import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { RowMain } from '../../styles/bootstrap';
import { ContBody, TextInfo, BtnGoHome } from './styles'
function PageNotFound() {
  return (
    <>
      <RowMain style={{ backgroundColor: '#374F4F', minHeight: '100vh' }}>
        <ContBody>
          <TextInfo style={{ fontSize: '100px' }}>:(</TextInfo>
          <TextInfo style={{ fontSize: '70px' }}> Page is not found 404</TextInfo>
          <BtnGoHome onClick={function () {
            var path = "/"
            window.location = path;
          }}
          >Go Home</BtnGoHome>
        </ContBody>
      </RowMain>
    </>
  );
}

export default PageNotFound;
