import immer from 'immer';

const initialState = {
    id: 0,
    selected: false
};

export default function contractselected(state = initialState, action)
{
    switch(action.type)
    {
        case '@contractselected/SET_SELECTED':
            return immer(state, draft => {
                draft.id = action.payload.id;
                draft.selected = true;
            });

        default:
            return state;
    }
};