import React from 'react';
import { Switch,Redirect, Route } from 'react-router-dom';

import { UserSession } from './route';

import login from '../pages/login';
import searchcontract from '../pages/searchcontract';
import edituser from '../pages/edituser';
import viewcontract from '../pages/viewcontract';
import editprop from '../pages/viewanexos/editproposal/';
import anexo from '../pages/viewanexos/anexo/index.js';
import correspondence from '../pages/viewanexos/editcorrepondence/';
import ata from '../pages/viewanexos/ediata/';
import allprocess from '../components/allprocess';
import notfound from '../pages/notfound';
import timeline from '../pages/timeline';
import userconfig from '../pages/user';
import minerdoc from '../pages/minerar';
import testes from '../pages/debug';
import newuser from '../pages/newuser';
export default function()
{
    return(
        <Switch>
            <UserSession path="/" exact component={login} />
            <UserSession path="/home" exact component={searchcontract} isPrivate={true}/>
            <UserSession path="/view-contract/:id" exact component={viewcontract} isPrivate={true}/>
            <UserSession path="/edit-anexo/:id" exact component={anexo} isPrivate={true}/>
            <UserSession path="/edit-prop/:id" exact component={editprop} isPrivate={true}/>
            <UserSession path="/edit-correspondence/:contract_id/:document_id" exact component={correspondence} isPrivate={true}/>
            <UserSession path="/edit-ata/:contract_id/:id/" exact component={ata} isPrivate={true}/>
            <UserSession path="/timeline" exact component={timeline} isPrivate={true}/>
            <UserSession path="/miner-doc" exact component={minerdoc} isPrivate={true}/>
            <UserSession path="/process" exact component={allprocess} isPrivate={true}/>
            <UserSession path="/userconfig" exact component={userconfig} isPrivate={true}/>
            <UserSession path="/404" exact component={notfound} isPrivate={true}/>
            <UserSession path="/testes" exact component={testes} isPrivate={true}/>
           
            <Route path="/new-user/1PXD1KFMNZNPx-01MX" exact component={newuser}/>
            <Route path="/edit-user/1PXD1KFMNZNPx-01MX" exact component={edituser}/>

            <Redirect to="/404"/> 
        </Switch>
    );
}