import produce from 'immer';

export default function user(state = { }, action)
{
    switch(action.type)
    {
        case '@auth/SIGN_IN_SUCESS':
            return produce(state, draft => {
                draft.user = action.payload.user;
            });

        case '@auth/SIGN_OUT':
            return produce(state, draft => {
                draft.user = null;
            });

        default: 
            return state;
    }
};