import React, { useState, useEffect, useMemo } from 'react';
import history from '../../util/history'
import { parseISO, format } from 'date-fns';
import { useSelector } from 'react-redux';
import socketio from 'socket.io-client';
import { toast } from 'react-toastify';

import api from '../../util/api';
import host from '../../config/host';

import filterForm from './functions/filterForm';

import ModalReg from './components/registercontract';

import {
    TextContent,
    ConContent,
    ColContent,
    RowContent,
    TitleProgress,
    ContainContent,
    FormGrup,
    FormLabel,
    ColContent2,
    FormControl,
    SendButton,
    ColContentFilter,
    DivScroll,
    ColContentInter,
    RecordButton,
    ContractButton,
    ClosedModalButton,
} from './styles';


function SearchContract() {
    const userId = useSelector(state => state.user.user.id);

    const [reload, setReload] = useState(0);
    const [documentList, setDocumentList] = useState([]);
    const [documentListRender, setDocumentListRender] = useState([]);

    const [fileName, setFileName] = useState("");
    const [contractor, setContractor] = useState("");
    const [hired, setHired] = useState("");
    const [description, setDescription] = useState("");


    const [openModalRegisterContract, setModalRegisterContract] = useState(false);

    //35.222.119.75
    const socket = useMemo(() => socketio(host.ip, {
        query: {
            user: userId
        }
    }), [userId]);
 
    useEffect(() => {
        socket.on('finish-process', data => {
            const { contract_name, name } = data;

            toast.info(`O OCR do arquivo "${name}" do contrato "${contract_name}" terminou de ser processado`)
        });
    }, [socket]);

    useEffect(() => {
        async function loadContracts() {
            const { data } = await api.get(`contract/list`);

            const formattedData = data.map(index => {
                const obj = {
                    ...index,
                    formattedDate: format(new Date(index.date), 'dd-MM-yyyy')
                };

                return obj;
            });

            setDocumentList(formattedData);
            setDocumentListRender(formattedData);
        }

        loadContracts();
    }, [reload]);


    const handleInputFileNameChange = data => {
        setFileName(data.target.value);
    };

    const handleInputContractorChange = data => {
        setContractor(data.target.value);
    };

    const handleInputHiredChange = data => {
        setHired(data.target.value);
    };

    const handleInputDescriptionChange = data => {
        setDescription(data.target.value);
    };

    const handleFilter = () => {
        var stringFP = fileName[0];        
        var _name = fileName;

        if(stringFP === " ")
        {
            _name = null;
        };

        stringFP = contractor[0]
        var _contractor = contractor;

        if(stringFP === " ")
        {
            _contractor = null;
        };

        stringFP = hired[0]
        var _hired = hired;

        if(stringFP === " ")
        {
            _hired = null;
        };

        stringFP = description[0]
        var _description = description;

        if(stringFP === " ")
        {
            _description = null;
        };

        var array = filterForm(
            documentList, 
            _name,
            _contractor,
            _hired,
            _description
        );

        setDocumentListRender(array);
    };

    const handleClearFilter = () => {
        setDocumentListRender(documentList);
    };

    return (
        <>
            <ModalReg 
                openModalRegisterContract={openModalRegisterContract} 
                setModalRegisterContract={setModalRegisterContract}
                reload={reload}
                setReload={setReload}
             />

            <ConContent>
                <RowContent>
                    <ColContent2 className="col-md-12 col-sm-12 col-12">
                        <TitleProgress>Filtros de pesquisa:</TitleProgress>
                        <ContainContent>
                            <ContainContent>
                                <FormGrup className="col-md-4 col-12"  >
                                    <FormLabel className="col-md-12 col-12">Nome do contrato:</FormLabel>
                                    <FormControl
                                        className="col-md-12 col-12"
                                        placeholder="Pesquise por nome do arquivo"
                                        type="text"
                                        onChange={handleInputFileNameChange}
                                        value={fileName}
                                    />
                                </FormGrup>
                                <FormGrup className="col-md-4 col-12" >
                                    <FormLabel className="col-lg-12 col-md-12">Nome da contratante</FormLabel>
                                    <FormControl
                                        required placeholder="Pesquise por nome do contratante"
                                        className="col-lg-12 col-md-12 col-12"
                                        type="text"
                                        onChange={handleInputContractorChange}
                                        value={contractor}
                                    />
                                </FormGrup>
                                <FormGrup className="col-md-4 col-12" >
                                    <FormLabel className="col-lg-12 col-md-12" >Nome do contratado</FormLabel>
                                    <FormControl
                                        required
                                        placeholder="Pesquise por nome do contratado"
                                        className="col-lg-12 col-md-12 col-12"
                                        type="text"
                                        onChange={handleInputHiredChange}
                                        value={hired}
                                    />
                                </FormGrup>
                                <FormGrup className="col-md-12 col-12" >
                                    <FormLabel className="col-lg-12 col-md-12">Objeto do contrato:</FormLabel>
                                    <FormControl
                                        as="textarea"
                                        rows="3"
                                        placeholder="Pesquise por conteudo do documento"
                                        className="col-lg-12 col-md-12 col-12"
                                        onChange={handleInputDescriptionChange}
                                        value={description}
                                    />
                                </FormGrup>
                            </ContainContent>
                            <ContainContent>
                                <SendButton
                                    className="col-md-6" id="SubForm"
                                    name="btForm" type="button"
                                    onClick={handleFilter}
                                >
                                    Filtrar
                                </SendButton>
                                
                                <RecordButton
                                    onClick={() => setModalRegisterContract(true)}
                                    className="col-md-3"
                                    id="SubForm" name="btForm"
                                    type="button"
                                >Cadastrar</RecordButton>
                            </ContainContent>
                        </ContainContent>
                    </ColContent2>
                    <ColContentFilter className="col-lg-12 col-md-12 col-sm-12 col-12">
                        <ColContent className="col-md-12">
                            <DivScroll className="col-lg-12 col-md-12 col-sm-12 col-12">
                                <ColContentInter className="col-md-12">
                                    <ColContent className="col-lg-3 col-md-3 col-sm-8 col-8 ">
                                        Contrato:
                                    </ColContent>
                                    <ColContent className="col-lg-2 col-md-2 col-sm-8 col-8">
                                        Data:
                                    </ColContent>
                                    <ColContent className="col-lg-2 col-md-2 col-sm-8 col-8">
                                        Contratante:
                                    </ColContent>
                                    <ColContent className="col-lg-2 col-md-2 col-sm-8 col-8">
                                        Contratada:
                                    </ColContent>
                                    <ColContent className="col-lg-3 col-md-3 col-sm-8 col-12">
                                        Objeto do contrato:
                                    </ColContent>
                                </ColContentInter>
                                {documentListRender.map(document => (
                                    <ColContentInter className="col-md-12">
                                        <ColContent className="col-lg-3 col-md-3 col-sm-8 col-8 ">
                                            <ContractButton href="#" onClick={() => history.push(`/view-contract/${document.id}`)}>{document.name}</ContractButton>
                                        </ColContent>
                                        <ColContent className="col-lg-2 col-md-2 col-sm-8 col-8">
                                            <TextContent> {document.formattedDate}</TextContent>
                                        </ColContent>
                                        <ColContent className="col-lg-2 col-md-2 col-sm-8 col-8">
                                            <TextContent> {document.contractor}</TextContent>
                                        </ColContent>
                                        <ColContent className="col-lg-2 col-md-2 col-sm-8 col-8">
                                            <TextContent>{document.hired}</TextContent>
                                        </ColContent>
                                        <ColContent className="col-lg-3 col-md-3 col-sm-8 col-12 text-center" >
                                            <TextContent>{document.description}</TextContent>
                                        </ColContent>
                                    </ColContentInter>
                                ))}
                            </DivScroll>
                        </ColContent>
                    </ColContentFilter>
                </RowContent>
            </ConContent>
        </>
    );
}

export default SearchContract;
