import React, { useState, useEffect, useMemo } from 'react';
import { useParams, useHistory } from 'react-router-dom';

// import socketio from 'socket.io-client';
import { format, parseISO } from 'date-fns';
// import { toast } from 'react-toastify';
import api from '../../../util/api';
import icoLoading from '../../../assets/images/icoLoading.gif';

import host from '../../../config/host';


import {
    NameArchive,
    ContainContent,
    ColContent,
    TextContent,
    FormGrup,
    FormLabel,
    FormControl,
    FormFile,
    Div,
    InputButtonEdit,
    ButtonExclude,

} from './styles';

import icoEdit from '../../../assets/images/img_388620.png';
import icoCheck from '../../../assets/images/checkEdit.png';
import { toast } from 'react-toastify';

export default function EditAnexo() {
    const history = useHistory();

    const { id } = useParams();

    const [sendDate, setSendDate] = useState('');

    const [reload, setReload] = useState(0);

    const [newFileId, setNewFileId] = useState(0);
    const [file, setFile] = useState({ 
        name: '',
        path: ''
     });
    const [fileSelect, setFileSelect] = useState(false);
    const [loadFile, setLoadFile] = useState(false);

    const [typeDefault, setTypeDefault] = useState('');
    const [type, setType] = useState('');
    const [typeSelect, setTypeSelect] = useState(false);

    const [onClickDelete, setOnClickDelete] = useState(false);

    useEffect(() => {
        async function loadFileDefault()
        {
            const { data } = await api.get(`filesdefault/get/${id}`);
            var { type, createdAt, File } = data;

            createdAt = format(parseISO(createdAt), 'dd/MM/yyyy');

            setSendDate(createdAt);

            setType(type);
            setTypeDefault(type);
            setFile({
                name: File.name,
                path: File.path
            });
        };

        loadFileDefault();
    } ,[reload]);

    const handleInputFileChange = async data => {
        const form = new FormData();
        form.append('file', data.target.files[0]);

        try{
            setLoadFile(true);

            const { data: { id } } = await api.post('files/upload', form);

            setNewFileId(id);
            setLoadFile(false);
        }catch(err)
        {
            toast.error('Erro ao carregar o arquivo, contate um administrador');
        }
    };

    const handleButtonFileChange = async () => {
        if (!fileSelect) {
            setFileSelect(true);
        } else {
            if(loadFile)
            {
                return;
            };

            if(newFileId !== 0)
            {
                await api.put(`filesdefault/update/${id}`, {
                    column: 'file_id',
                    content: newFileId
                });

                setReload(reload + 1);
                setNewFileId(0);
            };

            setFileSelect(false);
        };
    };


    const handleInputTypeChange = data => {
        const { target: { value }} = data;
        // setType(data.target.value);
        if(value === " ")
        {
            return;
        };

        setType(value);
    };


    const handleButtonTypeChange = async () => {
        if (!typeSelect) {
            setTypeSelect(true);
        } else {
            setTypeSelect(false);

            if(typeDefault === type)
            {
                return;
            };

            await api.put(`filesdefault/update/${id}`, {
                column: 'type',
                content: type
            });
        };
    };

    const handleButtonDelete = async () => {
        if(!onClickDelete)
        {
          return setOnClickDelete(true);
        };
    
        await api.delete(`filesdefault/delete/${id}`);
    
        history.push('/');
    };

    return (
        <>
            <ContainContent style={{ paddingBottom: '50px' }}>
                <ColContent className="col-md-10 col-sm-10 col-12" >
                    <NameArchive>Edição de Anexos</NameArchive>
                    <ContainContent>                           
                        <FormGrup className="col-md-4 col-12" >
                            <FormLabel>Data de upload:</FormLabel>
                            <Div className="d-flex col-md-12 m-0">
                                <FormControl
                                    placeholder={sendDate}
                                    icoLoading id="send_date"
                                    className="col-md-10 col-sm-10 col-10"
                                    disabled
                                    type="input"
                                />
                            </Div>
                        </FormGrup>
                        <FormGrup className="col-md-6 col-12" >
                            <FormLabel>Tipo:</FormLabel>
                            <Div className="d-flex col-md-12 m-0">
                                {
                                    !typeSelect ?
                                        <input 
                                            className="col-md-10 col-10"
                                            disabled
                                            placeholder={type}
                                        />
                                    : 
                                    <FormControl onChange={handleInputTypeChange} disabled={typeSelect ? false : true} className="col-md-10 col-10" as="select">
                                        <option value=" "> </option>
                                        <option value="contrato">contrato</option>
                                        <option value="anexo">anexo</option>
                                        <option value="aditivo">aditivo</option>
                                    </FormControl>   
                                }
                                <InputButtonEdit
                                    style={{ backgroundColor: typeSelect ? '#00FA9A' : '#909090' }}
                                    className="col-md-2 col-sm-2 col-2"
                                    onClick={handleButtonTypeChange}
                                >
                                    <img width="20" height="20" src={typeSelect ? icoCheck : icoEdit} />
                                </InputButtonEdit>
                            </Div>

                        </FormGrup>
                        <FormGrup className="col-md-6 col-12" >
                            <Div className="d-flex col-md-12 m-0">
                                {
                                    !fileSelect ?
                                        <a 
                                            href={`${host.ip}/files/${file.path}`} 
                                            target="_blank"
                                        > <TextContent>{file.name}</TextContent></a>
                                    : 
                                    <>
                                        <FormFile
                                            multiple
                                            className="position-relative"
                                            required
                                            name="file"
                                            id="validationFormik108"
                                            onChange={handleInputFileChange}
                                            feedbackTooltip
                                            disabled={fileSelect ? false : true}
                                                // onChange={handleInputFilesChange}
                                        />
                                    </>
                                }
                                <InputButtonEdit
                                    style={{ backgroundColor: fileSelect ? '#00FA9A' : '#909090' }}
                                    className="col-md-2 col-sm-2 col-2"
                                    onClick={handleButtonFileChange}
                                >
                                    <img width="20" height="20" src={fileSelect ? icoCheck : icoEdit} />
                                </InputButtonEdit>
                            </Div>
                            {loadFile && <img width="30" height="30" style={{ marginLeft: '40%', marginTop: '5%' }} src={icoLoading} />}
                        </FormGrup>
                    </ContainContent>
                    <ContainContent>
                        <ButtonExclude
                            className="col-md-3 col-sm-6 col-12"
                            id="exlude"
                            name="btn_exclude"
                            type="button"
                            onClick={handleButtonDelete}
                        >
                            Excluir
                        </ButtonExclude> 
                    </ContainContent>

                </ColContent>

            </ContainContent >
        </>
    );
};


