import { Container, Row, Col, Form, Button, Dropdown } from 'react-bootstrap';
import Styled from 'styled-components';
import PerfectScrollbar from 'react-perfect-scrollbar';

export const TagName = Styled.div`
    transition:0.3s;
    border-radius:5px 0px 0px 5px;
    font-size:12px;
    padding:10px;
    background-color:#00CC5C;
    color:white;
    font-weight:bold;
`;

export const DivScrollSubject = Styled(PerfectScrollbar)`
    max-height:100px;
    padding:0px 30px;
`;

export const SeachContractInput = Styled(Dropdown)`
`;

export const SeachContractToogle = Styled(Dropdown.Toggle)`
background-color:white!important;
color:black!important;
height:40px;
text-align:start;
display:flex;
justify-content:space-between;
align-items:center;
    &:active{
        background-color:white!important;
        color:black!important;
    }
    &:hover{
    transition:0.3s;
    background-color:white!important;
    color:black!important;
    }
    &:focus{
        transition:0.3s;
        background-color:white!important;
    }
`;
export const SeachContractMenu = Styled(Dropdown.Menu)``;
export const SeachContractItem = Styled(Dropdown.Item)``;

export const ExcludeBtn = Styled(Button)`
    transition:0.3s;
    font-size:14px;
    padding:10px;
    background-color:#ff0000;
    color:white;
    font-weight:bold;
    border:none;
    border-radius:0px 5px 5px 0px;
        &:hover{
            transition:0.3s;
        background-color:#990000;
        color:white;
        }
        &:focus{
            transition:0.3s;
            background-color:#770000!important;
            color:white;
            border:none;
            box-shadow:none;
        }
`;


export const DivScroll = Styled(PerfectScrollbar)`
    max-height:450px;
    padding:20px;
`;


export const ButtonGrupReg = Styled(Button)`
    margin:5px 0;
    transition:0.3s;
    font-size:14px;
    background-color:#00CC5C;
    color:white;
    font-weight:bold;
    border:none;
    &:hover{
        transition:0.3s;
    background-color:#009945;
    color:white;
    }
    &:focus{
        transition:0.3s;
        background-color:#909090!important;
        color:white;
        border:none;
        box-shadow:none;
    }
`
export const ButtonGrupClose = Styled(Button)`
    margin:5px 0;
    transition:0.3s;
    font-size:14px;
    background-color:#ff9900;
    color:white;
    font-weight:bold;
    border:none;
    &:hover{
        transition:0.3s;
    background-color:#ff7700;
    color:white;
    }
    &:focus{
        transition:0.3s;
        background-color:#ff5500!important;
        color:white;
        border:none;
        box-shadow:none;
    }
`;

export const ContainNav = Styled(Container)`
    width:100%;
    margin:0;
    padding:0;
    display:flex;
    align-items:center;
    justify-content:center;
`;
export const ContainContent = Styled(Row)`
    margin:0;
    justify-content:center;
    align-items:center;
    display:flex;
    width:100%;
    padding:10px;
    border-radius:10px;
`;

export const ColContent = Styled(Col)`
margin-top:20px;
border-radius:10px;
background-color:white;
-webkit-box-shadow: -1px -4px 35px 7px rgba(0,0,0,0.16);
-moz-box-shadow: -1px -4px 35px 7px rgba(0,0,0,0.16);
box-shadow: -1px -4px 35px 7px rgba(0,0,0,0.16);
padding:10px;
display:flex;
justify-content:center;
align-items:center;
flex-direction:column;
`;

export const FormGrup = Styled(Form.Group)`

`;
export const FormLabel = Styled(Form.Label)`
    padding:0;
    margin:0;
`;
export const FormControl = Styled(Form.Control)`

`;

export const FormFile = Styled(Form.File)`

`;
export const FormCheck = Styled(Form.Check)`

`;
export const ButtonEnviar = Styled(Button)`
transition:0.3s;
width:100%;
margin:0px 15px;
background-color:#00CC5C;
color:white;
font-weight:bold;
border:none;
    &:hover{
        transition:0.3s;
    background-color:#009945;
    color:white;
    }
    &:focus{
        transition:0.3s;
        background-color:#909090!important;
        color:white;
        border:none;
        box-shadow:none;
    }
`;

export const NameArchive = Styled.h2`
    font-size:14px;
`;



export const TitleProgress = Styled.h2`
    font-size:18px;
    width:100%;
    text-align:center;
    margin-bottom:20px;
    margin-top:20px;
`;



export const ClosedModalButton = Styled(Button)`
transition:0.3s;
margin:0px 15px;
background-color:#990000;
color:white;
font-weight:bold;
border:none;
    &:hover{
        transition:0.3s;
    background-color:#770000;
    color:white;
    }
    &:focus{
        transition:0.3s;
        background-color:#550000!important;
        color:white;
        border:none;
        box-shadow:none;
    }
`;






