import React from "react";
import { Container, Row, Col, Form, Button } from "react-bootstrap";
import Styled from "styled-components";
import response from "../../../util/px2vw";
import PerfectScrollbar from "react-perfect-scrollbar";

export const DivScrollSubject = Styled(PerfectScrollbar)`
    max-height:30vh;
    padding:0px 30px;
`;

export const TagName = Styled.div`
    transition:0.3s;
    border-radius:5px 0px 0px 5px;
    font-size:12px;
    padding:10px;
    background-color:#00CC5C;
    color:white;
    font-weight:bold;
`;


export const TextContent = Styled.p`
    font-size:15px;
    max-width: 30ch;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
`;

export const ExcludeBtn = Styled(Button)`
    transition:0.3s;
    font-size:14px;
    padding:10px;
    background-color:#ff0000;
    color:white;
    font-weight:bold;
    border:none;
    border-radius:0px 5px 5px 0px;
        &:hover{
            transition:0.3s;
        background-color:#990000;
        color:white;
        }
        &:focus{
            transition:0.3s;
            background-color:#770000!important;
            color:white;
            border:none;
            box-shadow:none;
        }
`;

