import React, { useEffect, useState, useMemo } from "react";
import { useSelector } from "react-redux";
import socketio from "socket.io-client";

import api from "../../util/api";
import host from "../../config/host";

import {
  ListTopBar,
  NameArchive,
  ContainContent,
  ColContentMain,
  DivScroll,
  ContainContentBody,
  ListTitle,
} from "./styles";

import icoLoading from "../../assets/images/icoLoading.gif";

export default function () {
  const id = useSelector((state) => state.user.user.id);

  const [processList, setProcessList] = useState([]);

  useEffect(() => {
    const loadProcess = async () => {
      const { data } = await api.get("/process/list");

      const documentsInProcess = data.filter(
        (index) => index.waiting === false
      );
      const documentsInWaiting = data.filter((index) => index.waiting === true);

      setProcessList([...documentsInProcess, ...documentsInWaiting]);
    };

    loadProcess();

    setInterval(loadProcess, 10000);
  }, []);

  return (
    <ColContentMain className="col-md-12 col-sm-12 col-12">
      <ContainContentBody className="col-md-6 col-sm-12 col-12">
        <ListTopBar>
          <ListTitle>Arquivos processando</ListTitle>
          <ContainContent>
            <ContainContent className="col-md-3 col-3">
              <NameArchive>Contrato</NameArchive>
            </ContainContent>
            <ContainContent className="col-md-2 col-2">
              <NameArchive>Tipo</NameArchive>
            </ContainContent>
            <ContainContent className="col-md-3 col-3">
              <NameArchive>Arquivo</NameArchive>
            </ContainContent>
            <ContainContent className="col-md-2 col-2">
              <NameArchive>Status</NameArchive>
            </ContainContent>
          </ContainContent>
        </ListTopBar>
        <DivScroll>
          {processList.map((index) => (
            <ContainContent>
              <ContainContent className="col-md-3 col-3">
                <NameArchive>{index.contract_name}</NameArchive>
              </ContainContent>
              <ContainContent className="col-md-2 col-2">
                <NameArchive>{index.document}</NameArchive>
              </ContainContent>
              <ContainContent className="col-md-3 col-3">
                <NameArchive>{index.name}</NameArchive>
              </ContainContent>
              <ContainContent className="col-md-2 col-2">
                <NameArchive>
                  {index.waiting ? (
                    "em fila"
                  ) : (
                    <img width="25" height="25" src={icoLoading} />
                  )}
                </NameArchive>
              </ContainContent>
            </ContainContent>
          ))}
        </DivScroll>
      </ContainContentBody>
    </ColContentMain>
  );
}
