import React, { useState, useEffect, useMemo } from "react";
import { useParams, useHistory } from "react-router-dom";
import { useSelector } from "react-redux";
import socketio from "socket.io-client";
import { format, parseISO } from "date-fns";
import { toast } from "react-toastify";
import api from "../../../util/api";
import host from '../../../config/host';

import icoLoading from "../../../assets/images/icoLoading.gif";

import ModalAddThemes from "../addthemescp";

import {
  TextContent,
  TitleProgress,
  ContainNav,
  NameArchive,
  ContainContent,
  TagName,
  ExcludeBtn,
  ColContent2,
  ColContent,
  ButtonEnviar,
  FormGrup,
  FormLabel,
  FormControl,
  FormFile,
  Div,
  ButtonAdd,
  InputButtonEdit,
  ButtonExclude,
  FormCheck,
  SeachContractItem,
  SeachContractMenu,
  SeachContractToogle,
  SeachContractInput,
  ButtonGrupClose,
  ButtonGrupReg,
  DivScrollSubject,
} from "./styles";

import icoEdit from "../../../assets/images/img_388620.png";
import icoCheck from "../../../assets/images/checkEdit.png";

export default function EditCorrespondence() {
  const history = useHistory();

  const { document_id } = useParams();
  const { contract_id } = useParams();

  const userId = useSelector((state) => state.user.user.id);

  const [reload, setReload] = useState(0);
  const [reloadThemes, setReloadThemes] = useState(0);

  const [themesSelecteds, setThemesSelecteds] = useState([]);

  const [senderName, setsenderName] = useState("");
  const [senderNameSelect, setsenderNameSelect] = useState(false);

  const [code, setCode] = useState("");
  const [codeSelect, setCodeSelect] = useState(false);

  const [recipientName, setRecipientName] = useState("");
  const [recipientNameSelect, setRecipientNameSelect] = useState(false);

  const [dateView, setDateView] = useState("");
  const [sendDate, setSendDate] = useState('');
  const [sendDateSelect, setSendDateSelect] = useState(false);

  const [subject, setSubject] = useState("");
  const [subjectSelect, setSubjectSelect] = useState(false);

  const [aceptDate, setAceptDate] = useState("");
  const [aceptDateSelect, setAceptDateSelect] = useState(false);

  const [file, setFile] = useState(0);
  const [newFileId, setNewFileId] = useState([]);
  const [fileSelect, setFileSelect] = useState(false);
  const [loadFile, setLoadFile] = useState(false);

  const [type, setType] = useState("");
  const [typeSelect, setTypeSelect] = useState(false);

  const [description, setDescription] = useState("");
  const [descriptionSelect, setdescriptionSelect] = useState(false);

  const [impact, setImpact] = useState(null);

  const [openModalSelectThemes, setOpenModalSelectThemes] = useState(false);
  const [openModalEditSelectThemes, setOpenModalEditSelectThemes] = useState(false);

  const [textFilterRelation, setFilterTextRelation] = useState('');
  const [relationSelected, setRelationSelected] = useState({
    name: '',
    id: 0
  });
  const [correspondenceList, setCorrespondenceList] = useState([]);
  const [conrrespondenceRender, setCorrespondenceRender] = useState([]);
  const [relationEditSelected, setRelationEditSelected] = useState(false);

  const [onClickDelete, setOnClickDelete] = useState(false);

  useEffect(() => {
    async function loadCorrespondence() {
      const { data } = await api.get(`correspondences/get/${document_id}`);

      const {
        sender,
        recipient,
        code,
        themes: themesCorrespondence,
        subject,
        date,
        type,
        description,
        impact,
        File,
        Correspondence
      } = data;

      const dateFormatted = format(new Date(date), 'dd/MM/yyyy');

      if (Correspondence) {
        setRelationSelected({
          name: Correspondence.subject,
          id: Correspondence.id
        })
      };

      setsenderName(sender);
      setRecipientName(recipient);
      setCode(code);
      setSubject(subject);
      setDateView(dateFormatted);
      setType(type);
      setDescription(description);
      setFile(File);
      setImpact(impact);
      setThemesSelecteds(themesCorrespondence ? themesCorrespondence.split(",") : []);
    };
    loadCorrespondence();
  }, [reload]);

  useEffect(() => {
    async function loadCorrespondences() {
      const { data } = await api.get(`correspondences/list/${contract_id}`);

      const filterData = data.filter(index => index.id != document_id);

      const formattedData = filterData.map(index => {
        const { id, subject } = index;

        const obj = {
          id,
          name: subject
        };

        return obj;
      });

      setCorrespondenceList(formattedData);
      setCorrespondenceRender(formattedData);
    };

    loadCorrespondences();
  }, [])


  const handleInputsenderName = (data) => {
    setsenderName(data.target.value);
  };

  const handleInputRecipientName = (data) => {
    setRecipientName(data.target.value);
  };

  const handleInputCodeName = (data) => {
    setCode(data.target.value);
  };

  const hadleInputSubjectChange = (data) => {
    setSubject(data.target.value);
  };

  const handleInputsetSendDateChange = (data) => {
    setSendDate(data.target.value);
  };

  const handleInputDescriptionChange = (data) => {
    setDescription(data.target.value);
  };

  const handleInputTypeChange = (data) => {
    setType(data.target.value);
  };

  const handleInputFilterRelationChange = (data) => {
    const { target: { value } } = data;

    setFilterTextRelation(value);

    const correspondenceFilter = correspondenceList.filter(
      index => index.name.toLowerCase().indexOf(value.toLowerCase()) > -1
    );

    setCorrespondenceRender(correspondenceFilter);
  };

  const handleInputFileChange = async (data) => {
    const form = new FormData();
    form.append("file", data.target.files[0]);

    try {
      setLoadFile(true);
      const { data: { id } } = await api.post("files/upload", form);

      setNewFileId(id);
      setLoadFile(false);
    } catch (err) {
      toast.error(
        "Não foi possível carregar o PDF, por favor contate um administrador"
      );
    }
  };


  const handleButtonSelectRelation = correspondence => {
    setRelationSelected(correspondence);
  };

  const handleButtonEditSelect = async () => {
    if (!relationEditSelected) {
      setRelationEditSelected(true);
    } else {
      if (relationSelected.name) {
        await api.put(`correspondences/update/${document_id}`, {
          column: 'correspondence_id',
          content: relationSelected.id
        });

        setReload(reload + 1);
      };

      setRelationEditSelected(false);
    };
  };

  const handleButtonNameSenderChange = async () => {
    if (!senderNameSelect) {
      setsenderNameSelect(true);
    } else {
      if (senderName) {
        await api.put(`correspondences/update/${document_id}`, {
          column: 'sender',
          content: senderName
        });

        setReload(reload + 1);
      };

      setsenderNameSelect(false);
    }
  };

  const handleButtonSubjectChange = async () => {
    if (!subjectSelect) {
      setSubjectSelect(true);
    } else {
      if (subject) {
        await api.put(`correspondences/update/${document_id}`, {
          column: 'subject',
          content: subject
        });

        setReload(reload + 1);
      };

      setSubjectSelect(false);
    }
  };

  const handleButtonCodeNameChange = async () => {
    if (!codeSelect) {
      setCodeSelect(true);
    } else {
      if (code) {
        await api.put(`correspondences/update/${document_id}`, {
          column: 'code',
          content: code
        });

        setReload(reload + 1);
      };

      setCodeSelect(false);
    }
  };

  const handleButtonNameReciptChange = async () => {
    if (!recipientNameSelect) {
      setRecipientNameSelect(true);
    } else {

      if (recipientName) {
        await api.put(`correspondences/update/${document_id}`, {
          column: 'recipient',
          content: recipientName
        });

        setReload(reload + 1);
      };

      setRecipientNameSelect(false);
    }
  };

  const handleButtonSendDatePropChange = async () => {
    if (!sendDateSelect) {
      setSendDateSelect(true);
    } else {

      if (sendDate) {
        await api.put(`correspondences/update/${document_id}`, {
          column: 'date',
          content: sendDate
        });

        setReload(reload + 1);
      };

      setSendDateSelect(false);
    }
  };

  const handleButtonTypeChange = async () => {
    if (!typeSelect) {
      setTypeSelect(true);
    } else {
      await api.put(`correspondences/update/${document_id}`, {
        column: 'type',
        content: type
      });

      setReload(reload + 1);
      setTypeSelect(false);
    }
  };

  const handleButtonFilePropChange = async () => {
    if (!fileSelect) {
      setFileSelect(true);
    } else {

      if (loadFile) {
        return;
      };

      if (newFileId != 0) {
        await api.put(`correspondences/update/${document_id}`, {
          column: 'file_id',
          content: newFileId
        });

        setNewFileId(0);
        setReload(reload + 1);
      }

      setFileSelect(false);
    }
  };

  const handleButtonDescripPropChange = async () => {
    if (!descriptionSelect) {
      setdescriptionSelect(true);
    } else {
      if (description) {
        await api.put(`correspondences/update/${document_id}`, {
          column: 'description',
          content: description
        });

        setReload(reload + 1);
      };

      setdescriptionSelect(false);
    }
  };

  const handleLabelImpactChange = async () => {
    setImpact(!impact);

    await api.put(`correspondences/update/${document_id}`, {
      column: 'impact',
      content: !impact
    });
  };

  const handleButtonRemoveTheme = theme => {
    var themes = themesSelecteds;

    const p = themes.findIndex(index => index === theme);
    themes.splice(p, 1)

    setReloadThemes(reloadThemes + 1);
    setThemesSelecteds(themes);
  };

  const handleSaveThemes = async () => {
    await api.put(`correspondences/update/${document_id}`, {
      column: 'themes',
      content: themesSelecteds.toString()
    });

    setReload(reload + 1);
  };

  const handleButtonDelete = async () => {
    if (!onClickDelete) {
      return setOnClickDelete(true);
    };

    await api.delete(`correspondences/delete/${document_id}`);

    history.push('/');
  };

  return (
    <>
      <ModalAddThemes
        openModalSelectThemes={openModalSelectThemes}
        setOpenModalSelectThemes={setOpenModalSelectThemes}
        contractId={contract_id}
        themesSelecteds={themesSelecteds}
        setThemesSelecteds={setThemesSelecteds}
      />
      <ContainContent style={{ paddingBottom: "50px" }}>
        <ColContent className="col-md-7 col-sm-7 col--12">
          <NameArchive>Edição de correspondência</NameArchive>

          <ContainContent>
            <FormGrup className="col-md-8 col-12">
              <FormLabel>Assuntos destaque:</FormLabel>
              <DivScrollSubject>
                <ContainContent className="align-items:center">
                  {
                    setReloadThemes &&
                    themesSelecteds.map(index => (
                      <FormGrup className="d-flex col-md-6 col-6">
                        <TagName className="col-md-9 col-9">
                          ! {index}
                        </TagName>
                        <ExcludeBtn
                          className="col-md-3 col-3"
                          onClick={() => handleButtonRemoveTheme(index)}
                        >
                          X
                          </ExcludeBtn>
                      </FormGrup>
                    ))
                  }
                </ContainContent>
              </DivScrollSubject>
            </FormGrup>
            <FormGrup className="col-md-3 col-12">
              <ButtonAdd onClick={() => setOpenModalSelectThemes(true)} >
                Adicionar assunto
              </ButtonAdd>
              <ButtonGrupReg onClick={handleSaveThemes}>
                Salvar assuntos
              </ButtonGrupReg>
            </FormGrup>
          </ContainContent>

          <ContainContent>
            <FormGrup className="col-md-4 col-12">
              <FormLabel>Remetente:</FormLabel>
              <Div className="d-flex col-md-12 m-0">
                <FormControl
                  id="senderName"
                  value={senderName}
                  className="col-md-10 col-sm-10 col-10"
                  disabled={senderNameSelect ? false : true}
                  type="text"
                  onChange={handleInputsenderName}
                />
                <InputButtonEdit
                  style={{
                    backgroundColor: senderNameSelect ? "#00FA9A" : "#909090",
                  }}
                  className="col-md-2 col-sm-2 col-2"
                  onClick={handleButtonNameSenderChange}
                >
                  <img
                    width="20"
                    height="20"
                    src={senderNameSelect ? icoCheck : icoEdit}
                  />
                </InputButtonEdit>
              </Div>
            </FormGrup>
            <FormGrup className="col-md-4 col-12">
              <FormLabel>Destinatario:</FormLabel>
              <Div className="d-flex col-md-12 m-0">
                <FormControl
                  id="recipient_id"
                  value={recipientName}
                  className="col-md-10 col-sm-10 col-10"
                  disabled={recipientNameSelect ? false : true}
                  type="text"
                  onChange={handleInputRecipientName}
                />
                <InputButtonEdit
                  style={{
                    backgroundColor: recipientNameSelect
                      ? "#00FA9A"
                      : "#909090",
                  }}
                  className="col-md-2 col-sm-2 col-2"
                  onClick={handleButtonNameReciptChange}
                >
                  <img
                    width="20"
                    height="20"
                    src={recipientNameSelect ? icoCheck : icoEdit}
                  />
                </InputButtonEdit>
              </Div>
            </FormGrup>
            <FormGrup className="col-md-4 col-12">
              <FormLabel>Codigo:</FormLabel>
              <Div className="d-flex col-md-12 m-0">
                <FormControl
                  id="recipient_id"
                  value={code}
                  className="col-md-10 col-sm-10 col-10"
                  disabled={codeSelect ? false : true}
                  type="text"
                  onChange={handleInputCodeName}
                />
                <InputButtonEdit
                  style={{
                    backgroundColor: codeSelect ? "#00FA9A" : "#909090",
                  }}
                  className="col-md-2 col-sm-2 col-2"
                  onClick={handleButtonCodeNameChange}
                >
                  <img
                    width="20"
                    height="20"
                    src={codeSelect ? icoCheck : icoEdit}
                  />
                </InputButtonEdit>
              </Div>
            </FormGrup>
            <FormGrup className="col-md-8 col-12">
              <FormLabel>Assunto:</FormLabel>
              <Div className="d-flex col-md-12 m-0">
                <FormControl
                  id="recipient_id"
                  value={subject}
                  className="col-md-10 col-sm-10 col-10"
                  disabled={subjectSelect ? false : true}
                  type="text"
                  onChange={hadleInputSubjectChange}
                />
                <InputButtonEdit
                  style={{
                    backgroundColor: subjectSelect ? "#00FA9A" : "#909090",
                  }}
                  className="col-md-2 col-sm-2 col-2"
                  onClick={handleButtonSubjectChange}
                >
                  <img
                    width="20"
                    height="20"
                    src={subjectSelect ? icoCheck : icoEdit}
                  />
                </InputButtonEdit>
              </Div>
            </FormGrup>
            <FormGrup className="col-md-4 col-12">
              <FormLabel>Data de envio:</FormLabel>
              <Div className="d-flex col-md-12 m-0">
                {
                  sendDateSelect ?
                    <FormControl
                      icoLoading
                      id="send_date"
                      className="col-md-10 col-sm-10 col-10"
                      disabled={sendDateSelect ? false : true}
                      type="date"
                      onChange={handleInputsetSendDateChange}
                    />
                    :
                    <FormControl
                      type="text"
                      className="col-md-10 col-sm-10 col-10"
                      value={dateView}
                      disabled
                    />
                }
                <InputButtonEdit
                  style={{
                    backgroundColor: sendDateSelect ? "#00FA9A" : "#909090",
                  }}
                  className="col-md-2 col-sm-2 col-2"
                  onClick={handleButtonSendDatePropChange}
                >
                  <img
                    width="20"
                    height="20"
                    src={sendDateSelect ? icoCheck : icoEdit}
                  />
                </InputButtonEdit>
              </Div>
            </FormGrup>
            <FormGrup className="col-md-6 col-12">
              <FormLabel>Tipo:</FormLabel>
              <Div className="d-flex col-md-12 m-0">
                <FormControl
                  onChange={handleInputTypeChange}
                  disabled={typeSelect ? false : true}
                  value={type}
                  className="col-md-10 col-10"
                  as="select"
                >
                  <option value="email">E-mail</option>
                  <option value="enviada">Correspondência enviada</option>
                  <option value="recebido">Correspondência recebida</option>
                </FormControl>
                <InputButtonEdit
                  style={{
                    backgroundColor: typeSelect ? "#00FA9A" : "#909090",
                  }}
                  className="col-md-2 col-sm-2 col-2"
                  onClick={handleButtonTypeChange}
                >
                  <img
                    width="20"
                    height="20"
                    src={typeSelect ? icoCheck : icoEdit}
                  />
                </InputButtonEdit>
              </Div>
            </FormGrup>
            <FormGrup className="col-md-6 col-12 ">
              <FormLabel>{fileSelect ? 'Selecione o arquivo:' : 'Arquivo'}</FormLabel>
              <Div className="d-flex col-md-12 m-0">
                {
                  fileSelect ?
                    <FormFile
                      className="position-relative"
                      required
                      name="file"
                      id="validationFormik107"
                      feedbackTooltip
                      onChange={handleInputFileChange}
                    />
                    :
                    <a
                      href={`${host.ip}/files/${file.path}`}
                      target="_blank"
                    > <TextContent>{file.name}</TextContent></a>
                }
                <InputButtonEdit
                  style={{
                    backgroundColor: fileSelect ? "#00FA9A" : "#909090",
                  }}
                  className="col-md-2 col-sm-2 col-2 m-0"
                  onClick={handleButtonFilePropChange}
                >
                  <img
                    width="20"
                    height="20"
                    src={fileSelect ? icoCheck : icoEdit}
                  />
                </InputButtonEdit>
              </Div>
              {loadFile && (
                <img
                  width="30"
                  height="30"
                  style={{ marginLeft: "40%", marginTop: "5%" }}
                  src={icoLoading}
                />
              )}
            </FormGrup>
            <FormGrup className="col-md-12 col-12">
              <FormLabel className="col-md-12 col-12">
                Relação:
              </FormLabel>
              <Div className="d-flex col-md-12 m-0">
              <SeachContractInput className="col-md-10 col-10 p-0">
                <SeachContractToogle
                  className="col-md-12 col-12"
                  id="dropdown-custom-components"
                  disabled={relationEditSelected ? false : true}
                >
                  {relationSelected.name}
                </SeachContractToogle>
                <SeachContractMenu>
                  <FormControl
                    autoFocus
                    className="mx-3 my-2 w-auto"
                    placeholder="Consultar..."
                    value={textFilterRelation}
                    onChange={handleInputFilterRelationChange}
                  />

                  {conrrespondenceRender.map(index => (
                    <SeachContractItem className={index.name}
                      onClick={() => handleButtonSelectRelation(index)}
                    >
                      {index.name}
                    </SeachContractItem>
                  ))}
                </SeachContractMenu>
              </SeachContractInput>
              <InputButtonEdit
                style={{
                  backgroundColor: relationEditSelected ? "#00FA9A" : "#909090",
                }}
                className="col-md-2 col-2"
                onClick={handleButtonEditSelect}
              >
                <img
                  width="20"
                  height="20"
                  src={relationEditSelected ? icoCheck : icoEdit}
                />
              </InputButtonEdit>
                </Div>
            </FormGrup>

            <FormCheck
              type="switch"
              id="custom-switch"
              label="Ocorrência importante"
              onChange={handleLabelImpactChange}
              id="theme"
              checked={impact}
            />
            <FormGrup className="col-md-12 col-12">
              <FormLabel>Descrição:</FormLabel>
              <Div className="d-flex col-md-12 m-0">
                <FormControl
                  disabled={descriptionSelect ? false : true}
                  className="col-md-10 col-10"
                  as="textarea"
                  rows="5"
                  onChange={handleInputDescriptionChange}
                  value={description}
                />
                <InputButtonEdit
                  style={{
                    backgroundColor: descriptionSelect ? "#00FA9A" : "#909090",
                  }}
                  className="col-md-2 col-sm-2 col-2"
                  onClick={handleButtonDescripPropChange}
                >
                  <img
                    width="20"
                    height="20"
                    src={descriptionSelect ? icoCheck : icoEdit}
                  />
                </InputButtonEdit>
              </Div>
            </FormGrup>
          </ContainContent>
          <ContainContent>
            <ButtonExclude
              className="col-md-3 col-sm-6 col-12"
              id="exlude"
              name="btn_exclude"
              type="button"
              onClick={handleButtonDelete}
            >
              Excluir
            </ButtonExclude>
          </ContainContent>
        </ColContent>
      </ContainContent>
    </>
  );
}
