import React from 'react';
import { Container, Row, Col, Form, Button } from 'react-bootstrap';
import Styled from 'styled-components';
import PerfectScrollbar from 'react-perfect-scrollbar';

import px2vw from '../../../../util/px2vw';

export const ClosedModalButton = Styled(Button)`
transtion:0.3s;
margin:0px 15px;
background-color:#990000;
color:white;
font-weight:bold;
border:none;
    &:hover{
        transtion:0.3s;
    background-color:#770000;
    color:white;
    }
    &:focus{
        transtion:0.3s;
        background-color:#550000!important;
        color:white;
        border:none;
        box-shadow:none;
    }
`;

export const DivScroll = Styled(PerfectScrollbar)`
    max-height:450px;
    padding:20px;
`;


export const ContainNav = Styled(Container)`
    width:100%;
    margin:0;
    padding:0;
    display:flex;
    align-items:center;
    justify-content:center;
    
`;
export const ContainContent = Styled(Row)`
    margin:0;
    justify-content:center;
    align-items:center;
    display:flex;
    width:100%;
    padding:10px;
    border-radius:10px;
`;
export const ColContent2 = Styled(Col)`
margin-top:20px;
-webkit-box-shadow: -1px -4px 35px 7px rgba(0,0,0,0.16);
-moz-box-shadow: -1px -4px 35px 7px rgba(0,0,0,0.16);
box-shadow: -1px -4px 35px 7px rgba(0,0,0,0.16);
padding:10px;
margin-left:30px;
background-color:white;
border-radius:10px;

@media (max-width: 620px) { 
    margin-left:0px;
  }

`;
export const ColContent = Styled(Col)`
margin-top:20px;
border-radius:10px;
background-color:white;
-webkit-box-shadow: -1px -4px 35px 7px rgba(0,0,0,0.16);
-moz-box-shadow: -1px -4px 35px 7px rgba(0,0,0,0.16);
box-shadow: -1px -4px 35px 7px rgba(0,0,0,0.16);
padding:10px;
display:flex;
justify-content:center;
align-items:center;
flex-direction:column;
`;

export const FormGrup = Styled(Form.Group)`

`;
export const FormLabel = Styled(Form.Label)`
    padding:0;
    margin:0;
`;
export const FormControl = Styled(Form.Control)`

`;

export const FormFile = Styled(Form.File)`

`;
export const FormCheck = Styled(Form.Check)`

`;
export const ButtonEnviar = Styled(Button)`
transtion:0.3s;
width:100%;
margin:0px 15px;
background-color:#00CC5C;
color:white;
font-weight:bold;
border:none;
    &:hover{
        transtion:0.3s;
    background-color:#009945;
    color:white;
    }
    &:focus{
        transtion:0.3s;
        background-color:#909090!important;
        color:white;
        border:none;
        box-shadow:none;
    }
`;

export const NameArchive = Styled.h2`
    font-size:14px;
`;

export const ExitButton = Styled.button`
border-radius:5px;
background-color:#C9130A;
color:white;
border:none;
padding:2px 10px;
font-weight:bold;
&:hover{
    background-color:#D60602;
    border:none;
}
&:focus{
    background-color:#BD0009;
    border:none;
}
&:active{
    background-color:#BD0009;
    border:none;
}
`;

export const TitleProgress = Styled.h2`
    font-size:18px;
    width:100%;
    text-align:center;
    margin-bottom:20px;
    margin-top:20px;
`;




