import React, { useState, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { toast } from 'react-toastify';
import history from '../../util/history';

import { signOut } from '../../redux/modules/auth/actions';

import {
    NavigateBar,
    NavigateButtonRight,
    NavigateButtonLeft,
    ContainNav,
    NavbarContent,
    IcoToggle,
    ConfigIco,
    NavbarMenu,
    BoxInfoUser,
    SubNav,
    IcoMenu,
    UserBox,
    BoxPhotoUser,
    UserConfigLink,
    NameUser,
    SubNavLink
} from './styles';

import icoConfigImg from '../../assets/images/icoconfig.png';
import menuImg1 from '../../assets/images/papel.png';
import menuImg2 from '../../assets/images/linha-do-tempo.png';
import menuImg3 from '../../assets/images/procurar.png';
import menuImg4 from '../../assets/images/sair.png';
import menuImg5 from '../../assets/images/icoprogress.png'


function Header() {
    const dispatch = useDispatch();

    const signed = useSelector(state => state.auth.signed);

    const [page, setPage] = useState(window.location.pathname);
    const userName = useSelector(state => state.user);

    if (page == '/home') {
        var progress = '#374F4F';
        var timeLine = '#374F4F';
        var minerdoc = '#374F4F';
        var home = '#45E68D';
    } else if (page == '/timeline') {
        var progress = '#374F4F';
        var timeLine = '#45E68D';
        var minerdoc = '#374F4F';
        var home = '#374F4F';
    } else if (page == '/miner-doc') {
        var progress = '#374F4F';
        var timeLine = '#374F4F';
        var minerdoc = '#45E68D';
        var home = '#374F4F';
    } else if (page == '/process') {
        var progress = '#45E68D';
        var timeLine = '#374F4F';
        var minerdoc = '#374F4F';
        var home = '#374F4F';
    }

    return (

        <>


            {signed ? <ContainNav>
                <NavbarContent className="col-lg-10 col-md-10 col-sm-11 col-12" collapseOnSelect expand="lg" bg="dark" variant="dark">
                    <IcoToggle aria-controls="responsive-navbar-nav" />
                    <NavbarMenu.Collapse id="responsive-navbar-nav">
                        <SubNav className="col-lg-12 col-md-12 col-sm-12 col-12">
                            <SubNavLink style={{ borderBottom: `solid 3px ${home}` }}
                                onClick={function () {
                                    history.push('/home')
                                    setPage(window.location.pathname)
                                }
                                }
                            >
                                <IcoMenu src={menuImg1} />Contratos
                            </SubNavLink>
                            <SubNavLink style={{ borderBottom: `solid 3px ${timeLine}` }} 
                             onClick={function () {
                                history.push('/timeline')
                                setPage(window.location.pathname)
                            }}
                            ><IcoMenu src={menuImg2} />TimeLine</SubNavLink>
                            <SubNavLink style={{ borderBottom: `solid 3px ${minerdoc}` }}
                                onClick={function () {
                                    history.push('/miner-doc')
                                    setPage(window.location.pathname)
                                }}
                            ><IcoMenu src={menuImg3} />Minerar Documento</SubNavLink>
                            <SubNavLink style={{ borderBottom: `solid 3px ${progress}` }}
                                onClick={function () {
                                    history.push('/process')
                                    setPage(window.location.pathname)
                                }}
                            >
                                <IcoMenu src={menuImg5} />Progresso de arquivos
                            </SubNavLink>
                            <SubNavLink
                                onClick={() => {
                                    dispatch(signOut());
                                    history.push('/');
                                }}
                            >
                                <IcoMenu src={menuImg4} />Sair
                            </SubNavLink>
                        </SubNav>
                    </NavbarMenu.Collapse>
                </NavbarContent>
                <UserBox className="col-lg-2 col-md-2 col-sm-1 col-12">
                    <BoxInfoUser>
                        <NameUser>{userName && userName.user.name}</NameUser>
                        <UserConfigLink href="#" onClick={() => {
                            history.push('/edit-user')
                            setPage(window.location.pathname)
                        }}></UserConfigLink>
                    </BoxInfoUser>
                </UserBox>
            </ContainNav> : <> </>}
          
        </>
    );
}



export default Header;
