import { addDays, isWithinInterval, parseISO } from 'date-fns';

export default function filterDate(documents, dateInitial, dateFinal)
{
    documents = documents.filter(index => isWithinInterval( 
        new Date(index.props.date),
        { start: new Date(dateInitial), end: addDays(new Date(dateFinal), 1) }
    ));

    return documents;
};