import { format } from 'date-fns'

export default function formatData(data)
{
    const dataFormatted = [];

    dataFormatted.push(...data.FilesDefaults.map(index => {
        const { id, date, type, File: { name }, File } = index;
        
        const obj = {
            id,
            name,
            date,
            dateView: format(new Date(date), 'dd/MM/yyyy'),
            type: 'padrao',
            File
        };

        if(type === 'contrato'){
            obj.typeView = type;
        } else {
            obj.typeView = type + ' do contrato';
        };

        return obj;
    }));

    dataFormatted.push(...data.Correspondences.map(index => {
        const { id, code, subject, date, File } = index;

        const obj = {
            id,
            code,
            name: subject,
            date,
            dateView: format(new Date(date), 'dd/MM/yyyy'),
            type: 'correspondencia',
            typeView: 'correspondência',
            File
        };

        return obj;
    }));

    dataFormatted.push(...data.NotesMeetings.map(index => {
        const { id, name, date, code, File } = index;

        const obj = {
            id,
            name,
            code,
            date,
            dateView: format(new Date(date), 'dd/MM/yyyy'),
            type: 'ata',
            typeView: 'ata de reunião',
            File
        };

        return obj;
    }));

    dataFormatted.push(...data.TechnicalProposals.map(index => {
        const { id, name, accept_date, File } = index;

        const obj = {
            id,
            name,
            date: accept_date,
            dateView: format(new Date(accept_date), 'dd/MM/yyyy'),
            type: 'proposta',
            typeView: 'proposta técnica',
            File
        };

        return obj;
    }));

    return dataFormatted;
};