export const PopUp = Styled.div`
    z-index: 10;
    word-wrap:break-word;
    visibility:hidden;
    background-color:#33cce5;
    position:absolute;
    padding: 10px 10px;
    border-radius:10px;
    line-height:1.2;
    max-width:550px;
    min-width:550px;
    max-height:150px;
    align-items:center
    justify-content:center;
    display:flex;
    cursor:pointer;
    bottom:95%;
    right:-290px;
    :after{ /*Triangulo*/
    content: "";
    width: 0;
    height: 0;
    position: absolute;
    border-right: 15px solid transparent;
    border-top: 15px solid #33cce5;
    border-bottom: 15px solid transparent;
    top:97%; 
    left: 48%;
}
`;

export const BoxPopUp = Styled.div`
    position:relative;
    font-size:12px;
    animation: fadeIn;
    transition: all 0.4s;
    display: inline-block;
`;

export const PreviewIco = Styled.div`
    cursor:pointer;
    &:hover ${PopUp}{
        visibility:visible;
    }
`;





