export default function formatData(data) {
  const formattedData = [];

  data.FilesDefaults = data.FilesDefaults.filter((index) => index.ready);

  data.FilesDefaults.forEach((index) => {
    const {
      id,
      type,
      date,
      File: { name },
      File,
    } = index;

    var obj = {
      _id: id,
      start: date,
      editable: false,
      props: {
        type,
        date,
        File,
      },
    };

    if (type === "contrato") {
      obj.content = `<div id="${id}:anexo;" style="padding:5px;">${
        name || " "
      }</br>[contrato]</div>`;
    } else if (type === "aditivo") {
      obj.content = `<div id="${id}:anexo;" style="background-color:#B0E0E6;padding:5px;">${
        name || " "
      }</br>[aditivo do contrato]</div>`;
    } else if (type === "anexo") {
      obj.content = `<div id="${id}:anexo;" style="background-color:#FFF8DC;padding:5px;">${
        name || " "
      }</br>[anexo do contrato]</div>`;
    }

    formattedData.push(obj);
  });

  data.Correspondences = data.Correspondences.filter((index) => index.ready);

  data.Correspondences.forEach((index) => {
    const { id, subject, date, type, themes, impact, File } = index;

    const obj = {
      _id: id,
      content: `<div id="${id}:correspondencia;" style="background-color:#FFFF00;padding:5px;">${
        subject || " "
      }</br>[correspondência ${type}]</div>`,
      start: date,
      editable: false,
      props: {
        type: "correspondencia",
        subtype: type,
        date,
        File,
        impact,
        themes,
      },
    };

    formattedData.push(obj);
  });

  data.NotesMeetings = data.NotesMeetings.filter((index) => index.ready);

  data.NotesMeetings.forEach((index) => {
    const { id, name, date, themes, File, impact } = index;

    const obj = {
      _id: id,
      content: `<div id="${id}:ata;" style="background-color:#7FFF00;padding:5px;">${
        name || " "
      }<br/>[ata de reunião]</div>`,
      start: date,
      editable: false,
      props: {
        type: "ata",
        date,
        File,
        impact,
        themes,
      },
    };

    formattedData.push(obj);
  });

  data.TechnicalProposals = data.TechnicalProposals.filter(
    (index) => index.ready
  );

  data.TechnicalProposals.forEach((index) => {
    const { id, name, accept_date, File } = index;

    const obj = {
      _id: id,
      content: `<div id="${id}:proposta;" style="background-color:#F5FFFA;padding:5px;">${
        name || " "
      }</br>[proposta técnica]</div>`,
      start: accept_date,
      editable: false,
      props: {
        type: "proposta",
        date: accept_date,
        File,
      },
    };

    formattedData.push(obj);
  });

  return formattedData;
}
