import React, { useEffect, useState, useMemo } from "react";
import { useParams, useSelector } from "react-router-dom";
import Modal from "react-modal";
import { parseISO, format } from "date-fns";
import socketio from "socket.io-client";
import { toast } from "react-toastify";
import api from "../../../../util/api";

import {
  ColModal,
  RowModal,
  BtnAdd,
  BtnRemove,
  FormLabel,
  FormGrup,
  ScrollLimiter,
  FormControl,
} from "./styles";

export default function ({
  openModalSelectThemes,
  setOpenModalSelectThemes,
  themesSelecteds,
  setThemesSelecteds,
}) {
  const { id } = useParams();

  const [themes, setThemes] = useState([]);
  const [renderThemes, setRenderThemes] = useState([]);
  const [textFilter, setTextFilter] = useState('');
  

  const handleFormSubmit = (data) => {
    data.preventDefault();

    const alldocuments = document.querySelectorAll(".theme");

    const arrayThemesSelecteds = [];

    alldocuments.forEach((index) => {
      const { checked, value, id } = index;

      if (checked) {
        const existsValue = themesSelecteds.find((index) => index.id === id);

        if (existsValue) return;

        arrayThemesSelecteds.push({
          value,
          id,
        });
      }
    });

    if (arrayThemesSelecteds[0]) {
      setThemesSelecteds([...themesSelecteds, ...arrayThemesSelecteds]);
      setOpenModalSelectThemes(false);
    }
  };

  useEffect(() => {
    async function loadThemes() {
      const { data } = await api.get(`themes/list/${id}`);

      setThemes(data);
      setRenderThemes(data);
    }

    loadThemes();
  }, []);

  const handleNameFilterChange = (data) => {
    const { target: { value } } = data;
    
    setTextFilter(value);

    const themesFilter = themes.filter(index => index.name.toLowerCase().indexOf(value.toLowerCase()) > -1);

    setRenderThemes(themesFilter);
  };

  return (
    <Modal
      style={{
        overlay: {
          backgroundColor: "rgba(25,25,25, .8)",
        },
        content: {
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
          padding: "0",
          top: "0",
          bottom: "0",
          left: "0",
          right: "0",
          border: "none",
          backgroundColor: "rgba(25,25,25, .0)",
          margin: "0",
          borderRadius: "10px",
          height: "100vh",
          width: "100vw",
        },
      }}
      isOpen={openModalSelectThemes}
      onRequestClose={() => setOpenModalSelectThemes(false)}
    >
      <RowModal>
        <ColModal className="col-md-4 col-4">
          <ScrollLimiter>
            <RowModal>
              <FormGrup className="col-md-12 col-12">
              <FormLabel className="col-m-6 col-6">Pesquisar: </FormLabel>
                <FormControl  
                  autoFocus
                  className="mx-3 my-2 w-auto"
                  placeholder="Consultar..."
                  onChange={handleNameFilterChange}
                  value={textFilter}
                type="text" />
              </FormGrup>
              {renderThemes.map((index) => (
                <FormGrup className="col-md-6 col-6">
                  <RowModal>
                    <FormLabel className="col-m-6 col-6" for={index.id}>
                      {index.name}
                    </FormLabel>
                    <input
                      style={{ width: "20px", height: "20px" }}
                      className="col-m-6 col-6"
                      type="checkbox"
                      id={index.id}
                      value={index.name}
                      class="theme"
                    />
                  </RowModal>
                </FormGrup>
              ))}
            </RowModal>
          </ScrollLimiter>
        </ColModal>

        <ColModal className="col-md-2 col-2">
          <RowModal>
            <BtnAdd className="col-md-6 col-12" onClick={handleFormSubmit}>
              Adicionar
            </BtnAdd>
            <BtnRemove
              className="col-md-6 col-12"
              onClick={() => setOpenModalSelectThemes(false)}
            >
              Cancelar
            </BtnRemove>
          </RowModal>
        </ColModal>
      </RowModal>
    </Modal>
  );
}
