import React, { useState, useEffect, useMemo } from 'react';
import { useParams, useHistory } from 'react-router-dom';
import { useSelector } from 'react-redux';
import socketio from 'socket.io-client';
import { format } from 'date-fns';
import { toast } from 'react-toastify';
import { useDispatch } from 'react-redux';

import { setSelected } from '../../redux/modules/contractselected/action';

import api from '../../util/api';

import ModalTec from './components/registertecproposal';
import ModalNotes from './components/registernotesmeeting';
import ModalCorrespondences from './components/registercorrespondence';
import ModalThemes from './components/registerthemes';
import ModalAnexo from './components/registeranexos';
import ContractDelete from './components/contractdelete';

import host from '../../config/host';

import ListArchive from './components/listarchive';

import {
    TitleProgress,
    ContainContent,
    ColContent2,
    ColContent,
    FormGrup,
    FormLabel,
    FormControl,
    ButtonMenuView,
    Div,
    InputButtonEdit,
    ButtonExclude,
    ColRow,
    ColContentInter,
    RowScroll,
} from './styles';

import icoEdit from '../../assets/images/img_388620.png';
import icoCheck from '../../assets/images/checkEdit.png';

export default function App() {
    const history = useHistory();
    const dispatch = useDispatch();

    const { id } = useParams();

    const userId = useSelector(state => state.user.user.id);

    const [cotractName, setContractName] = useState('');
    const [cotractNameSelected, setCotractNameSelected] = useState(false);
    const [reload, setReload] = useState(0);

    const [contractor, setContractor] = useState('');
    const [contractorSelected, setContractorSelected] = useState(false);

    const [hired, setHired] = useState('');
    const [hiredSelected, setHiredSelected] = useState(false);

    const [signDate, setSignDate] = useState('');
    const [signSelected, setSignSelected] = useState(false);

    const [description, setDescription] = useState('');
    const [descriptionSelected, setDescriptionSelected] = useState(false);

    const [openModalAnexo, setOpenModalAnexo] = useState(false);
    const [openModalTec, setOpenModalTec] = useState(false);
    const [openModalNotes, setOpenModalNotes] = useState(false);
    const [openModalCorrespondence, setOpenModalCorrespondence] = useState(false);
    const [openModalDelete, setOpenModalDelete] = useState(false);
    const [openModalThemes, setOpenModalThemes] = useState(false);

    const [reloadThemes, setReloadThemes] = useState(0);
    const [reloadList, setReloadList] = useState(0);

    const socket = useMemo(() => socketio(host.ip, {
        transports: ['websocket'],
        query: {
            user: userId
        }
    }), [userId]);

    useEffect(() => {
        socket.on('finish-process', data => {
            const { contract_name, name } = data;

            toast.info(`O OCR do arquivo "${name}" do contrato "${contract_name}" terminou de ser processado`)
        });
    }, [socket]);

    useEffect(() => {
        dispatch(setSelected(id));
    }, []);

    useEffect(() => {
        async function loadContract() {
            const { data } = await api.get(`contract/get/${id}`);

            const { name, contractor, hired, date, description } = data;

            const dateFormatted = format(new Date(date), 'yyyy-MM-dd');

            setContractName(name);
            setContractor(contractor);
            setHired(hired);
            setSignDate(dateFormatted);
            setDescription(description);
        };

        loadContract();
    }, [reload]);

    const handleInputContractNameChange = data => {
        setContractName(data.target.value);
    };

    const handleInputContractorChange = data => {
        setContractor(data.target.value);
    };

    const handleButtonContractorChange = async () => {
        if (!contractorSelected) {
            return setContractorSelected(true);
        }

        setContractorSelected(false);

        await api.put(`contract/update/${id}`, {
            column: 'contractor',
            content: contractor
        });
    };

    const handleInputHiredChange = data => {
        setHired(data.target.value);
    };

    const handleContractNameHiredChange = async () => {
        if (!cotractNameSelected) {
            return setCotractNameSelected(true);
        }

        setCotractNameSelected(false);

        await api.put(`contract/update/${id}`, {
            column: 'name',
            content: cotractName
        });
    };

    const handleButtonHiredChange = async () => {
        if (!hiredSelected) {
            return setHiredSelected(true);
        }

        setHiredSelected(false);

        await api.put(`contract/update/${id}`, {
            column: 'hired',
            content: hired
        });
    };

    const handleInputSignChange = data => {
        setSignDate(data.target.value);


        console.log(data.target.value);
    };

    const handleButtonSignChange = async () => {
        if (!signSelected) {
            return setSignSelected(true);
        };

        const firstP = signDate[0]
        if (firstP === 0 || signDate.length !== 10) {
            return toast.error("Preencha a data corretamente");
        };

        setSignSelected(false);


        await api.put(`contract/update/${id}`, {
            column: 'date',
            content: signDate
        });
    };

    const handleInputDescriptionChange = data => {
        setDescription(data.target.value);
    };

    const handleButtonDescriptionChange = async () => {
        if (!descriptionSelected) {
            return setDescriptionSelected(true);
        }

        setDescriptionSelected(false);

        await api.put(`contract/update/${id}`, {
            column: 'description',
            content: description
        });
    };

    return (
        <>
            <ModalAnexo reloadList={reloadList} setReloadList={setReloadList} openModalAnexo={openModalAnexo} setOpenModalAnexo={setOpenModalAnexo} contractId={id} />
            <ModalTec reloadList={reloadList} setReloadList={setReloadList} openModalTec={openModalTec} setOpenModalTec={setOpenModalTec} contractId={id} />
            <ModalNotes reloadList={reloadList} setReloadList={setReloadList}s openModalNotes={openModalNotes} setOpenModalNotes={setOpenModalNotes} contractId={id} reloadThemes={reloadThemes} />
            <ModalCorrespondences reloadList={reloadList} setReloadList={setReloadList} openModalCorrespondence={openModalCorrespondence} setOpenModalCorrespondence={setOpenModalCorrespondence} contractId={id} reloadThemes={reloadThemes} />
            <ModalThemes openModalThemes={openModalThemes} setOpenModalThemes={setOpenModalThemes} contractId={id} reloadThemes={reloadThemes} setReloadThemes={setReloadThemes} />
            <ContractDelete openModalDelete={openModalDelete} setOpenModalDelete={setOpenModalDelete} contractId={id} />

            <ContainContent >
                <ColContent className="col-md-7 col-sm-7 col--12" >
                    <ContainContent>
                        <FormGrup className="col-md-12 col-12" >
                            <FormLabel>Nome do contrato:</FormLabel>
                            <Div className="d-flex col-md-12 m-0">
                                <FormControl
                                    id="contractortName"
                                    className="col-md-10 col-sm-10 col-10"
                                    value={cotractName}
                                    disabled={cotractNameSelected ? false : true}
                                    type="text"
                                    onChange={handleInputContractNameChange}
                                />
                                <InputButtonEdit
                                    onClick={handleContractNameHiredChange}
                                    style={{ backgroundColor: cotractNameSelected ? '#00FA9A' : '#909090' }}
                                    className="col-md-2 col-sm-2 col-2"
                                >
                                    <img width="20" height="20" src={cotractNameSelected ? icoCheck : icoEdit} />
                                </InputButtonEdit>
                            </Div>
                        </FormGrup>
                        <FormGrup className="col-md-6 col-12" >
                            <FormLabel>Contratante:</FormLabel>
                            <Div className="d-flex col-md-12 m-0">
                                <FormControl
                                    id="contractortName"
                                    className="col-md-10 col-sm-10 col-10"
                                    value={contractor}
                                    disabled={contractorSelected ? false : true}
                                    type="text"
                                    onChange={handleInputContractorChange}
                                />
                                <InputButtonEdit
                                    onClick={handleButtonContractorChange}
                                    style={{ backgroundColor: contractorSelected ? '#00FA9A' : '#909090' }}
                                    className="col-md-2 col-sm-2 col-2"
                                >
                                    <img width="20" height="20" src={contractorSelected ? icoCheck : icoEdit} />
                                </InputButtonEdit>
                            </Div>
                        </FormGrup>

                        <FormGrup className="col-md-6 col-12"  >
                            <FormLabel>Contratada:</FormLabel>
                            <Div className="d-flex col-md-12 m-0">
                                <FormControl id="hiredName"
                                    onChange={handleInputHiredChange}
                                    className="col-md-10 col-sm-10 col-10"
                                    value={hired}
                                    disabled={hiredSelected ? false : true}
                                    type="text"
                                />
                                <InputButtonEdit
                                    onClick={handleButtonHiredChange}
                                    style={{ backgroundColor: hiredSelected ? '#00FA9A' : '#909090' }}
                                    className="col-md-2 col-sm-2 col-2"
                                >
                                    <img width="20" height="20" src={hiredSelected ? icoCheck : icoEdit} />
                                </InputButtonEdit>
                            </Div>
                        </FormGrup>

                        <FormGrup className="col-md-12 col-12" >
                            <FormLabel className="col-lg-6 col-md-7" >Data de Assinatura</FormLabel>
                            <Div className="d-flex col-md-12 m-0">
                                <FormControl
                                    onChange={handleInputSignChange}
                                    value={signDate}
                                    disabled={signSelected ? false : true}
                                    type="date"
                                />
                                <InputButtonEdit
                                    onClick={handleButtonSignChange}
                                    style={{ backgroundColor: signSelected ? '#00FA9A' : '#909090' }}
                                    className="col-md-2 col-sm-2 col-2"
                                >
                                    <img width="20" height="20" src={signSelected ? icoCheck : icoEdit} />
                                </InputButtonEdit>
                            </Div>
                        </FormGrup>

                        <FormGrup className="col-md-12 col-12" >
                            <FormLabel className="col-md-12" >Objeto do contrato:</FormLabel>
                            <Div className="d-flex col-md-12 m-0">
                                <FormControl
                                    className="col-md-10 col-sm-10 col-10"
                                    value={description}
                                    onChange={handleInputDescriptionChange}
                                    disabled={descriptionSelected ? false : true}
                                    as="textarea"
                                    rows="5"
                                />
                                <InputButtonEdit
                                    onClick={handleButtonDescriptionChange}
                                    style={{ backgroundColor: descriptionSelected ? '#00FA9A' : '#909090' }}
                                    className="col-md-2 col-sm-2 col-2"
                                >
                                    <img width="20" height="20" src={descriptionSelected ? icoCheck : icoEdit} />
                                </InputButtonEdit>
                            </Div>
                        </FormGrup>
                    </ContainContent>


                    <ContainContent>

                        <ButtonExclude onClick={() => setOpenModalDelete(true)}
                            className="col-md-3 col-sm-6 col-12"
                            id="EditForm"
                            name="btnEdit"
                            type="button">
                            Excluir
                        </ButtonExclude>

                    </ContainContent>

                </ColContent>
                <ColContent2 className="col-md-4 col-sm-4 col-12 d-flex flex-column justify-content-center align-items-center">
                    <ContainContent>
                        <TitleProgress>Cadastros</TitleProgress>
                        <ContainContent className="col-md-12">
                            <ButtonMenuView onClick={() => setOpenModalAnexo(true)}>Contrato/Anexo/Aditivo</ButtonMenuView>
                            <ButtonMenuView onClick={() => setOpenModalTec(true)}>Proposta técnica comercial</ButtonMenuView>
                            <ButtonMenuView onClick={() => setOpenModalNotes(true)}> Atas de reunião</ButtonMenuView>
                            <ButtonMenuView onClick={() => setOpenModalCorrespondence(true)}> Correspondência</ButtonMenuView>
                            <ButtonMenuView onClick={() => setOpenModalThemes(true)}>Assunto destaque</ButtonMenuView>
                        </ContainContent>
                    </ContainContent>
                </ColContent2>
            </ContainContent>
                <ListArchive reloadList={reloadList} style={{
                    width: '100%',
                }} contractId={id} />
        </>
    );
};


