export default function filterThemes(documents, themes)
{
    for(const theme of themes)
    {
        documents = documents.filter(index => {
            if(!index.props.themes)
            {
                return false;
            } else {
                if(index.props.themes.toLowerCase().indexOf(theme.toLowerCase()) > -1)
                {
                    return true;
                } else {
                    return false;
                };
            }
        });
    };

    return documents;
};