import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { toast } from "react-toastify";
import { format, addDays, isWithinInterval, parseISO } from "date-fns";
import { useHistory } from "react-router-dom";

import host from "../../../../config/host";
import api from "../../../../util/api";

import formatData from "./function/formatdata";

import {
  MainRow,
  ColContain,
  ColRow,
  RowScroll,
  FormControl,
  FormGrup,
  FormLabel,
  ButtonFiltersDate,
  ButtonFiltersClean,
  NameArchive,
  ColContentInter,
  ContractButton,
} from "./styles";

import TrueIco from "../../../../assets/images/true.png";
import FalseIco from "../../../../assets/images/false.png";

export default function ({ contractId, reloadList }) {
  const history = useHistory();

  const [subDocumentList, setSubDocumentList] = useState([]);
  const [typeSelected, setTypeSelected] = useState("todos");
  const [documentRender, setDocumentRender] = useState([]);

  const [dateInitial, setDateInitial] = useState("");
  const [dateFinal, setDateFinal] = useState("");

  useEffect(() => {
    async function loadSubDocuments() {
      var { data } = await api.get(`contract/list/subdocuments/${contractId}`);

      const formattedData = formatData(data);

      setDocumentRender(formattedData);
      setSubDocumentList(formattedData);
    }

    loadSubDocuments();
  }, [reloadList]);

  const handleInputDateInitialChange = (data) => {
    setDateInitial(data.target.value);
  };

  const handleButtonTypeDocChange = (data) => {
    const { id: document_id, type } = data;

    switch (type) {
      case "correspondencia":
        history.push(`/edit-correspondence/${contractId}/${document_id}`);
        break;

      case "ata":
        history.push(`/edit-ata/${contractId}/${document_id}`);
        break;

      case "proposta":
        history.push(`/edit-prop/${document_id}`);
        break;

      default:
        history.push(`/edit-anexo/${document_id}`);
        break;
    }
  };

  const handleInputDateFinalChange = (data) => {
    setDateFinal(data.target.value);
  };

  const handleSelectChange = (data) => {
    const {
      target: { value },
    } = data;

    setTypeSelected(value);

    var filter;

    switch (value) {
      case "todos":
        filter = subDocumentList;
        break;

      case "padrao":
        filter = subDocumentList.filter((index) => index.type === "padrao");
        break;

      case "proposta":
        filter = subDocumentList.filter((index) => index.type === "proposta");
        break;

      case "ata":
        filter = subDocumentList.filter((index) => index.type === "ata");
        break;

      case "correspondencia":
        filter = subDocumentList.filter(
          (index) => index.type === "correspondencia"
        );
        break;
    }

    setDocumentRender(filter);
  };

  const handleButtonFilterDate = () => {
    var filter;

    switch (typeSelected) {
      case "todos":
        filter = subDocumentList;
        break;

      case "padrao":
        filter = subDocumentList.filter((index) => index.type === "padrao");
        break;

      case "proposta":
        filter = subDocumentList.filter((index) => index.type === "proposta");
        break;

      case "ata":
        filter = subDocumentList.filter((index) => index.type === "ata");
        break;

      case "correspondencia":
        filter = subDocumentList.filter(
          (index) => index.type === "correspondencia"
        );
        break;
    }

    if (dateInitial && dateFinal) {
      const dateFilter = filter.filter((index) =>
        isWithinInterval(new Date(index.date), {
          start: parseISO(dateInitial),
          end: addDays(parseISO(dateFinal), 1),
        })
      );

      setDocumentRender(dateFilter);
    }
  };

  const handleButtonClearFilterDate = () => {
    setDateInitial("");
    setDateFinal("");
    setTypeSelected("todos");
    setDocumentRender(subDocumentList);
  };

  return (
    <>
      <MainRow>
        <ColContain className="col-md-11 col-sm-11 col-11">
          <MainRow>
            <FormGrup className="col-md-4 col-12">
              <FormLabel>De:</FormLabel>
              <FormControl
                onChange={handleInputDateInitialChange}
                value={dateInitial}
                required
                type="date"
              />
            </FormGrup>
            <FormGrup className="col-md-4 col-12">
              <FormLabel>Ate:</FormLabel>
              <FormControl
                onChange={handleInputDateFinalChange}
                value={dateFinal}
                required
                type="date"
              />
            </FormGrup>
            <FormGrup className="col-md-4 col-12 d-flex align-items-center justify-content-center">
              <ButtonFiltersDate onClick={handleButtonFilterDate}>
                Filtrar por data
              </ButtonFiltersDate>
              <ButtonFiltersClean onClick={handleButtonClearFilterDate}>
                Limpar filtro
              </ButtonFiltersClean>
            </FormGrup>
            <FormGrup className="col-md-12 col-12">
              <FormLabel style={{ fontSize: "18px" }}>
                Selecione o tipo:
              </FormLabel>
              <FormControl
                style={{
                  border: "solid 1px #cfcfcf",
                  fontSize: "16px",
                  padding: "10px 5px",
                }}
                onChange={handleSelectChange}
                value={typeSelected}
                className="col-md-12 col-12"
                as="select"
              >
                <option value="todos">Todos</option>
                <option value="padrao">Contrato/Anexo/Aditivo</option>
                <option value="proposta">Proposta Técnica</option>
                <option value="ata">Atas de reunião</option>
                <option value="correspondencia">Correspondência</option>
              </FormControl>
            </FormGrup>
          </MainRow>
          <RowScroll>
            <ColContentInter className="col-md-12">
              <ColRow className="col-md-3 col-sm-6 col-6 d-flex justify-content-center">
                Nome:
              </ColRow>
              <ColRow className="col-md-2 col-sm-6 col-6 d-flex justify-content-center">
                Data:
              </ColRow>
              <ColRow className="col-md-2 col-sm-6 col-6 d-flex justify-content-center">
                Tipo:
              </ColRow>
              <ColRow className="col-md-2 col-sm-6 col-6 d-flex justify-content-center">
                Codigo:
              </ColRow>
              <ColRow className="col-md-3 col-sm-6 col-6 d-flex justify-content-center">
                Editar:
              </ColRow>
            </ColContentInter>
            {documentRender.map((index) => (
              <ColContentInter className="col-md-12">
                <ColRow className="col-md-3 col-sm-6 col-6 d-flex justify-content-center">
                  <NameArchive>{index.name}</NameArchive>
                </ColRow>
                <ColRow className="col-md-2 col-sm-6 col-6 d-flex justify-content-center">
                  <NameArchive>{index.dateView}</NameArchive>
                </ColRow>
                <ColRow className="col-md-2 col-sm-6 col-6 d-flex justify-content-center">
                  <a
                    href={`${host.ip}/files/${index.File.path}`}
                    target="_blank"
                  >
                    <NameArchive>{index.typeView}</NameArchive>
                  </a>
                </ColRow>
                <ColRow className="col-md-2 col-sm-6 col-6 d-flex justify-content-center">
                  {index.code && index.code}
                </ColRow>
                <ColRow className="col-md-3 col-sm-6 col-6 d-flex justify-content-center">
                  <ContractButton
                    href="#"
                    onClick={() => handleButtonTypeDocChange(index)}
                  >
                    Editar
                  </ContractButton>
                </ColRow>
                {/* <ColRow className="col-md-3 col-sm-6 col-6 d-flex justify-content-center">
                                        {list.impact ? <img width="20" height="20" src={TrueIco} /> : <img width="20" height="20" src={FalseIco} />}
                                    </ColRow> */}
              </ColContentInter>
            ))}
          </RowScroll>
        </ColContain>
      </MainRow>
    </>
  );
}
