import React from 'react';
import { Navbar, Nav, Button, Container, NavDropdown, Row } from 'react-bootstrap';
import Styled from 'styled-components';

export const NavigateButtonLeft = Styled.div`
    background-color:#374F4F;
    width:40px;
    height:40px;
    border-radius:50%;
    color:white;
    display:flex;
    justify-content:center;
    align-items:center;
    font-weight:bold;
    cursor:pointer;
    padding:10px;
    &:hover{
        background-color:#376f4f;
    }
   
`;
export const NavigateButtonRight = Styled.div`
    background-color:#374F4F;
    width:40px;
    height:40px;
    border-radius:50%;
    color:white;
    display:flex;
    justify-content:center;
    align-items:center;
    font-weight:bold;
    cursor:pointer;
    padding:10px;
    &:hover{
        background-color:#376f4f;
    }
   
`;
export const NavigateBar = Styled.div`
    margin:0;
    margin-top:20px;
    display:flex;
    width:100%;
    padding-left:30px;
    padding-right:30px;
    justify-content:space-between;
`;
export const ContainNav = Styled(Row)`
    width:100%;
    margin:0;
    padding:10px;
    display:flex;
    align-items:center;
    justify-content:center;  
`;
export const NavbarContent = Styled(Navbar)`
    width:100%;
    margin:0;
    min-height:70px;
    border:#f1f1f1 solid 3px;
    background-color:#374F4F!important;
    border-radius:10px;
    
`;
export const NavbarMenu = Styled(Navbar)`
    width:100%;
    margin:0;
    min-height:70px;
    border:solid 3px;
    background-color:#374F4F!important;
`;

export const UserBox = Styled(Navbar.Brand)`
    border-radius:10px;
    border:#f1f1f1 solid 3px;
    min-height:80px;
    margin:0;
    padding: 5px 15px; 
    background-color:#374F4F!important;
    display:flex;
    justify-content:center;
    align-items:center;
    padding-top:21px;
    padding-bottom:21px;  
`;
export const SubNav = Styled(Nav)`
    display:flex;
    flex-direction:column;
    align-items:center;
    justify-content:center;
    margin:0;
    padding:0;
    background-color:#374F4F!important;
`;

export const SubNavLink = Styled(Nav.Link)`
transition: 0.5s;;
    width:100%;
    margin:10px 10px;
    padding:0;
    height:auto;
    display:flex;
    justify-content:center;
    align-items:center;
    font-size:14px;
    color:white!important;
    min-height:60px;
    border-bottom:solid 3px #374F4F;
    
    &:hover{
       transition: 0.5s;;
       border-bottom:solid 3px white;
    }
`;
export const NavDrop = Styled(NavDropdown)`
background-color:#363636!important;
`;

export const Btn = Styled(Button)`
    margin:0;
    padding:0;
`;

export const IcoMenu = Styled.img`
    width:30px;
    height:30px;
    margin:10px;
`;


export const NameUser = Styled.h2`
margin:0;
font-size:15px;
color:white;
`;

export const IcoToggle = Styled(Navbar.Toggle)`
    margin:0;
    padding:0;
    width:100%;
    border:3px solid #363636;
    &:span{
        color:black;
    }
`;
export const BoxInfoUser = Styled.div`
display:flex;
justify-content:center;
align-items:center;
flex-direction:column;
min-height:50px;

`;
export const BoxPhotoUser = Styled.div`
    background-size:cover;
    background-position:center;
    width:50px;
    height:50px;
    border: solid 3px;
    border-radius:50px;
    margin-right:10px;
    border-color: #45E68D;
   
`;

export const ConfigIco = Styled.img`
    transition:0.4s;
    width:18px;
    height:18px;
    margin:5px;
    &:hover{
    transition:0.4s;
    transform: scale(1.2);
}
`;


export const UserConfigLink = Styled.a`
color:white;
font-size:14px;
font-weight:bold;
`;


