import { Container, Button } from 'react-bootstrap';
import Styled from 'styled-components';

export const WindowsC1 = Styled(Container)`
    height:100vh;
    display:flex;
    justify-content:center;
    align-items:center;
`;

export const WindowsC2 = Styled(Container)` 
    width:500px;
    background-color:#374F4F;
    display:flex;
    flex-direction:column;
    justify-content:center;
    align-items:center;
    padding:50px;
`;
export const Label = Styled.label`
    color:white;
    margin:10px 0px;
`;

export const Div = Styled.div`
    flex-direction:column;
    display:flex;
    width:80%;
`;

export const Link = Styled.a`
    color:white!important;
    text-decoration:none;
`;

export const IcoUser = Styled.img`
    width:100px;
    height:100px;
    margin:10px;
`;

export const BtnLogin = Styled(Button)`
transtion:0.3s;
width:50%;
margin:20px 15px;
background-color:#00CC5C;
color:white;
font-weight:bold;
border:none;
    &:hover{
        transtion:0.3s;
    background-color:#009945;
    color:white;
    }
    &:focus{
        transtion:0.3s;
        background-color:#909090!important;
        color:white;
        border:none;
        box-shadow:none;
    }
`;

export const InputTxt = Styled.input`
`;
