import produce from 'immer';

const initialState = {
    signed: false,
    token: null
}

export default function user(state = initialState, action)
{
    switch(action.type)
    {
        case '@auth/SIGN_IN_SUCESS':
            return produce(state, draft => {
                draft.signed = true;
                draft.token = action.payload.token;
            });

        case '@auth/SIGN_OUT':
            return produce(state, draft => {
                draft.signed = false;
                draft.token = null;
            });

        default: 
            return state;
    }
};