import React, { useState } from 'react';
import Modal from 'react-modal';
import { toast } from 'react-toastify';

import api from '../../../../util/api';

import {
    ContainContent,
    ColContent,
    ButtonEnviar,
    FormGrup,
    FormLabel,
    FormControl,
    FormFile,
    ClosedModalButton,
} from './styles';

import icoLoading from '../../../../assets/images/icoLoading.gif';

function Registeranexos({ openModalAnexo, setOpenModalAnexo, contractId, reloadList, setReloadList}) {

    const [type, setType] = useState('contrato');

    const [file, setFile] = useState(0);
    const [loadingFile, setLoadingFile] = useState(false);

    const handleSelectTypeChange = data => {
        setType(data.target.value);
    };

    const handleInputFileChange = async data => {
        const form = new FormData();
        form.append('file', data.target.files[0]);

        try{
            setLoadingFile(true);

            const answear = await api.post('files/upload', form);

            setFile(answear.data.id);
            setLoadingFile(false);
        }catch(err)
        {
            toast.error('Houve um erro ao carregar o arquivo, contate um administrador');
        }
    };
    

    const clearAll = () => {
        setFile(0);
        
        document.getElementById("validationFormik107").value = "";
    };

    
    const handleSubmit = async () => {
        try{
            if(loadingFile)
            {
                toast.error('Aguarde até que o arquivo seja carregado'); 
            }

            if(file === 0)
            {
                toast.error('É necessário carregar um arquivo'); 
            };

            await api.post('filesdefault/register', {
                type,
                file_id: file,
                contract_id: contractId
            });

            toast.success('Documento enviado!');

            setReloadList(reloadList + 1);
            clearAll();
        }catch(err)
        {
            toast.error('Houve um erro ao carregar o arquivo, contate um administrador'); 
        }
    };


    return (
        <Modal style={
            {
                overlay: {
                    backgroundColor: 'rgba(25,25,25, .8)',
                },
                content: {
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    padding: '0',
                    top: '0',
                    bottom: '0',
                    left: '0',
                    right: '0',
                    border: 'none',
                    backgroundColor: 'rgba(25,25,25, .0)',
                    margin: '0',
                    borderRadius: '10px',
                    height: '100vh',
                    width: '100vw',
                },
            }
        }

            isOpen={openModalAnexo} onRequestClose={() => {
                if (loadingFile) return ;
                setOpenModalAnexo(false);
            }}>


            <ColContent className="col-md-8 col-sm-7 col--12" >
                <div className="p-2 col-md-12 col-sm-12 col-12 d-flex justify-content-end">
                    <ClosedModalButton onClick={() => { if(loadingFile) return; setOpenModalAnexo(false) } }>X</ClosedModalButton>
                </div>
                <ContainContent>
                    <FormGrup className="col-md-6 col-12" >
                        <FormLabel>Tipo:</FormLabel>
                        <FormControl
                            value={type}
                            onChange={handleSelectTypeChange}
                            className="col-md-12 col-12" as="select"
                        >
                            <option value="contrato">Contrato</option>
                            <option value="anexo">Anexo do contrato</option>
                            <option value="aditivo">Aditivo do contrato</option>
                        </FormControl>
                    </FormGrup>
                    <FormGrup className="col-md-6 col-12" >
                        <FormLabel>Selecione o arquivo:</FormLabel>
                            <FormFile
                                onChange={handleInputFileChange}
                                className="position-relative"
                                required
                                name="file"
                                id="validationFormik107"
                                feedbackTooltip
                            />
                        {loadingFile && <img width="30" height="30" style={{ marginLeft: '40%', marginTop: '5%' }} src={icoLoading} />}
                    </FormGrup>
                </ContainContent>
                <ContainContent>
                    <ButtonEnviar
                        onClick={handleSubmit} 
                        id="SubForm" 
                        name="btForm" 
                        type="button">Enviar
                    </ButtonEnviar>
                </ContainContent>
            </ColContent>
        </Modal>
    );
}

export default Registeranexos;