import React from 'react';
import { Container, Row, Col, Form, Button } from 'react-bootstrap';
import Styled from 'styled-components';

export const ClosedModalButton = Styled(Button)`
transition:0.3s;
margin:0px 15px;
background-color:#990000;
color:white;
font-weight:bold;
border:none;
    &:hover{
        transition:0.3s;
    background-color:#770000;
    color:white;
    }
    &:focus{
        transition:0.3s;
        background-color:#550000!important;
        color:white;
        border:none;
        box-shadow:none;
    }
`;


export const TextContent = Styled.p`
    font-size:15px;
    max-width: 30ch;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
`;

export const ButtonEdit = Styled(Button)`
transition:0.3s;
margin:0px 15px;
padding:5px 20px;
color:white;
font-weight:bold;
border:none;
  
`;

export const ContainContent = Styled(Row)`
    margin:0;
    justify-content:center;
    display:flex;
    width:100%;
    border-radius:10px;
    padding:5px;
`;
export const ColContent2 = Styled(Col)`
margin-top:20px;
-webkit-box-shadow: -1px -4px 35px 7px rgba(0,0,0,0.16);
-moz-box-shadow: -1px -4px 35px 7px rgba(0,0,0,0.16);
box-shadow: -1px -4px 35px 7px rgba(0,0,0,0.16);
padding:10px;
margin-left:30px;
background-color:white;
border-radius:10px;

@media (max-width: 620px) { 
    margin-left:0px;
  }

`;
export const ColContent = Styled(Col)`
margin-top:20px;
border-radius:10px;
background-color:white;
-webkit-box-shadow: -1px -4px 35px 7px rgba(0,0,0,0.16);
-moz-box-shadow: -1px -4px 35px 7px rgba(0,0,0,0.16);
box-shadow: -1px -4px 35px 7px rgba(0,0,0,0.16);
padding:10px;
display:flex;
justify-content:center;
align-items:center;
flex-direction:column;
`;

export const FormGrup = Styled(Form.Group)`

`;
export const FormLabel = Styled(Form.Label)`
    padding:0;
    margin:0;
`;
export const FormControl = Styled(Form.Control)`

`;

export const FormFile = Styled(Form.File)`

`;

export const ButtonEnviar = Styled(Button)`
transition:0.3s;
width:100%;
margin:0px 15px;
background-color:#00CC5C;
color:white;
font-weight:bold;
border:none;
    &:hover{
        transition:0.3s;
    background-color:#009945;
    color:white;
    }
    &:focus{
        transition:0.3s;
        background-color:#909090!important;
        color:white;
        border:none;
        box-shadow:none;
    }
`;
export const Div = Styled.div`
margin:0;
padding:0;
`;

export const InputButtonEdit = Styled(Button)`
display:flex;
align-items:center;
justify-content:center;
margin-left:10px;
padding:5px 5px;
border:none;
`;


export const ButtonExclude = Styled(Button)`
transition:0.3s;
margin:0px 15px;
background-color:#990000;
color:white;
font-weight:bold;
border:none;
    &:hover{
        transition:0.3s;
    background-color:#770000;
    color:white;
    }
    &:focus{
        transition:0.3s;
        background-color:#550000!important;
        color:white;
        border:none;
        box-shadow:none;
    }
`;



export const NameArchive = Styled.h2`
    font-size:18px;
    padding:10px;
`;










