import { format, parseISO } from 'date-fns';

export default function formatData(data)
{
    const formattedData = [];
    
    data.FilesDefaults = data.FilesDefaults.filter(index => index.ready);

    data.FilesDefaults.forEach(index => {
        const { id, type, date, File: { name }, File } = index;

        let obj;

        if(type === 'contrato'){
            obj = {
                id,
                name: name && name.toLowerCase(),
                type,
                date,
                formattedDate: format(new Date(date), 'dd/MM/yyyy'),
                File
            };
        } else {
            obj = {
                id,
                name: name && name.toLowerCase(),
                type,
                typeView: type + ' do contrato',
                date,
                formattedDate: format(new Date(date), 'dd/MM/yyyy'),
                File
            };
        }

        formattedData.push(obj);
    }); 

    data.Correspondences = data.Correspondences.filter(index => index.ready);

    data.Correspondences.forEach(index => {
        const { id, subject, date, type, code, themes, sender, recipient, impact, File } = index;

        const obj = {
            id,
            name: subject && subject.toLowerCase(),
            date,
            formattedDate: format(new Date(date), 'dd/MM/yyyy'),
            type: 'correspondencia',
            subtype: type,
            typeView: 'corresp. ' + type,
            code,
            themes,
            sender,
            recipient,
            impact,
            File
        };

        formattedData.push(obj);
    });

    data.NotesMeetings = data.NotesMeetings.filter(index => index.ready);

    data.NotesMeetings.forEach(index => {
        const { id, name, date, code, themes, impact, File } = index;

        const obj = {
            id,
            name: name && name.toLowerCase(),
            date,
            formattedDate: format(new Date(date), 'dd/MM/yyyy'),
            type: 'ata',
            typeView: 'ata de reunião',
            code,
            themes,
            impact,
            File
        };

        formattedData.push(obj);
    });

    data.TechnicalProposals = data.TechnicalProposals.filter(index => index.ready);

    data.TechnicalProposals.forEach(index => {
        const { id, name, accept_date, File } = index;

        const obj = {
            id,
            name: name && name.toLowerCase(),
            date: accept_date,
            formattedDate: format(new Date(accept_date), 'dd/MM/yyyy'),
            type: 'proposta',
            typeView: 'proposta técnica',
            File
        };

        formattedData.push(obj);
    });

    console.log(formattedData)

    return formattedData;
};