import {Col,Row,Form, Container, Button } from 'react-bootstrap';
import Styled from 'styled-components';

export const WindowsC1 = Styled(Container)`
    margin-top:30px;
    height:50vh;
    display:flex;
    justify-content:center;
    align-items:center;
    background-color:#f2f2f2;
`;

export const WindowsC2 = Styled(Container)` 
border-radius:15px;
    width:500px;
    background-color:#374F4F;
    display:flex;
    flex-direction:column;
    justify-content:center;
    align-items:center;
    padding:50px;
`;
export const Label = Styled.label`
    color:white;
    margin:10px 0px;
`;

export const Div = Styled(Row)`
`;
export const ColContent = Styled(Col)`
    display:flex;
    width:100%;
    margin-top:10px;
`;
export const FormControl = Styled(Form.Control)`

`;
export const InputButtonEdit = Styled(Button)`
display:flex;
align-items:center;
justify-content:center;
margin-left:10px;
padding:5px 5px;
border:none;
`;

export const Link = Styled.a`
    color:white!important;
    text-decoration:none;
`;

export const IcoUser = Styled.img`
    width:150px;
    height:150px;
    margin:10px;
    border-radius:100%;
    border:solid 5px #45E68D;
`;

export const BtnLogin = Styled(Button)`
    margin:20px 0px;
    width:50%;
    border-radius:25px;
    background-color:#FFA200;
    border:none;
    font-weight:bold;
    &:hover {
      background-color:white;
      color:#FFA200;   
    }
`;

export const InputTxt = Styled.input`
`;
