import React, { useState, useEffect } from 'react';
import Modal from 'react-modal';
import { toast } from 'react-toastify';
import api from '../../../../util/api';

import {
    ContainNav,
    ContainContent,
    ColContent,
    ButtonEnviar,
    FormGrup,
    FormLabel,
    FormCheck,
    FormControl,
    FormFile,
    DivScroll,
    Mask,
    ExitButton,
    ClosedModalButton
} from './styles';

import icoLoading from '../../../../assets/images/icoLoading.gif';

function Proposta({ openModalTec, setOpenModalTec, contractId, reloadList, setReloadList }) {
    const [date, setDate] = useState('');
    const [dateEnv, setDateEnv] = useState('');
    const [dateRec, setDateRec] = useState('');
    const [nameProp, setNameProp] = useState('');
    const [description, setDescription] = useState('');

    const [file, setFile] = useState(0);
    const [files, setFiles] = useState([]);
    const [loadFile, setLoadFile] = useState(false);

    const [many, setMany] = useState(false);

    const handleInputNameProp = data => {
        setNameProp(data.target.value);
    }

    const handleInputDateEnvChange = data => {
        setDateEnv(data.target.value);
    }
    const handleInputDateRecChange = data => {
        setDateRec(data.target.value);
    }

    const handleInputDescriptionChange = data => {
        setDescription(data.target.value);
    }

    const handleInputFileChange = async data => {
        const form = new FormData();
        form.append('file', data.target.files[0]);

        try {
            setLoadFile(true);
            const answear = await api.post('files/upload', form);

            const { id } = answear.data;

            setFile(id);
            setLoadFile(false);
        } catch (err) {
            toast.error('Não foi possível carregar o PDF, por favor contate um administrador');
        }
    };

    const handleInputFilesChange = async data => {
        const { files } = data.target;
        const filesId = [];

        try {
            setLoadFile(true);

            for (var i = 0; i < files.length; i++) {
                const form = new FormData();
                form.append('file', files[i]);

                const { data: { id } } = await api.post('files/upload', form)

                filesId.push(id);
            };

            setLoadFile(false);
        } catch (err) {
            toast.error(`Erro ao carregar, contate um administrador`);
        }

        setFiles(filesId);
    }

    const clearAll = () => {
        setNameProp('');
        setDateEnv('');
        setDateRec('');
        setDescription('');
        setFile(0);
        if (!many) {
            document.getElementById("validationFormik107").value = "";
        } else {
            document.getElementById("validationFormik108").value = "";
        }
    };

    const handleSubmit = async () => {
        if (!many) {
            try {
                const dateSendFirstP = date[0];
                if(dateSendFirstP === "0" || dateEnv.length !== 10)
                {
                    return toast.error("Preencha o campo de data de envio corretamente");
                };

                const dateRecFirstP = dateRec[0];
                if(dateRecFirstP === "0" || dateRec.length !== 10)
                {
                    return toast.error("Preencha o campo de data de aceite corretamente");
                };

                if (loadFile) {
                    return toast.error("Aguarde até que o PDF seja enviado");
                };

                if (file === 0) {
                    return toast.error("Você precisa carregar um PDF");
                };

                await api.post('technicalp/register', {
                    name: nameProp,
                    description,
                    send_date: dateEnv,
                    accept_date: dateRec,
                    file_id: file,
                    contract_id: contractId
                });

                setReloadList(reloadList + 1);
                clearAll();

                toast.success('Documento enviado!');
            } catch (err) {
                toast.error("Erro ao se conectar com o servidor, contate um administrador!");
            }
        } else {
            try {
                const dateFirstP = dateRec[0];
                if(dateFirstP === "0" || dateRec.length !== 10)
                {
                    return toast.error("Preencha o campo de data de aceite corretamente");
                };

                if (loadFile) {
                    return toast.error("Aguarde até que o PDF seja enviado");
                }

                if (!files[0]) {
                    return toast.error("Você precisa ao menos carregar um PDF");
                };

                await api.post('technicalp/register/many', {
                    accept_date: dateRec,
                    files_id: files,
                    contract_id: contractId
                });

                clearAll();
                setReloadList(reloadList + 1);

                toast.success('Documentos enviado!');
                
            } catch (err) {
                toast.error("Erro ao se conectar com o servidor, contate um administrador!");
            }
        }
    };

    return (
        <Modal
            style={
                {
                    overlay: {
                        backgroundColor: 'rgba(25,25,25, .8)',
                    },
                    content: {
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                        padding: '0',
                        top: '0',
                        bottom: '0',
                        left: '0',
                        right: '0',
                        border: 'none',
                        backgroundColor: 'rgba(25,25,25, .0)',
                        margin: '0',
                        borderRadius: '10px',
                        height: '100vh',
                        width: '100vw',
                    },
                }
            }

            isOpen={openModalTec} onRequestClose={() => {
                if (loadFile) return;
                setOpenModalTec(false);
            }}
        >
            <ContainNav>
                <ContainContent>
                    <ColContent className="col-md-7 col-sm-7 col--12" >
                        <div className="p-2 col-md-12 col-sm-12 col-12 d-flex justify-content-end">
                            <ClosedModalButton onClick={() => { if (loadFile) return; setOpenModalTec(false)}}>X</ClosedModalButton>
                        </div>
                        <ContainContent>
                            <FormGrup className="col-md-12 col-12" >
                                <FormLabel>Nome da proposta:</FormLabel>
                                <FormControl
                                    disabled={!many ? false : true}
                                    required
                                    type="text"
                                    placeholder="Digite o nome"
                                    onChange={handleInputNameProp}
                                    value={nameProp}
                                ></FormControl>
                            </FormGrup>
                            
                            <FormGrup className="col-md-6 col-12" >
                                <FormLabel>Data envio:</FormLabel>
                                <FormControl
                                    disabled={!many ? false : true}
                                    required placeholder="00/00/00"
                                    type="date"
                                    onChange={handleInputDateEnvChange}
                                    value={dateEnv}
                                ></FormControl>
                            </FormGrup>
                            <FormGrup className="col-md-6 col-12" >
                                <FormLabel>Data aceite:</FormLabel>
                                <FormControl
                                    required placeholder="00/00/00"
                                    type="date"
                                    onChange={handleInputDateRecChange}
                                    value={dateRec}
                                ></FormControl>
                            </FormGrup>
                            <FormGrup className="col-md-12 col-12" >
                                <FormLabel>Selecione o arquivo:</FormLabel>
                                {
                                    !many ?
                                        <FormFile
                                            className="position-relative"
                                            required
                                            name="file"
                                            id="validationFormik107"
                                            feedbackTooltip
                                            onChange={handleInputFileChange}
                                        />
                                        :
                                        <FormFile
                                            multiple
                                            className="position-relative"
                                            required
                                            name="file"
                                            id="validationFormik108"
                                            feedbackTooltip
                                            onChange={handleInputFilesChange}
                                        />
                                }
                                {loadFile && <img width="30" height="30" style={{ marginLeft: '40%', marginTop: '5%' }} src={icoLoading} />}
                            </FormGrup>
                        </ContainContent>
                        <ContainContent>
                            <FormGrup className="col-md-12 col-12" >
                                <FormLabel>Descrição:</FormLabel>
                                <FormControl
                                    disabled={!many ? false : true}
                                    className="col-md-12 col-12"
                                    as="textarea"
                                    rows="5"
                                    placeholder="Digite uma descrição"
                                    onChange={handleInputDescriptionChange}
                                    value={description}
                                />
                            </FormGrup>
                        </ContainContent>
                        <ContainContent>
                            <ButtonEnviar id="SubForm" name="btForm" type="button" onClick={handleSubmit}>Enviar</ButtonEnviar>
                        </ContainContent>
                        <FormCheck
                            type="switch"
                            id="custom-switch-massive"
                            label="Em massa"
                            onChange={() => setMany(!many)}
                        />
                    </ColContent>
                </ContainContent>
            </ContainNav>
        </Modal>
    );
}

export default Proposta;
