import React, { useState, useParams } from 'react';
import { useDispatch } from 'react-redux';
import { signInRequest } from '../../redux/modules/auth/actions'
import api from '../../util/api';
import { RowMain } from '../../styles/bootstrap';
import { ColContent, InputButtonEdit, FormControl, WindowsC1, WindowsC2, InputTxt, IcoUser, BtnLogin, Div, Label, Link } from './styles';
import photouser from '../../assets/images/faceuser.jpg';

import icoEdit from '../../assets/images/img_388620.png';
import icoCheck from '../../assets/images/checkEdit.png';

function User() {
  const [UserNameSelected, setUserNameSelected] = useState(false);
  const [UserCompanySelected, setUserCompanySelected] = useState(false);
  const [UserLoginSelected, setUserLoginSelected] = useState(false);
  const [UserPasswordSelected, setUserPasswordSelected] = useState(false);

  const [UserInfo, setUserInfo] = useState(
    {
      photo: photouser,
      name: 'renata',
      username: 'renatabtb',
      password: '112233',
      company: 'MRV',
    }
  );
  return (
    <>
      <RowMain style={{ backgroundColor: '#f2f2f2' }}>
        <WindowsC1>
          <WindowsC2>
            <IcoUser src={UserInfo.photo} />
            <Div>
              <ColContent className="col-md-6 col-6">
                <FormControl
                  id="contractortName"
                  className="col-md-9 col-sm-9 col-9"
                  value={UserInfo.name}
                  disabled={UserNameSelected ? false : true}
                  type="text"
                />
                <InputButtonEdit
                  style={{ backgroundColor: UserNameSelected ? '#00FA9A' : '#909090' }}
                  className="col-md-3 col-sm-3 col-3"
                >
                  <img width="20" height="20" src={UserNameSelected ? icoCheck : icoEdit} />
                </InputButtonEdit>
              </ColContent>
              <ColContent className="col-md-6 col-6">
                <FormControl
                  id="contractortName"
                  className="col-md-9 col-sm-9 col-9"
                  value={UserInfo.company}
                  disabled={UserCompanySelected ? false : true}
                  type="text"
                />
                {/* <InputButtonEdit
                  style={{ backgroundColor: contractorSelected ? '#00FA9A' : '#909090' }}
                  className="col-md-2 col-sm-2 col-2"
                >
                  <img width="20" height="20" src={contractorSelected ? icoCheck : icoEdit} />
                </InputButtonEdit> */}
              </ColContent>
              <ColContent className="col-md-6 col-6">
                <FormControl
                  id="contractortName"
                  className="col-md-9 col-sm-9 col-9"
                  value={UserInfo.username}
                  disabled={UserLoginSelected ? false : true}
                  type="text"
                />
                <InputButtonEdit
                  style={{ backgroundColor: UserLoginSelected ? '#00FA9A' : '#909090' }}
                  className="col-md-3 col-sm-3 col-3"
                >
                  <img width="20" height="20" src={UserLoginSelected ? icoCheck : icoEdit} />
                </InputButtonEdit>
              </ColContent>
              <ColContent className="col-md-6 col-6">
                <FormControl
                  id="contractortName"
                  className="col-md-9 col-sm-9 col-9"
                  value={UserInfo.password}
                  disabled={UserPasswordSelected ? false : true}
                  type="password"
                />
                <InputButtonEdit
                  style={{ backgroundColor: UserPasswordSelected ? '#00FA9A' : '#909090' }}
                  className="col-md-3 col-sm-3 col-3"
                >
                  <img width="20" height="20" src={UserPasswordSelected ? icoCheck : icoEdit} />
                </InputButtonEdit>
              </ColContent>

            </Div>
          </WindowsC2>
        </WindowsC1>
        <WindowsC1>
          <WindowsC2>
            <Div>
              <h1>oal</h1>
            </Div>
          </WindowsC2>
        </WindowsC1>
      </RowMain>
    </>
  );
}

export default User;
