import React, { useState, useEffect } from "react";
import { useParams, useHistory } from "react-router-dom";
import { useSelector } from "react-redux";
import { toast } from "react-toastify";
import { format } from 'date-fns';

import api from "../../../util/api";
import host from '../../../config/host';

import icoLoading from "../../../assets/images/icoLoading.gif";

import {
  TextContent,
  NameArchive,
  ContainContent,
  ColContent,
  TagName,
  ExcludeBtn,
  FormGrup,
  FormLabel,
  FormControl,
  FormFile,
  Div,
  InputButtonEdit,
  ButtonExclude,
  FormCheck,
  ButtonAdd,
  ButtonGrupReg,
  DivScrollSubject,
} from "./styles";

import ModalAddThemes from '../addthemescp';

import icoEdit from "../../../assets/images/img_388620.png";
import icoCheck from "../../../assets/images/checkEdit.png";

export default function EditAta() {
  const history = useHistory();

  const { id } = useParams();
  const { contract_id } = useParams();

  const userId = useSelector((state) => state.user.user.id);

  const [reload, setReload] = useState(0);
  const [reloadThemes, setReloadThemes] = useState(0);

  const [themesSelecteds, setThemesSelecteds] = useState([]);

  const [renderSelects, setRenderSelects] = useState([]);

  const [impact, setImpact] = useState(false);

  const [ataName, setataName] = useState("");
  const [ataNameSelect, setataNameSelect] = useState(false);

  const [code, setCode] = useState("");
  const [codeSelect, setCodeSelect] = useState(false);

  const [newDate, setNewDate] = useState("");
  const [DocumentDateDate, setDocumentDateDate] = useState("");
  const [DocumentDateDateSelect, setDocumentDateDateSelect] = useState(false);

  const [file, setFile] = useState(0);
  const [newFileId, setNewFileId] = useState([]);
  const [fileSelect, setFileSelect] = useState(false);
  const [loadFile, setLoadFile] = useState(false);

  const [type, setType] = useState("email");
  const [typeSelect, setTypeSelect] = useState(false);

  const [description, setDescription] = useState("");
  const [descriptionSelect, setdescriptionSelect] = useState(false);

  const [openModalSelectThemes, setOpenModalSelectThemes] = useState(false);
  const [openModalEditSelectThemes, setOpenModalEditSelectThemes] = useState(false);

  const [onClickDelete, setOnClickDelete] = useState(false);

  useEffect(() => {
    async function loadNoteMeeting()
    {
      const { data } = await api.get(`notesmeeting/get/${id}`);

      const { code, name, themes, date, impact, description, File } = data;


      setataName(name);
      setCode(code);
      setThemesSelecteds(themes ? themes.split(',') : []);
      setDocumentDateDate(format(new Date(date), 'dd/MM/yyyy'));
      setImpact(impact);
      setDescription(description);
      setFile(File);
    };

    loadNoteMeeting();
  }, [reload]);

  const handleInputataName = (data) => {
    setataName(data.target.value);
  };

  const handleInputCodeName = (data) => {
    setCode(data.target.value);
  };

  const handleInputNewDateChange = (data) => {
    setNewDate(data.target.value);
  };

  const handleInputDescriptionChange = (data) => {
    setDescription(data.target.value);
  };

  const handleInputTypeChange = (data) => {
    setType(data.target.value);
  };

  const handleInputFileChange = async (data) => {
    const form = new FormData();
    form.append("file", data.target.files[0]);

    try {
      setLoadFile(true);
      const answear = await api.post("files/upload", form);

      const { id } = answear.data;

      setNewFileId(id);
      setLoadFile(false);
    } catch (err) {
      toast.error(
        "Não foi possível carregar o PDF, por favor contate um administrador"
      );
    }
  };

  const handleButtonNameataChange = async () => {
    if (!ataNameSelect) {
      setataNameSelect(true);

    } else {
      if(ataName)
      {
        await api.put(`notesmeeting/update/${id}`, {
          column: 'name',
          content: ataName 
        });

        setReload(reload + 1);
      };

      setataNameSelect(false);
    }
  };

  const handleButtonCodeNameChange = async () => {
    if (!codeSelect) {
      setCodeSelect(true);
    } else {
      if(code)
      {
        await api.put(`notesmeeting/update/${id}`, {
          column: 'code',
          content: code 
        });

        setReload(reload + 1);
      };

      setCodeSelect(false);
    }
  };

  const handleButtonDocumentDateDatePropChange = async () => {
    if (!DocumentDateDateSelect) {
      setDocumentDateDateSelect(true);
    } else {
      if(newDate)
      {
        await api.put(`notesmeeting/update/${id}`, {
          column: 'date',
          content: newDate 
        });

        setReload(reload + 1);
      };

      setDocumentDateDateSelect(false);
    }
  };

  const handleButtonFilePropChange = async () => {
    if (!fileSelect) {
      setFileSelect(true);
    } else {
      if(newFileId != 0)
      {
        await api.put(`notesmeeting/update/${id}`, {
          column: 'file_id',
          content: newFileId 
        });

        setReload(reload + 1);
      };

      setFileSelect(false);
    }
  };

  const handleButtonDescripPropChange = async () => {
    if (!descriptionSelect) {
      setdescriptionSelect(true);

    } else {

      if(description)
      {
        await api.put(`notesmeeting/update/${id}`, {
          column: 'description',
          content: description 
        });

        setReload(reload + 1);
      };

      setdescriptionSelect(false);
    }
  };

  const handleLabelImpactChange = async () => {
    setImpact(!impact);

    await api.put(`notesmeeting/update/${id}`, {
      column: 'impact',
      content: !impact
    });
  };

  const handleButtonRemoveTheme = theme => {
    var themes = themesSelecteds;

    const p = themes.findIndex(index => index === theme);
    themes.splice(p, 1)

    setReloadThemes(reloadThemes + 1);
    setThemesSelecteds(themes);
  };

  const handleSaveThemes = async () => {
    await api.put(`notesmeeting/update/${id}`, {
      column: 'themes',
      content: themesSelecteds
    });

    setReload(reload + 1);
  };

  const handleButtonDelete = async () => {
    if(!onClickDelete)
    {
      return setOnClickDelete(true);
    };

    await api.delete(`notesmeeting/delete/${id}`);

    history.push('/');
};

  return (
    <>  
      <ModalAddThemes 
        openModalSelectThemes={openModalSelectThemes}
        setOpenModalSelectThemes={setOpenModalSelectThemes}
        contractId={contract_id}
        themesSelecteds={themesSelecteds}
        setThemesSelecteds={setThemesSelecteds}
      />

      <ContainContent style={{ paddingBottom: "50px" }}>
        <ColContent className="col-md-7 col-sm-7 col--12">
          <NameArchive>Edição de Ata de reunião</NameArchive>

          <ContainContent>
          <FormGrup className="col-md-8 col-12">
              <FormLabel>Assuntos destaque:</FormLabel>
              <DivScrollSubject>
                <ContainContent className="align-items:center">
                  {
                    setReloadThemes &&
                      themesSelecteds.map(index => (
                        <FormGrup className="d-flex col-md-6 col-6">
                          <TagName className="col-md-9 col-9">
                            ! {index}
                          </TagName>
                          <ExcludeBtn
                            className="col-md-3 col-3"
                            onClick={() => handleButtonRemoveTheme(index)}
                          >
                            X
                          </ExcludeBtn>
                      </FormGrup>
                    ))
                  }
                </ContainContent>
              </DivScrollSubject>
          </FormGrup>
       
            <FormGrup className="col-md-3 col-12">
              <ButtonAdd onClick={() => setOpenModalSelectThemes(true)} >
                Adicionar assunto
              </ButtonAdd>
              <ButtonGrupReg onClick={handleSaveThemes}>
                Salvar assuntos
              </ButtonGrupReg>
            </FormGrup>
          </ContainContent>


          <ContainContent>
            <FormGrup className="col-md-10 col-12">
              <DivScrollSubject></DivScrollSubject>
            </FormGrup>
            <FormGrup className="col-md-4 col-12">
              <FormLabel>Nome da ata:</FormLabel>
              <Div className="d-flex col-md-12 m-0">
                <FormControl
                  id="ata_name"
                  value={ataName}
                  className="col-md-10 col-sm-10 col-10"
                  disabled={ataNameSelect ? false : true}
                  type="text"
                  onChange={handleInputataName}
                />
                <InputButtonEdit
                  style={{
                    backgroundColor: ataNameSelect ? "#00FA9A" : "#909090",
                  }}
                  className="col-md-2 col-sm-2 col-2"
                  onClick={handleButtonNameataChange}
                >
                  <img
                    width="20"
                    height="20"
                    src={ataNameSelect ? icoCheck : icoEdit}
                  />
                </InputButtonEdit>
              </Div>
            </FormGrup>
            <FormGrup className="col-md-4 col-12">
              <FormLabel>Codigo:</FormLabel>
              <Div className="d-flex col-md-12 m-0">
                <FormControl
                  id="recipient_id"
                  value={code}
                  className="col-md-10 col-sm-10 col-10"
                  disabled={codeSelect ? false : true}
                  type="text"
                  onChange={handleInputCodeName}
                />
                <InputButtonEdit
                  style={{
                    backgroundColor: codeSelect ? "#00FA9A" : "#909090",
                  }}
                  className="col-md-2 col-sm-2 col-2"
                  onClick={handleButtonCodeNameChange}
                >
                  <img
                    width="20"
                    height="20"
                    src={codeSelect ? icoCheck : icoEdit}
                  />
                </InputButtonEdit>
              </Div>
            </FormGrup>
            <FormGrup className="col-md-4 col-12">
              <FormLabel>Data:</FormLabel>
              <Div className="d-flex col-md-12 m-0">
                {
                  DocumentDateDateSelect ?
                    <FormControl
                      onChange={handleInputNewDateChange}
                      icoLoading
                      id="DocumentDate_date"
                      className="col-md-10 col-sm-10 col-10"
                      type="date"
                    />
                  :
                  <FormControl
                    value={DocumentDateDate}
                    icoLoading
                    id="DocumentDate_date_input"
                    className="col-md-10 col-sm-10 col-10"
                    disabled
                    type="text"
                  />
                }
                <InputButtonEdit
                  style={{
                    backgroundColor: DocumentDateDateSelect
                      ? "#00FA9A"
                      : "#909090",
                  }}
                  className="col-md-2 col-sm-2 col-2"
                  onClick={handleButtonDocumentDateDatePropChange}
                >
                  <img
                    width="20"
                    height="20"
                    src={DocumentDateDateSelect ? icoCheck : icoEdit}
                  />
                </InputButtonEdit>
              </Div>
            </FormGrup>
            <FormGrup className="col-md-6 col-12">
              <FormLabel>{fileSelect ? 'Selecione o arquivo:' : 'Arquivo'}</FormLabel>
              <Div className="d-flex col-md-12 m-0">
                {
                  fileSelect ? 
                    <FormFile
                      className="position-relative"
                      required
                      name="file"
                      id="validationFormik107"
                      feedbackTooltip
                      onChange={handleInputFileChange}
                    />
                  :
                    <a 
                        href={`${host.ip}/files/${file.path}`} 
                        target="_blank"
                    > <TextContent>{file.name}</TextContent></a>
                }
                
                <InputButtonEdit
                  style={{
                    backgroundColor: fileSelect ? "#00FA9A" : "#909090",
                  }}
                  className="col-md-2 col-sm-2 col-2"
                  onClick={handleButtonFilePropChange}
                >
                  <img
                    width="20"
                    height="20"
                    src={fileSelect ? icoCheck : icoEdit}
                  />
                </InputButtonEdit>
              </Div>
              {loadFile && (
                <img
                  width="30"
                  height="30"
                  style={{ marginLeft: "40%", marginTop: "5%" }}
                  src={icoLoading}
                />
              )}
            </FormGrup>
            <FormGrup className="col-md-6 col-12 d-flex align-items-center justify-content-center">
              <FormCheck
                type="switch"
                label="Ocorrência importante"
                checked={impact}
                onChange={handleLabelImpactChange}
                id="theme"
              />
            </FormGrup>

            <FormGrup className="col-md-12 col-12">
              <FormLabel>Descrição:</FormLabel>
              <Div className="d-flex col-md-12 m-0">
                <FormControl
                  disabled={descriptionSelect ? false : true}
                  className="col-md-10 col-10"
                  as="textarea"
                  rows="5"
                  onChange={handleInputDescriptionChange}
                  value={description}
                />
                <InputButtonEdit
                  style={{
                    backgroundColor: descriptionSelect ? "#00FA9A" : "#909090",
                  }}
                  className="col-md-2 col-sm-2 col-2"
                  onClick={handleButtonDescripPropChange}
                >
                  <img
                    width="20"
                    height="20"
                    src={descriptionSelect ? icoCheck : icoEdit}
                  />
                </InputButtonEdit>
              </Div>
            </FormGrup>
          </ContainContent>
          <ContainContent>
            <ButtonExclude
              className="col-md-3 col-sm-6 col-12"
              id="exlude"
              name="btn_exclude"
              type="button"
              onClick={handleButtonDelete}
            >
              Excluir
            </ButtonExclude>
          </ContainContent>
        </ColContent>
      </ContainContent>
    </>
  );
}
