export default function filterTerms(documents, terms)
{
    for(const term of terms)
    {
        const { value } = term;

        const arrayFilter = [];

        documents.forEach(index => {
            const position = index.props.File.ocr.toLowerCase().indexOf(value.toLowerCase());

            if(position > -1)
            {
                const obj = {
                    ...index,
                };  

                arrayFilter.push(obj);
            };
        });

        documents = arrayFilter;
    };

    return documents;
}