export const ClosedModalButton = Styled(Button)`
transtion:0.3s;
margin:0px 15px;
background-color:#990000;
color:white;
font-weight:bold;
border:none;
    &:hover{
        transtion:0.3s;
    background-color:#770000;
    color:white;
    }
    &:focus{
        transtion:0.3s;
        background-color:#550000!important;
        color:white;
        border:none;
        box-shadow:none;
    }
`;

