import React, { useState, useEffect, useMemo } from 'react';
import { useParams, useHistory } from 'react-router-dom';
import { useSelector } from 'react-redux';
import socketio from 'socket.io-client';
import { format, parseISO } from 'date-fns';
import { toast } from 'react-toastify';

import host from '../../../config/host';

import api from '../../../util/api';
import icoLoading from '../../../assets/images/icoLoading.gif';



import {
    TextContent,
    TitleProgress,
    ContainNav,
    NameArchive,
    ContainContent,
    ColContent2,
    ColContent,
    ButtonEnviar,
    FormGrup,
    FormLabel,
    FormControl,
    FormFile,
    Div,
    InputButtonEdit,
    ButtonExclude,
    FormCheck,
} from './styles';

import icoEdit from '../../../assets/images/img_388620.png';
import icoCheck from '../../../assets/images/checkEdit.png';

export default function EditProp() {
    const history = useHistory();

    const { id } = useParams();

    const [reload, setReload] = useState(0);

    const [proposalName, setProposalName] = useState('');
    const [proposalNameSelected, setProposalNameSelected] = useState(false);

    const [newSendDate, setNewSendDate] = useState('');
    const [sendDateView, setSendDateView] = useState('');
    const [sendDateSelect, setSendDateSelect] = useState(false);

    const [newAcceptDate, setNewAcceptDate] = useState('');
    const [acceptDateView, setAcceptDateView] = useState('');
    const [aceptDateSelect, setAceptDateSelect] = useState(false);

    const [newFileId, setNewFileId] = useState(0);
    const [file, setFile] = useState({
        name: '',
        path: ''
    });
    const [fileSelect, setFileSelect] = useState(false);
    const [loadFile, setLoadFile] = useState(false);

    const [description, setDescription] = useState('');
    const [descriptionSelect, setdescriptionSelect] = useState(false);

    const [onClickDelete, setOnClickDelete] = useState(false);

    useEffect(() => {
        async function loadTecProposal() {
            var { data } = await api.get(`technicalp/get/${id}`);

            var { description, name, accept_date, send_date, File } = data;

            send_date = send_date && format(parseISO(send_date), 'dd/MM/yyyy');
            accept_date = format(parseISO(accept_date), 'dd/MM/yyyy');

            setFile({
                name: File.name,
                path: File.path
            });
            setProposalName(name);
            setDescription(description);
            setSendDateView(send_date);
            setAcceptDateView(accept_date);
        };

        loadTecProposal();
    }, [reload]);

    const handleInputproposalName = data => {
        setProposalName(data.target.value);
    };

    const handleInputsetSendDateChange = data => {
        setNewSendDate(data.target.value);
    };

    const handleInputaceptDateChange = data => {
        setNewAcceptDate(data.target.value);
    };

    const handleInputDescriptionChange = data => {
        setDescription(data.target.value);
    };


    const handleInputFileChange = async data => {
        const form = new FormData();
        form.append('file', data.target.files[0]);

        try {
            setLoadFile(true);

            const { data: { id } } = await api.post('files/upload', form);

            setNewFileId(id);
            setLoadFile(false);
        } catch (err) {
            toast.error('Erro ao carregar o arquivo, contate um administrador');
        }
    };


    const handleButtonNamePropChange = async () => {
        if (!proposalNameSelected) {
            setProposalNameSelected(true);
        } else {
            await api.put(`technicalp/update/${id}`, {
                column: 'name',
                content: proposalName
            });

            setProposalNameSelected(false);
            setReload(reload + 1);
        }
    };

    const handleButtonSendDatePropChange = async () => {
        if (!sendDateSelect) {
            setSendDateSelect(true);
        } else {
            if (newSendDate) {
                await api.put(`technicalp/update/${id}`, {
                    column: 'send_date',
                    content: newSendDate
                });
            };

            setReload(reload + 1);
            setSendDateSelect(false);
        }
    };

    const handleButtonAceptDatePropChange = async () => {
        if (!aceptDateSelect) {
            setAceptDateSelect(true);
        } else {
            if (newAcceptDate) {
                await api.put(`technicalp/update/${id}`, {
                    column: 'accept_date',
                    content: newAcceptDate
                });
            };

            setReload(reload + 1);
            setAceptDateSelect(false);
        };
    };

    const handleButtonFilePropChange = async () => {
        if (!fileSelect) {
            setFileSelect(true);
        } else {
            if (loadFile) {
                return;
            };

            if (newFileId !== 0) {
                await api.put(`technicalp/update/${id}`, {
                    column: 'file_id',
                    content: newFileId
                });

                setReload(reload + 1);
                setNewFileId(0);
            };

            setFileSelect(false);
        };
    };
    const handleButtonDescripPropChange = async () => {
        if (!descriptionSelect) {
            setdescriptionSelect(true);
        } else {
            if (description) {
                await api.put(`technicalp/update/${id}`, {
                    column: 'description',
                    content: description
                });
            };

            setReload(reload + 1);
            setdescriptionSelect(false);
        }
    };

    const handleButtonDelete = async () => {
        if (!onClickDelete) {
            return setOnClickDelete(true);
        };

        await api.delete(`technicalp/delete/${id}`);

        history.push('/');
    };

    return (
        <>
            <ContainContent >
                <ColContent className="col-md-7 col-sm-7 col--12" >
                    <NameArchive>Edição de proposta técnica</NameArchive>
                    <ContainContent>
                        <FormGrup className="col-md-12 col-12" >
                            <FormLabel>Nome da proposta:</FormLabel>
                            <Div className="d-flex col-md-12 m-0">
                                <FormControl
                                    id="proposalname"
                                    value={proposalName}
                                    className="col-md-10 col-sm-10 col-10"
                                    disabled={proposalNameSelected ? false : true}
                                    type="text"
                                    onChange={handleInputproposalName}
                                />
                                <InputButtonEdit
                                    style={{ backgroundColor: proposalNameSelected ? '#00FA9A' : '#909090' }}
                                    className="col-md-2 col-sm-2 col-2"
                                    onClick={handleButtonNamePropChange}
                                >
                                    <img width="20" height="20" src={proposalNameSelected ? icoCheck : icoEdit} />
                                </InputButtonEdit>
                            </Div>
                        </FormGrup>
                        <FormGrup className="col-md-6 col-12" >
                            <FormLabel>Data de envio:</FormLabel>
                            <Div className="d-flex col-md-12 m-0">
                                {
                                    !sendDateSelect ?
                                        <FormControl
                                            value={sendDateView}
                                            id="send_date"
                                            className="col-md-10 col-sm-10 col-10"
                                            disabled
                                            type="text"
                                        />
                                        :
                                        <FormControl
                                            onChange={handleInputsetSendDateChange}
                                            icoLoading id="send_date"
                                            className="col-md-10 col-sm-10 col-10"
                                            type="date"
                                        />
                                }
                                <InputButtonEdit
                                    style={{ backgroundColor: sendDateSelect ? '#00FA9A' : '#909090' }}
                                    className="col-md-2 col-sm-2 col-2"
                                    onClick={handleButtonSendDatePropChange}
                                >
                                    <img width="20" height="20" src={sendDateSelect ? icoCheck : icoEdit} />
                                </InputButtonEdit>
                            </Div>
                        </FormGrup>
                        <FormGrup className="col-md-6 col-12" >
                            <FormLabel>Data de aceite:</FormLabel>
                            <Div className="d-flex col-md-12 m-0">
                                {
                                    !aceptDateSelect ?
                                        <FormControl
                                            id="acept_date"
                                            value={acceptDateView}
                                            className="col-md-10 col-sm-10 col-10"
                                            disabled
                                            type="text"
                                        />
                                        :
                                        <FormControl
                                            onChange={handleInputaceptDateChange}
                                            id="acept_date"
                                            className="col-md-10 col-sm-10 col-10"
                                            type="date"
                                        />
                                }

                                <InputButtonEdit
                                    style={{ backgroundColor: aceptDateSelect ? '#00FA9A' : '#909090' }}
                                    className="col-md-2 col-sm-2 col-2"
                                    onClick={handleButtonAceptDatePropChange}
                                >
                                    <img width="20" height="20" src={aceptDateSelect ? icoCheck : icoEdit} />
                                </InputButtonEdit>
                            </Div>
                        </FormGrup>
                        <FormGrup className="col-md-12 col-12" >
                            <Div className="d-flex col-md-12 m-0">
                                <FormGrup className="col-md-6 col-sm-10 col-10">
                                <FormLabel>Documento:</FormLabel>
                                    {
                                        !fileSelect ?
                                            <a
                                                href={`${host.ip}/files/${file.path}`}
                                                target="_blank"
                                            > <TextContent>{file.name}</TextContent></a>
                                            :
                                            <>
                                                <FormFile
                                                    className="position-relative"
                                                    required
                                                    name="file"
                                                    id="validationFormik108"
                                                    onChange={handleInputFileChange}
                                                    feedbackTooltip
                                                // onChange={handleInputFilesChange}
                                                />
                                            </>
                                    }
                                    </FormGrup>
                                    <InputButtonEdit
                                        style={{ marginTop:'25px', height:'30px', backgroundColor: fileSelect ? '#00FA9A' : '#909090' }}
                                        className="col-md-2 col-sm-2 col-2"
                                        onClick={handleButtonFilePropChange}
                                    >
                                        <img width="20" height="20" src={fileSelect ? icoCheck : icoEdit} />
                                    </InputButtonEdit>
                                
                            </Div>
                            {loadFile && <img width="30" height="30" style={{ marginLeft: '40%', marginTop: '5%' }} src={icoLoading} />}
                        </FormGrup>

                        <FormGrup className="col-md-12 col-12" >
                            <FormLabel>Descrição:</FormLabel>
                            <Div className="d-flex col-md-12 m-0">
                                <FormControl
                                    disabled={descriptionSelect ? false : true}
                                    className="col-md-10 col-10"
                                    as="textarea"
                                    rows="5"
                                    placeholder="Digite uma descrição"
                                    onChange={handleInputDescriptionChange}
                                    value={description}

                                />
                                <InputButtonEdit
                                    style={{ backgroundColor: descriptionSelect ? '#00FA9A' : '#909090' }}
                                    className="col-md-2 col-sm-2 col-2"
                                    onClick={handleButtonDescripPropChange}

                                >
                                    <img width="20" height="20" src={descriptionSelect ? icoCheck : icoEdit} />
                                </InputButtonEdit>
                            </Div>
                        </FormGrup>
                    </ContainContent>
                    <ContainContent>
                        <ButtonExclude
                            className="col-md-3 col-sm-6 col-12"
                            id="exlude"
                            name="btn_exclude"
                            type="button"
                            onClick={handleButtonDelete}
                        >
                            Excluir
                        </ButtonExclude>

                        {/* <FormCheck
                            type="switch"
                            id="custom-switch-massive"
                            label="Em massa"
                            onChange={() => setMany(!many)}
                        /> */}
                    </ContainContent>

                </ColContent>

            </ContainContent >
        </>
    );
};


