import React, { useState, useEffect } from 'react';
import Modal from 'react-modal';
import { toast } from 'react-toastify';
import api from '../../../../util/api';
import {
    MainRow,
    MainContainer,
    FormGrup,
    FormLabel,
    FormControl,
    ButtonEnviar,
    ButtonExclude,
} from './styles';
export default function ({ openModalThemes, setOpenModalThemes, contractId, setReloadThemes, reloadThemes }) {
    const [themeList, setThemeList] = useState([]);
    const [newTheme, setNewTheme] = useState('');

    const [themeSelected, setNewThemeSelected] = useState(null);

    useEffect(() => {
        async function loadThemes() {
            const { data } = await api.get(`themes/list/${contractId}`);

            setThemeList(data);
        };

        loadThemes()
    }, []);

    const handleInputThemeChange = data => {
        setNewTheme(data.target.value);
    };

    const handleSubmit = async () => {
        if ((!newTheme) || (newTheme[0] === " ")) {
            return toast.error('Insira um assunto corretamente (sem espaço no início)');
        };

        await api.post('themes/register', {
            name: newTheme,
            contract_id: contractId
        });

        const { data } = await api.get(`themes/list/${contractId}`);

        setThemeList(data);
        setNewTheme('');
        setReloadThemes(reloadThemes + 1);

        toast.success('Assunto cadastrado com sucesso');
    };

    const handleInputSelectChange = data => {
        setNewThemeSelected(data.target.value);
    };

    const handleDelete = async () => {
        try {
            await api.delete(`themes/delete/${themeSelected}`);

            const { data } = await api.get(`themes/list/${contractId}`);

            setThemeList(data);
            setReloadThemes(reloadThemes + 1);

            toast.success('Deletado com sucesso!');
        } catch (err) {

        }
    };

    return (
        <Modal
            style={
                {
                    overlay: {
                        backgroundColor: 'rgba(25,25,25, .8)',
                    },
                    content: {
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                        padding: '20px',
                        top: '0',
                        bottom: '0',
                        left: '0',
                        right: '0',
                        border: 'none',
                        backgroundColor: 'rgba(25,25,25, .0)',
                        margin: '0',
                        borderRadius: '10px',
                        height: '100vh',
                        width: '100%',
                    },
                }
            }
            //
            isOpen={openModalThemes} onRequestClose={() => setOpenModalThemes(false)}
        >
            <MainRow>
                <MainContainer>

                    <FormGrup className="col-md-12 d-flex justify-content-end">
                        <ButtonExclude onClick={() => setOpenModalThemes(false)}>X</ButtonExclude>
                    </FormGrup>
                    <MainRow>
                        <FormGrup className="col-md-10">
                            <FormLabel>Cadastrar assunto destaque:</FormLabel>
                            <input type="text" placeholder="Digite o assunto destaque" value={newTheme} onChange={handleInputThemeChange} style={{ width: '100%' }} />
                        </FormGrup>
                        <ButtonEnviar className="col-md-2 col-11" onClick={handleSubmit}>Cadastrar</ButtonEnviar>
                    </MainRow>
                    <MainRow style={{marginTop:'30px'}}>
                        <FormGrup className="col-md-10" >
                            <FormLabel>Deletar assunto destaque:</FormLabel>
                            <FormControl value={themeSelected} onChange={handleInputSelectChange} className="col-md-12 col-12" as="select">
                                <option value={null}> </option>
                                {themeList.map(index => (
                                    <option value={index.id}>{index.name}</option>
                                ))}
                            </FormControl>
                        </FormGrup>
                        <ButtonExclude className="col-md-2 col-11" onClick={handleDelete}>Deletar</ButtonExclude>
                    </MainRow>
                </MainContainer>
            </MainRow>
        </Modal>

    );
};

