import React, { useState, useEffect } from "react";
import { useDispatch } from "react-redux";
import api from "../../util/api";
import history from "../../util/history";
import { toast } from "react-toastify";

import {
  WindowsC1,
  WindowsC2,
  WindowsListScroll,
  BtnSaveEdit,
  Div,
  FormGrup,
  FormLabel,
  FormControl,
  InputButtonEdit,
  BtnSaveCreate,
  WindowsC2List,
  BtnExclude,
  CardHorizontalBody,
  CardDiv,
  ButtonEditCard,
  TextCardList,
  ButtonTypeCad,
  WindowDescription,
  SubTitleWindow,
} from "./styles";

import icoEdit from "../../assets/images/img_388620.png";
import icoCheck from "../../assets/images/checkEdit.png";

function Login() {
  const dispatch = useDispatch();
  const [userList, setCompanieList] = useState([]);

  const [hasUser, setHasUser] = useState(false);

  const [idSelected, setIdSelected] = useState(0);
  const [userName, setUserName] = useState("");
  const [userNameSelect, setUserNameSelect] = useState(false);

  const [login, setLogin] = useState("");
  const [loginSelect, setLoginSelect] = useState(false);

  const [password, setPassword] = useState("");
  const [passwordSelect, setPasswordSelect] = useState(false);

  const [att, setAtt] = useState(0);

  //Start Funcionarios Inputs changes

  const [idSelectedColaboreted, setIdSelectedColaboreted] = useState(0);

  const [ColaboratedList, setColaborateds] = useState([]);

  var [typeCad, SetStateCad] = useState(false);

  const companyes = ["MRV", "TENDA", "LOPES"];

  const [saveCompany, setSaveCompany] = useState("");
  const [saveCompanySelect, setSaveCompanySelect] = useState(false);

  const [LoginColarate, setLoginColarate] = useState("");
  const [cSelectLogin, setCSelectLogin] = useState(false);

  const [passwordColarate, setPasswordColarate] = useState("");
  const [cSelectPassword, SetCSelectPassword] = useState(false);

  useEffect(() => {
    async function loadUsers() {
      const { data } = await api.get("/user/list");

      setColaborateds(data);
    }

    loadUsers();
  }, [att]);

  const handleInputUserColaboretedEdit = (data) => {
    setIdSelectedColaboreted(data.id);
    setLoginColarate(data.user);
    setPasswordColarate(data.password);
    setHasUser(true);
  };

  const handleDeleteColaboreted = async (id) => {
    try {
      await api.delete(`user/delete/${id}`);
    } catch (err) {
      setAtt(att + 1);
    }
  };

  const handleButtonCompanyColaborated = async () => {
    if (!saveCompanySelect) {
      setSaveCompanySelect(true);
      console.log("caiu true");
    } else {
      console.log("caiu false");
      setSaveCompanySelect(false);
    }
  };

  const handleButtonLoginColaborated = async () => {
    if (!cSelectLogin) {
      setCSelectLogin(true);
      console.log("caiu true");
    } else {
      console.log("caiu false");
      setCSelectLogin(false);
    }
  };

  const handleButtonPasswordColaborated = async () => {
    if (!cSelectPassword) {
      SetCSelectPassword(true);
      console.log("caiu true");
    } else {
      console.log("caiu false");
      SetCSelectPassword(false);
    }
  };

  const handleLoginColaborated = (data) => {
    setLoginColarate(data.target.value);
  };

  const handlePassWordColaborated = (data) => {
    setPasswordColarate(data.target.value);
  };

  const handleCompanyUser = (data) => {
    setSaveCompany(data.target.value);
  };
  const handleCompanyButton = (data) => {
    SetStateCad(false);
  };
  const handleCollaboratorButton = (data) => {
    SetStateCad(true);
  };

  // End Funcionarios Input Change

  useEffect(() => {
    async function loadCompanies() {
      const { data } = await api.get("company/list");

      setCompanieList(data);
    }

    loadCompanies();
  }, [att]);

  const handleUserNameChange = (data) => {
    setUserName(data.target.value);
  };

  const handleUserLoginChange = (data) => {
    setLogin(data.target.value);
  };

  const handlePasswordChange = (data) => {
    setPassword(data.target.value);
  };

  const handleButtonUserNameSelectChange = async () => {
    if (!userNameSelect) {
      setUserNameSelect(true);
    } else {
      setUserNameSelect(false);
    }
  };

  const handleButtonLoginSelectChange = async () => {
    if (!loginSelect) {
      setLoginSelect(true);
    } else {
      setLoginSelect(false);
    }
  };

  const handleButtonPassSelectChange = async () => {
    if (!passwordSelect) {
      setPasswordSelect(true);
    } else {
      setPasswordSelect(false);
    }
  };

  const handleInputUserEdit = (data) => {
    setIdSelected(data.id);
    setUserName(data.name);
    setLogin(data.user);
    setPassword(data.password);
    setHasUser(true);
  };

  const handleUpdate = async (data) => {
    if (typeCad == false) {
      if (!userName) {
        return toast.error("Preencha os campos corretamente");
      }

      try {
        await api.put(`company/update/${idSelected}`, {
          name: userName,
          user: login,
          password,
        });

        setIdSelected(0);
        setUserName("");

        toast.success("Compania atualizada com sucesso!");

        setAtt(att + 1);
      } catch (err) {
        return toast.error(
          "Erro ao atualizar o usuário, contate um administrador"
        );
      }
    } else if (typeCad == true) {
      if (!LoginColarate || !passwordColarate || idSelectedColaboreted === 0) {
        return toast.error("Preencha os campos corretamente");
      }

      try {
        await api.put(`user/update/${idSelectedColaboreted}`, {
          user: LoginColarate,
          password: passwordColarate,
        });

        setIdSelectedColaboreted(0);
        setSaveCompany("");
        setLoginColarate("");
        setPasswordColarate("");
        setHasUser(false);

        toast.success("Usuário atualizado com sucesso!");

        setAtt(att + 1);
      } catch (err) {
        return toast.error(
          "Erro ao atualizar o usuário, contate um administrador"
        );
      }
    }
  };

  const handleDelete = async (id) => {
    try {
      await api.delete(`company/delete/${id}`);
      setAtt(att + 1);
    } catch (err) { }
  };

  return (
    <>
      <Div className="col-md-12 col-sm-12 col-12 d-flex justifty-content-center align-items-center m-0 p-3">
        <FormGrup className="col-md-2 col-12 d-flex align-items:center justify-content-center">
          <FormLabel for="type-account"></FormLabel>
          <ButtonTypeCad
            style={{ backgroundColor: typeCad ? "#909090" : "#ffaa5C" }}
            onClick={handleCompanyButton}
          >
            EMPRESA
          </ButtonTypeCad>
        </FormGrup>
        <FormGrup className="col-md-2 col-12 d-flex align-items-center justify-content-center">
          <FormLabel for="type-account"></FormLabel>
          <ButtonTypeCad
            style={{ backgroundColor: typeCad ? "#ffaa5C" : "#909090" }}
            onClick={handleCollaboratorButton}
          >
            FUNCIONARIO
          </ButtonTypeCad>
        </FormGrup>
      </Div>
      {typeCad ? (
        <>
          {/* FUNCIONARIO */}
          <WindowsC1>
            <WindowsC2>
              <Div>
                <FormGrup className="col-md-10  col-10">
                  <FormLabel for="login">Login:</FormLabel>
                  <FormControl
                    name="login"
                    type="text"
                    value={LoginColarate}
                    onChange={handleLoginColaborated}
                    disabled={cSelectLogin ? false : true}
                  />
                </FormGrup>
                <InputButtonEdit
                  style={{
                    backgroundColor: cSelectLogin ? "#00FA9A" : "#909090",
                  }}
                  className="col-md-2 col-sm-2 col-2"
                  onClick={handleButtonLoginColaborated}
                >
                  <img
                    width="20"
                    height="20"
                    src={cSelectLogin ? icoCheck : icoEdit}
                  />
                </InputButtonEdit>
              </Div>

              <Div>
                <FormGrup className="col-md-10  col-10">
                  <FormLabel for="senha">Senha:</FormLabel>
                  <FormControl
                    name="senha"
                    type="text"
                    value={passwordColarate}
                    onChange={handlePassWordColaborated}
                    disabled={cSelectPassword ? false : true}
                  />
                </FormGrup>
                <InputButtonEdit
                  style={{
                    backgroundColor: cSelectPassword ? "#00FA9A" : "#909090",
                  }}
                  className="col-md-2 col-sm-2 col-2"
                  onClick={handleButtonPasswordColaborated}
                >
                  <img
                    width="20"
                    height="20"
                    src={passwordSelect ? icoCheck : icoEdit}
                  />
                </InputButtonEdit>
              </Div>
              <Div>
                <BtnSaveEdit
                  onClick={handleUpdate}
                  className="col-md-5 col-sm-5 col-12"
                  disabled={hasUser ? false : true}
                >
                  Salvar edição
                </BtnSaveEdit>
                <BtnSaveCreate
                  className="col-md-12 col-sm-12 col-12"
                  onClick={function () {
                    history.push("/new-user/1PXD1KFMNZNPx-01MX");
                  }}
                >
                  Novo Cadastro
                </BtnSaveCreate>
              </Div>
            </WindowsC2>
          </WindowsC1>
          <WindowsC1>
            <WindowsC2List>
              <WindowDescription >
                  <SubTitleWindow className="col-md-3 col-sm-4 col-12">
                      Empresa:
                  </SubTitleWindow>
                  <SubTitleWindow className="col-md-3 col-sm-4 col-12">
                      Nome:
                  </SubTitleWindow>
                  <SubTitleWindow className="col-md-3 col-sm-4 col-12">
                      Senha:
                  </SubTitleWindow>
                  <SubTitleWindow className="col-md-3 col-sm-0 col-12">
                      --
                  </SubTitleWindow>               
              </WindowDescription>
              <WindowsListScroll>
                {ColaboratedList.map((data) => (
                  <CardHorizontalBody>
                    <CardDiv className="col-md-12 col-sm-12 col-12">
                      <TextCardList className="col-md-3 col-sm-4 col-12">
                        {data.Company.name}
                      </TextCardList>
                      <TextCardList className="col-md-3 col-sm-4 col-12">
                        {data.user}
                      </TextCardList>
                      <TextCardList className="col-md-3 col-sm-4 col-12">
                        {data.password}
                      </TextCardList>
                      <ButtonEditCard
                        className="col-md-2 col-sm-6 col-12"
                        onClick={() => handleInputUserColaboretedEdit(data)}
                      >
                        Editar
                      </ButtonEditCard>
                      <BtnExclude
                        onClick={() => handleDeleteColaboreted(data.id)}
                        className="col-md-1 col-sm-6 col-12"
                      >
                        Excluir
                      </BtnExclude>
                    </CardDiv>
                  </CardHorizontalBody>
                ))}
              </WindowsListScroll>
            </WindowsC2List>
          </WindowsC1>
        </>
      ) : (
          <>
            {/* EMPRESA */}
            <WindowsC1>
              <WindowsC2>
                <Div>
                  <FormGrup className="col-md-10  col-10">
                    <FormLabel for="login">Empresa:</FormLabel>
                    <FormControl
                      name="login"
                      type="text"
                      value={userName}
                      onChange={handleUserNameChange}
                      disabled={userNameSelect ? false : true}
                    />
                  </FormGrup>
                  <InputButtonEdit
                    style={{
                      backgroundColor: userNameSelect ? "#00FA9A" : "#909090",
                    }}
                    className="col-md-2 col-sm-2 col-2"
                    onClick={handleButtonUserNameSelectChange}
                  >
                    <img
                      width="20"
                      height="20"
                      src={userNameSelect ? icoCheck : icoEdit}
                    />
                  </InputButtonEdit>
                </Div>

                <Div>
                  <BtnSaveEdit
                    onClick={handleUpdate}
                    className="col-md-5 col-sm-5 col-12"
                    disabled={hasUser ? false : true}
                  >
                    Salvar edição
                </BtnSaveEdit>
                  <BtnSaveCreate
                    className="col-md-12 col-sm-12 col-12"
                    onClick={function () {
                      history.push("/new-user/1PXD1KFMNZNPx-01MX");
                    }}
                  >
                    Novo Cadastro
                </BtnSaveCreate>
                </Div>
              </WindowsC2>
            </WindowsC1>
            <WindowsC1>
              <WindowsC2List>
              <WindowDescription >
                  <SubTitleWindow className="col-md-3 col-sm-12 col-12">
                      Empresa:
                  </SubTitleWindow>
                  <SubTitleWindow className="col-md-3 col-sm-4 col-12">
                      --
                  </SubTitleWindow>
                  <SubTitleWindow className="col-md-3 col-sm-4 col-12">
                      --
                  </SubTitleWindow>
                  <SubTitleWindow className="col-md-3 col-sm-4 col-12">
                      --
                  </SubTitleWindow>               
              </WindowDescription>
                <WindowsListScroll>
                  {userList.map((data) => (
                    <CardHorizontalBody>
                      <CardDiv className="col-md-12 col-sm-12 col-12">
                        <TextCardList className="col-md-3 col-sm-12 col-12">
                          {data.name}
                        </TextCardList>
                        <TextCardList className="col-md-3 col-sm-6 col-12">
                        
                        </TextCardList>
                        <TextCardList className="col-md-3 col-sm-6 col-12">
                          
                        </TextCardList>
                        <ButtonEditCard
                          className="col-md-2 col-sm-6 col-12"
                          onClick={() => handleInputUserEdit(data)}
                        >
                          Editar
                      </ButtonEditCard>
                        <BtnExclude
                          onClick={() => handleDelete(data.id)}
                          className="col-md-1 col-sm-6 col-12"
                        >
                          Excluir
                      </BtnExclude>
                      </CardDiv>
                    </CardHorizontalBody>
                  ))}
                </WindowsListScroll>
              </WindowsC2List>
            </WindowsC1>
          </>
        )}
    </>
  );
}

export default Login;
