import React, { useState, useEffect } from "react";
import { useDispatch } from "react-redux";
import { toast } from "react-toastify";
import api from "../../util/api";
import history from "../../util/history";

import {
  WindowsC1,
  WindowsC2,
  BtnSaveEdit,
  Div,
  FormGrup,
  FormLabel,
  FormControl,
  ButtonTypeCad,
  BtnPageEdit,
} from "./styles";

function Login() {
  var [typeCad, SetStateCad] = useState(false);

  const dispatch = useDispatch();

  const [saveCompany, setSaveCompany] = useState("");

  const [att, setAtt] = useState(0);
  const [login, setLogin] = useState("");
  const [compani, setCompani] = useState("");
  const [companyes, setCompanies] = useState([]);
  const [password, setPassword] = useState("");
  const [passwordConfirm, setpasswordConfirm] = useState("");

  useEffect(() => {
    async function loadCompanies() {
      const { data } = await api.get("/company/list");

      setCompanies(data);
    }

    loadCompanies();
  }, [att]);

  const handleCompanyUser = (data) => {
    setSaveCompany(data.target.value);
  };

  const handleUserCompaniChange = (data) => {
    setCompani(data.target.value);
  };

  const handleUserLoginChange = (data) => {
    setLogin(data.target.value);
  };

  const handlePasswordChange = (data) => {
    setPassword(data.target.value);
  };

  const handlePasswordChangeConfirm = (data) => {
    setpasswordConfirm(data.target.value);
  };

  const handleCompanyButton = (data) => {
    SetStateCad(false);
    setLogin("");
    setCompani("");
    setPassword("");
    setpasswordConfirm("");
  };
  const handleCollaboratorButton = (data) => {
    SetStateCad(true);
    setLogin("");
    setCompani("");
    setPassword("");
    setpasswordConfirm("");
  };

  const handleSubmit = async () => {
    if (typeCad == false) {
      try {
        if (!compani) {
          return toast.error("Nome não pode ser nulo");
        }

        await api.post("/company/create", {
          name: compani,
        });

        setCompani("");

        toast.success("Compania cadastrada com sucesso!");
        setAtt(att + 1);
      } catch (err) {
        return toast.error("Compania já existe");
      }
    } else if (typeCad == true) {
      try {
        if (!login || !saveCompany || !password || !passwordConfirm) {
          return toast.error("Todos os campos são obrigatórios");
        }

        if (password !== passwordConfirm) {
          return toast.error("Senhas indiferem");
        }

        await api.post("/user/create", {
          user: login,
          company_id: saveCompany,
          password: password,
          confirmPassword: passwordConfirm,
        });

        setLogin("");
        setCompani("");
        setPassword("");
        setpasswordConfirm("");

        toast.success("Usuário cadastrado com sucesso!");
      } catch (err) {
        return toast.error(
          "Erro ao criar o cadastro, verifique as credenciais"
        );
      }
    }
  };

  return (
    <>
      <WindowsC1>
        <WindowsC2>
          <Div>
            <FormGrup className="col-md-6 d-flex align-items:center justify-content-center">
              <FormLabel for="type-account"></FormLabel>
              <ButtonTypeCad
                style={{ backgroundColor: typeCad ? "#909090" : "#ffaa5C" }}
                onClick={handleCompanyButton}
              >
                EMPRESA
              </ButtonTypeCad>
            </FormGrup>
            <FormGrup className="col-md-6 align-items-center justify-content-center">
              <FormLabel for="type-account"></FormLabel>
              <ButtonTypeCad
                style={{ backgroundColor: typeCad ? "#ffaa5C" : "#909090" }}
                onClick={handleCollaboratorButton}
              >
                FUNCIONARIO
              </ButtonTypeCad>
            </FormGrup>
          </Div>
          {typeCad && (
            <Div>
              <FormGrup className="col-md-12 col-12">
                <FormLabel for="login">Login:</FormLabel>
                <FormControl
                  name="login"
                  type="text"
                  value={login}
                  onChange={handleUserLoginChange}
                />
              </FormGrup>
            </Div>
          )}

          {typeCad ? (
            <Div>
              <FormGrup className="col-md-12 col-12">
                <FormLabel for="select-type">Selecione a empresa:</FormLabel>
                <FormControl
                  className="col-md-12 col-12"
                  as="select"
                  name="select-company"
                  onChange={handleCompanyUser}
                >
                  <option></option>
                  {companyes.map((data) => (
                    <option value={data.id}>{data.name}</option>
                  ))}
                </FormControl>
              </FormGrup>
            </Div>
          ) : (
            <Div>
              <FormGrup className="col-md-12 col-12">
                <FormLabel for="login">Companhia:</FormLabel>
                <FormControl
                  name="login"
                  type="text"
                  value={compani}
                  onChange={handleUserCompaniChange}
                />
              </FormGrup>
            </Div>
          )}
          {typeCad && (
            <Div>
              <Div>
                <FormGrup className="col-md-12 col-12">
                  <FormLabel for="senha">Senha:</FormLabel>
                  <FormControl
                    name="senha"
                    type="password"
                    value={password}
                    onChange={handlePasswordChange}
                  />
                </FormGrup>
              </Div>

              <Div>
                <FormGrup className="col-md-12 col-12">
                  <FormLabel for="senha">Confirmar Senha:</FormLabel>
                  <FormControl
                    name="senha"
                    type="password"
                    value={passwordConfirm}
                    onChange={handlePasswordChangeConfirm}
                  />
                </FormGrup>
              </Div>
            </Div>
          )}
          <Div>
            <BtnSaveEdit
              onClick={handleSubmit}
              className="col-md-12 col-sm-12 col-12"
            >
              Criar
            </BtnSaveEdit>
            <BtnPageEdit
              onClick={function () {
                history.push("/edit-user/1PXD1KFMNZNPx-01MX");
              }}
              className="col-md-12 col-sm-12 col-12"
            >
              Edição
            </BtnPageEdit>
          </Div>
        </WindowsC2>
      </WindowsC1>
    </>
  );
}

export default Login;
