export default function filterTerms(documents, terms)
{
    function cutText(position, fulltext)
    {
        var textNext = "";
        var textLast = "";

        for(var i = position - 300; i < position; i++)
        {
            if(!fulltext[i])
            {
                break;
            }

            textNext += fulltext[i]
        }

        for(var i = position; i < position + 1000; i++)
        {
            if(!fulltext[i])
            {
                break;
            }

            textNext += fulltext[i]
        }

        return textLast + textNext;
    }


    for(const term of terms)
    {
        const { value } = term;

        const arrayFilter = [];

        documents.forEach(index => {
            const position = index.File.ocr.toLowerCase().indexOf(value.toLowerCase());

            if(position > -1)
            {
                const obj = {
                    ...index,
                    preview: cutText(position, index.File.ocr),
                    position: position + 25
                };  

                arrayFilter.push(obj);
            };
        });

        documents = arrayFilter;
    };

    return documents;
}