export function signInRequest(user, password)
{
    return{
        type: '@auth/SIGN_IN_REQUEST',
        payload: { user, password }
    };
};

export function signInSucess(user, token)
{
    return{
        type: '@auth/SIGN_IN_SUCESS',
        payload: { user, token }
    };
};

export function signOut()
{
    return{
        type: '@auth/SIGN_OUT'
    }
}

