import { Row,Container, Button, Form } from 'react-bootstrap';
import Styled from 'styled-components';

export const WindowsC1 = Styled(Container)`
    display:flex;
    justify-content:center;
    align-items:center;
    margin-top: 10vh;
`;

export const ButtonTypeCad = Styled(Button)`
    border:none;
    background-color:#909090 ;
    
    &:hover{
        transition:0.3s;
        background-color:#ffaa5C;
        color:white;
    }

    &:focus{
        transition:0.3s;
        background-color:#ffaa5C!important;
        color:white;
        border:none;
        box-shadow:none;
    }

    &:disabled{
        transition:0.3s;
        background-color:#909090!important;
        color:white;
        border:none;
        box-shadow:none;
    }
`;

export const FormGrup = Styled(Form.Group)`

`;

export const FormLabel = Styled(Form.Label)`
    padding:0;
    margin:0;
    color:white;
`;
export const FormControl = Styled(Form.Control)`
    border-radius:0;
`;


export const WindowsC2 = Styled(Container)` 
    width:500px;
    background-color:#374F4F;
    display:flex;
    flex-direction:column;
    justify-content:center;
    align-items:center;
    padding:50px;
    border-radius:15px;
`;
export const Label = Styled.label`
    color:white;
    margin:10px 0px;
`;

export const Div = Styled(Row)`
    display:flex;
    width:100%;
    justify-content:center;
    align-items:center;
`;

