import { Row,Container, Button, Form, Col } from 'react-bootstrap';
import Styled from 'styled-components';
import PerfectScrollBar from 'react-perfect-scrollbar';


// Contain User Scroll
export const WindowsListScroll = Styled.div`
   
    width:100%;
`;

export const ButtonTypeCad = Styled(Button)`
    border:none;
    background-color:#909090 ;
    
    &:hover{
        transition:0.3s;
        background-color:#ffaa5C;
        color:white;
    }

    &:focus{
        transition:0.3s;
        background-color:#ffaa5C!important;
        color:white;
        border:none;
        box-shadow:none;
    }

    &:disabled{
        transition:0.3s;
        background-color:#909090!important;
        color:white;
        border:none;
        box-shadow:none;
    }
`;
// Start card user list
export const CardHorizontalBody = Styled(Row)`
    margin-top:15px;
    margin-bottom:15px;
`;

export const CardDiv = Styled(Row)`
    margin:0;
    border:white 3px solid;
    border-radius:5px;
    background-color:white;
    padding:0;
`;

export const ButtonEditCard = Styled(Button)`
    background-color:#FAC810;
    border-radius:0;
    border:none;
    font-weight:bold;
    &:hover{
        transition:0.3s;
        background-color:#FAaa10;
        color:white;
    }
    &:focus{
        transition:0.3s;
        background-color:#909090!important;
        color:white;
        border:none;
        box-shadow:none;
    }
 `;

 export const TextCardList = Styled.h2`
    margin-top:8px;
    font-size:16px;
    text-align:center;
 `;

 export const BtnExclude = Styled(Button)`
    border-radius:0;
    transition:0.3s;
    background-color:#CC0000;
    color:white;
    font-weight:bold;
    border:none;
        &:hover{
            transition:0.3s;
            background-color:#ff1100;
            color:white;
        }
        &:focus{
            transition:0.3s;
            background-color:#909090!important;
            color:white;
            border:none;
            box-shadow:none;
        }
        &:disabled{
            transition:0.3s;
            background-color:#909090!important;
            color:white;
            border:none;
            box-shadow:none;
        }
`;

// End card user list

export const WindowsC1 = Styled(Container)`
    display:flex;
    justify-content:center;
    align-items:center;
    margin-top: 5vh;
`;

export const FormGrup = Styled(Form.Group)`

`;
export const FormLabel = Styled(Form.Label)`
    padding:0;
    margin:0;
    color:white;
`;
export const FormControl = Styled(Form.Control)`

`;

export const InputButtonEdit = Styled(Button)`
    display:flex;
    height:40px;
    width:40px;
    align-items:center;
    justify-content:center;
    padding:5px 5px;
    margin-top:10px;
    border:none;
`;

export const WindowsC2 = Styled(Container)` 
    width:500px;
    background-color:#374F4F;
    display:flex;
    flex-direction:column;
    justify-content:center;
    align-items:center;
    padding:50px;
    border-radius:15px;
`;

export const WindowDescription = Styled(Row)`
    width:100%;
    margin:0;
`;

export const SubTitleWindow = Styled(Col)`
    color:white;
    font-size:16px;
    display:flex;
    align-items:center;
    justify-content:center;
`;

export const WindowsC2List = Styled(Container)` 
    width:100%;
    margin-top:50px;
    background-color:#374F4F;
    align-items:center;
    padding:50px;
    border-radius:10px;
    margin-bottom:50px;
`;

export const Label = Styled.label`
    color:white;
    margin:10px 0px;
`;

export const Div = Styled(Row)`
    display:flex;
    width:100%;
    justify-content:center;
    align-items:center;
`;

export const IcoUser = Styled.img`
    width:100px;
    height:100px;
    margin:10px;
    border-radius:100%;
    border:solid 3px #00CC5C;
`;

