import React, { useState, useEffect } from "react";
import Modal from "react-modal";
import { toast } from "react-toastify";

import api from "../../../../util/api";

import {
  ContainNav,
  ContainContent,
  ColContent,
  ButtonGrupReg,
  ButtonGrupClose,
  ButtonEnviar,
  FormGrup,
  FormLabel,
  FormCheck,
  FormControl,
  FormFile,
  DivScrollSubject,
  ClosedModalButton,
  ExcludeBtn,
  TagName,
} from "./styles";

import ModalSelectThemes from "../selectthemes";

import icoLoading from "../../../../assets/images/icoLoading.gif";

function RegisterNotesMeeting({
  openModalNotes,
  setOpenModalNotes,
  reloadThemes,
  contractId,
  reloadList,
  setReloadList
}) {
  const [openModalSelectThemes, setOpenModalSelectThemes] = useState(false);

  const [themesSelecteds, setThemesSelecteds] = useState([]);

  const [ataName, setAtaName] = useState("");
  const [code, setCode] = useState("");
  const [date, setDate] = useState("");
  const [impact, setImpact] = useState(false);
  const [description, setDescription] = useState("");

  const [file, setFile] = useState(0);
  const [files, setFiles] = useState([]);
  const [loadFile, setLoadFile] = useState(false);

  const [many, setMany] = useState(false);

  // useEffect(() => {
  //     async function loadThemes() {
  //         const { data } = await api.get(`themes/list/${contractId}`);

  //         setThemes(data);
  //     }

  //     loadThemes();
  // }, [reloadThemes]);

  const handleButtonDeleteTheme = (id) => {
    var themesSelectedsCopy = themesSelecteds;
    const index = themesSelecteds.findIndex((index) => index.id === id);
    themesSelectedsCopy.splice(index, 1);

    setThemesSelecteds([]);

    setTimeout(() => {
      setThemesSelecteds(themesSelectedsCopy);
    }, 10);
  };

  const handleInputAtaNameChange = (data) => {
    setAtaName(data.target.value);
  };

  const handleInputCodeChange = (data) => {
    setCode(data.target.value);
  };

  const handleInputDateChange = (data) => {
    setDate(data.target.value);
  };

  const handleInputFileChange = async (data) => {
    const form = new FormData();
    form.append("file", data.target.files[0]);

    try {
      setLoadFile(true);
      const answear = await api.post("files/upload", form);

      const { id } = answear.data;

      setFile(id);
      setLoadFile(false);
    } catch (err) {
      toast.error(
        "Não foi possível carregar o PDF, por favor contate um administrador"
      );
    }
  };

  const handleInputFilesChange = async (data) => {
    const { files } = data.target;
    const filesId = [];

    try {
      setLoadFile(true);

      for (var i = 0; i < files.length; i++) {
        const form = new FormData();
        form.append("file", files[i]);

        const {
          data: { id },
        } = await api.post("files/upload", form);

        filesId.push(id);
      }

      setLoadFile(false);
    } catch (err) {
      toast.error(`Erro ao carregar, contate um administrador`);
    }

    setFiles(filesId);
  };

  const handleInputImpactChange = () => {
    setImpact(!impact);
  };

  const handleInputDescriptionChange = async (data) => {
    setDescription(data.target.value);
  };

  const clearAll = () => {
    setAtaName("");
    setCode("");
    setThemesSelecteds([]);
    setFile(0);
    setDescription("");
    setDate("");
    if (!many) {
      document.getElementById("validationFormik107").value = "";
    } else {
      document.getElementById("validationFormik108").value = "";
    }
  };

  const handleSubmit = async () => {
    if (!many) {
      try {
        const dateFirstP = date[0];
        if (dateFirstP === "0" || date.length != 10) {
          return toast.error("Preencha a data corretamente");
        }

        if (loadFile) {
          return toast.error("Aguarde até que o PDF seja carregado");
        }

        if (file === 0) {
          return toast.error("Você precisa carregar um PDF");
        }

        var answear;

        const obj = {
          name: ataName,
          date,
          impact,
          description,
          contract_id: contractId,
          file_id: file,
        };

        if (themesSelecteds[0]) {
          const FormattedThemesSelecteds = themesSelecteds.map((index) => {
            const { value } = index;

            return value;
          });

          obj.themes = FormattedThemesSelecteds;
        }

        if (!code) {
          answear = await api.post("notesmeetings/register", obj);
        } else {
          obj.code = code;

          answear = await api.post("notesmeetings/register", obj);

          const { Error: returnError } = answear.data;
          if (returnError) {
            const { Message } = answear.data;

            return toast.error(Message);
          }
        }

        clearAll();
        setReloadList(reloadList + 1);

        toast.success("Documento enviado!");
      } catch (err) {
        toast.error(
          "Não foi possível cadastrar o documento, contate um administrador!"
        );
      }
    } else {
      try {
        const dateFirstP = date[0];
        if (dateFirstP === "0" || date.length != 10) {
          return toast.error("Preencha a data corretamente");
        }

        if (loadFile) {
          return toast.error("Aguarde até que o PDF seja carregado");
        }

        if (!files[0]) {
          return toast.error("Carregue ao menos um PDF");
        }

        await api.post("notesmeetings/register/many", {
          date,
          contract_id: contractId,
          files_id: files,
        });

        clearAll();
        setReloadList(reloadList + 1);

        toast.success("Documentos enviados!");
      } catch (err) {
        toast.error(
          "Não foi possível cadastrar o documento, contate um administrador!"
        );
      }
    }
  };

  return (
    <Modal
      style={{
        overlay: {
          backgroundColor: "rgba(25,25,25, .8)",
        },
        content: {
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
          padding: "0",
          top: "0",
          bottom: "0",
          left: "0",
          right: "0",
          border: "none",
          backgroundColor: "rgba(25,25,25, .0)",
          margin: "0",
          borderRadius: "10px",
          height: "100vh",
          width: "100vw",
        },
      }}
      isOpen={openModalNotes}
      onRequestClose={() => {
        if (loadFile) return;
        setOpenModalNotes(false);
      }}
    >
      <ContainNav>
        <ModalSelectThemes
          openModalSelectThemes={openModalSelectThemes}
          setOpenModalSelectThemes={setOpenModalSelectThemes}
          contractId={contractId}
          themesSelecteds={themesSelecteds}
          setThemesSelecteds={setThemesSelecteds}
        />
        <ColContent className="col-md-8 col-sm-12 col-12">
          <ContainContent>
            <FormGrup className="col-md-8 col-12">
              <FormLabel>Assuntos destaque:</FormLabel>
              <DivScrollSubject>
                <ContainContent className="align-items:center">
                  {themesSelecteds.map((index) => (
                    <>
                    
                      <FormGrup className="d-flex col-md-6 col-6">
                        <TagName className="col-md-9 col-9" id={index.id}>
                          ! {index.value}
                        </TagName>
                        <ExcludeBtn
                          className="col-md-3 col-3"
                          onClick={() => handleButtonDeleteTheme(index.id)}
                        >
                          X
                        </ExcludeBtn>
                      </FormGrup>
                    </>
                  ))}
                </ContainContent>
              </DivScrollSubject>
            </FormGrup>
            <FormGrup className="col-md-3 col-11">
              <ButtonGrupReg onClick={() => setOpenModalSelectThemes(true)}>
                Adicionar assunto
              </ButtonGrupReg>
              {/* <ButtonGrupClose >Limpar assunto</ButtonGrupClose> */}
            </FormGrup>

            <FormGrup className="p-2 col-md-1 col-1 d-flex justify-content-end">
              <ClosedModalButton
                onClick={() => {
                  if (loadFile) return;
                  setOpenModalNotes(false);
                }}
              >
                X
              </ClosedModalButton>
            </FormGrup>

            <FormGrup className="col-md-6 col-12">
              <FormLabel>Nome da ata:</FormLabel>
              <FormControl
                onChange={handleInputAtaNameChange}
                value={ataName}
                disabled={!many ? false : true}
                required
                placeholder="Digite o nome"
                type="text"
              />
            </FormGrup>
            <FormGrup className="col-md-6 col-12">
              <FormLabel>Data:</FormLabel>
              <FormControl
                value={date}
                onChange={handleInputDateChange}
                required
                type="date"
              />
            </FormGrup>
            <FormGrup className="col-md-12 col-12">
              <FormLabel>Código do documento:</FormLabel>
              <FormControl
                onChange={handleInputCodeChange}
                value={code}
                disabled={!many ? false : true}
                required
                placeholder="Digite o codigo"
                type="text"
              />
            </FormGrup>
          </ContainContent>
          <ContainContent style={{ justifyContent: "start" }}>
            <FormGrup className="col-md-6 col-12">
              <FormLabel>Selecione o arquivo:</FormLabel>
              {!many ? (
                <FormFile
                  onChange={handleInputFileChange}
                  className="position-relative"
                  required
                  name="file"
                  id="validationFormik107"
                  feedbackTooltip
                />
              ) : (
                <FormFile
                  onChange={handleInputFilesChange}
                  className="position-relative"
                  required
                  name="file"
                  id="validationFormik108"
                  feedbackTooltip
                  multiple
                />
              )}
              {loadFile && (
                <img
                  width="30"
                  height="30"
                  style={{ marginLeft: "40%", marginTop: "5%" }}
                  src={icoLoading}
                />
              )}
            </FormGrup>
            <FormGrup className="col-md-6 col-12">
              <FormCheck
                disabled={!many ? false : true}
                type="switch"
                id="custom-switch"
                label="Ocorrência importante"
                onChange={handleInputImpactChange}
              />
            </FormGrup>
          </ContainContent>
          <ContainContent>
            <FormGrup className="col-md-12 col-12">
              <FormLabel>Descrição:</FormLabel>
              <FormControl
                disabled={!many ? false : true}
                placeholder="Digite uma descrição"
                onChange={handleInputDescriptionChange}
                value={description}
                className="col-md-12 col-12"
                as="textarea"
                rows="5"
              />
            </FormGrup>
          </ContainContent>
          <ContainContent>
            <ButtonEnviar
              onClick={handleSubmit}
              id="SubForm"
              name="btForm"
              type="button"
            >
              Enviar
            </ButtonEnviar>
          </ContainContent>
          <FormCheck
            type="switch"
            id="custom-switch-massive"
            label="Em massa"
            onChange={() => setMany(!many)}
          />
        </ColContent>
      </ContainNav>
    </Modal>
  );
}

export default RegisterNotesMeeting;
