import React, { useState, useEffect } from 'react';
import Modal from 'react-modal';
import { toast } from 'react-toastify';

import history from '../../../../util/history';
import api from '../../../../util/api';

import {
    Body,
    Buttons
} from './styles';

export default function Delete({ openModalDelete, setOpenModalDelete,  contractId: contract_id })
{
    const [contracts, setContracts] = useState([]);
    const [contractSelected, setContractSelected] = useState(null);

    useEffect(() => {
        async function loadContracts()
        {
            const { data } = await api.get('contract/list');

            const formattedData = data.filter(index => index.id != contract_id);

            console.log(formattedData);

            setContracts(formattedData);
        };

        loadContracts();
    }, []);

    const handleSelectChange = data => {
        if(!data.target.value) return;

        setContractSelected(data.target.value);
    };

    const handleDelete = async () =>  {
        try{
            if(!contractSelected)
            {   
                return toast.error('Você precisa selecionar um contrato')
            };

            await api.delete(`contract/delete/${contract_id}/${contractSelected}`);

            toast.success('Contrato deletado com sucesso!');

            history.push('/home');
        }catch(err)
        {
            return toast.error('Erro ao deletar o contrato, contate um administrador');
        }
    };

    return(
        <Modal
            style={
                {
                    overlay: {
                        backgroundColor: 'rgba(25,25,25, .8)',
                    },
                    content: {
                        border: 'none',
                        backgroundColor: 'rgba(25,25,25, .0)',
                        margin: '30px',
                        borderRadius: '10px',
                        height: '600px',
                    },
                }
            }
            isOpen={openModalDelete}
        >
            <Body>
                <h1>Selecione o novo contrato para os subdocumentos</h1>
                <select onChange={handleSelectChange}>
                    <option value=""></option>
                    {contracts.map(index => (
                        <option value={index.id}>{index.name}</option>
                    ))}
                </select>
                <Buttons>
                    <button style={{ backgroundColor: '#990000' }} onClick={handleDelete}>Confirmar</button>
                    <button style={{ border: '1px solid #000', color: '#000' }} onClick={() => setOpenModalDelete(false)}>Cancelar</button>
                </Buttons>
            </Body>
        </Modal>
    )
};