/* eslint-disable */
import React, { useState, useEffect, useMemo } from "react";
import { parseISO, format, set } from "date-fns";
import { useParams } from "react-router-dom";
import { useSelector } from "react-redux";
import socketio from "socket.io-client";
import Modal from "react-modal";
import { toast } from "react-toastify";
import sortArray from 'sort-array';


import host from '../../config/host';
import api from "../../util/api";
import history from "../../util/history";

import formatData from "./functions/formatData";
import filterDate from "./functions/filterDate";
import filterImpact from "./functions/filterImpact";
import filterTerms from "./functions/filterTerms";
import filterThemes from "./functions/filterThemes";

//OBS: 
//Todas as constantes declaradas com como (handle) tem como objetivo
//Manipular informações nos campos do formulario
//Basicamente repetindo processo de inserir ou limpa um campo

// importações do arquivo styles.js
import {
  TextContentDocument,
  BtnRemoveTerm,
  ColContentPreview,
  PreviewIco,
  TextContentPopUp,
  BoxPopUp,
  PopUp,
  EditDocButton,
  DivScrollListTable,
  TextContent,
  DivScrollSubject,
  ColContent,
  RowContent,
  TitleProgress,
  ContainContent,
  FormGrup,
  FormLabel,
  ColContent2,
  FormControl,
  SendButton,
  ColContentFilter,
  DivScroll,
  ButtonFilters,
  ColContentInter,
  ContractButton,
  ClosedModalButton,
  FormCheck,
  FucosTextContent,
  BtnAddSubject,
  TermButton,
  SeachContractInput,
  SeachContractToogle,
  SeachContractMenu,
  SeachContractItem,
  PreviewContainer,
  PreviewText,
  PreviewTextTitle,
  PreviewRow,
  RecordButton,
  DivScrollPopUp,
  DocumentName,
} from "./styles";

import impact_icofalse from "../../assets/images/false.png";
import impact_icotrue from "../../assets/images/true.png";
import LoadingGif from "../../assets/images/icoLoading.gif";
import IconContent from "../../assets/images/letter-ico.png";



function MinerarDoc() {

  const state = useSelector(state => state);

  const { user: { user: { id: userId } } } = state;
  const { contractselected } = state;

  const [loading, setLoading] = useState(false);

  const [previewContent, setPreviewContent] = useState({
    File: {
      ocr: ''
    }
  });
  const [openModalPreview, setOpenModalPreview] = useState(false);
  const [documentListRender, setDocumentListRender] = useState([]);
  const [documentList, setDocumentList] = useState([]);

  var lastContractSelected;
  const [textFilterContract, setFilterTextContract] = useState('');
  const [contractSelected, setContractSelected] = useState({
    name: '',
    id: 0
  });
  const [contractList, setContractList] = useState([]);
  const [contractListRender, setContractListRender] = useState([]);

  const [dateFinal, setDateFinal] = useState("");
  const [dateInitial, setDateInitial] = useState("");
  const [dateSelected, setDateSelected] = useState(false);

  const [typeSelected, setTypeSelected] = useState("todos");

  const [typeCorrespondenceSelected, setTypeCorrespondenceSelected] = useState(
    "todos"
  );
  const [correspondenceSelected, setCorrespondenceSelected] = useState(false);

  const [themes, setThemes] = useState([]);
  const [renderThemesSelect, setRenderThemesSelect] = useState([]);
  const [themesSelected, setThemesSelected] = useState([]);
  const [existTheme, setExisteTheme] = useState(false);

  const [impact, setImpact] = useState(false);

  const [terms, setTerms] = useState([]);
  const [existsTerm, setExistsTerm] = useState(false);
  const [renderTermContent, setRenderContent] = useState(false);
  const [reload, setReload] = useState(0);
  var positionTerm = 0;
  // var cont = 0;

  const [typeOrder, setTypeOrder] = useState('asc');
  const [documentOrder, setDocumentOrder] = useState('asc');
  const [dateOrder, setDateOrder] = useState('asc');
  const [codeOrder, setCodeOrder] = useState('asc');
  const [impactOrder, setImpactOrder] = useState('asc');

  const [nameFilterClick, setNameFilterClick] = useState(false);
  const [dateFilterClick, setDateFilterClick] = useState(false);
  const [codFilterClick, setCodFilterClick] = useState(false);
  const [typeFilterClick, setTypeFilterClick] = useState(false);
  const [typeImpactClick, setTypeImpactClick] = useState(false);

  const socket = useMemo(
    () =>
      socketio(host.ip, {
        query: {
          user: userId,
        },
      }),
    [userId]
  );

  useEffect(() => {
    socket.on("finish-process", (data) => {
      const { contract_name, name } = data;

      toast.info(
        `O OCR do arquivo "${name}" do contrato "${contract_name}" terminou de ser processado`
      );
    });
  }, [socket]);

  useMemo(() => {
    async function loadThemes() {
      const { data } = await api.get(`themes/list/${contractSelected.id}`);

      setThemes(data);
    };

    loadThemes();
  }, [contractSelected])

  useEffect(() => {
    async function loadDocuments() {
      if (contractselected.selected) {
        setLoading(true);

        const { id } = contractselected;

        const { data } = await api.get(`contract/list/subdocuments/${id}`);

        const formattedData = formatData(data);

        setDocumentList(formattedData);
        setDocumentListRender(formattedData);

        setLoading(false);
      };
    };

    async function loadContracts() {
      const { data } = await api.get('contract/list');

      const formattedContracts = data.map(index => {
        const { id, name } = index;

        return {
          id,
          name
        };
      });

      if (contractselected.selected) {
        const { id } = contractselected;

        const equal = formattedContracts.find(index => index.id == id);

        const { data } = await api.get(`themes/list/${id}`);

        setThemes(data);
        setContractSelected(equal);
      };

      setContractList(formattedContracts);
      setContractListRender(formattedContracts);
    };

    loadContracts();
    loadDocuments();
  }, [contractselected]);

  const handleSelectType = () => {
    if (typeSelected == "correspondencia") {
      setCorrespondenceSelected(true);
    } else {
      setCorrespondenceSelected(false);
    }
  };

  const handleButtonAddTheme = () => {
    setRenderThemesSelect([...renderThemesSelect, 1]);
  };

  const handleButtonClearThemes = () => {
    setRenderThemesSelect([]);
    setThemesSelected([]);
    setExisteTheme(false);
  };

  const handleSelectThemeSelectedChange = (data) => {
    const {
      target: { value },
    } = data;

    const readyExists = themesSelected.find((index) => index === value);

    if (value === "" || readyExists) {
      return;
    }

    setExisteTheme(true);
    setThemesSelected([...themesSelected, data.target.value]);
  };

  const handleChangePreview = (data) => {
    setPreviewContent(data);
    if (openModalPreview == false) {  
      setOpenModalPreview(true);
    } else {
      setOpenModalPreview(false);
    }

    if(terms[0]){
      setTimeout(() => {
        let { value: text } = terms[terms.length - 1];
        text = text.toLowerCase();
        const inputText = document.getElementById('preview_theme');
        var innerHTML = inputText.innerHTML;
        var index = innerHTML.toLowerCase().indexOf(text);
  
        if (index >= 0) { 
         innerHTML = innerHTML.substring(0,index) + "<span style='background-color:green; color:white;' class='highlight'>" + innerHTML.substring(index,index+text.length) + "</span>" + innerHTML.substring(index + text.length);
         inputText.innerHTML = innerHTML;
        }
      }, 100);
    }
  };

  const handleSelectTypeCorrespondenceChange = (data) => {
    setTypeCorrespondenceSelected(data.target.value);
  };

  const handleSelectTypeChange = (data) => {
    setTypeSelected(data.target.value);
  };

  const handleContractNameFilterChange = data => {
    const { target: { value } } = data;

    setFilterTextContract(value);

    const filterContracts = contractList.filter(
      index => index.name.toLowerCase().indexOf(value.toLowerCase()) > -1
    );

    setContractListRender(filterContracts);
  };
  const handleContractNameSelect = (data) => {
    setContractSelected(data);
  };
  //
  const handleInputImpactChange = () => {
    setImpact(!impact);
  };

  const handleInputDateInitialChange = (data) => {
    setDateInitial(data.target.value);
  };

  //Limpa os campos de data
  const handleButtonRmDate = (data) => {
    setDateInitial('');
    setDateFinal('');
  };

  const handleInputDateFinalChange = (data) => {
    setDateFinal(data.target.value);
  };

  const handleInputTermChange = (data) => {
    const {
      target: { id },
      target: { value },
    } = data;

    var arrayTerms = terms;
    arrayTerms[id].value = value;

    setTerms(arrayTerms);
  };

  const handleReturnTermsPosition = () => {
    var positionActual = positionTerm;
    positionTerm++;

    setTimeout(() => { setColor() }, 50);

    return positionActual;
  };

  const setColor = () => {
    const lastPositionTerms = terms.length - 1;

    const lastdocument = document.getElementById(lastPositionTerms);

    lastdocument.style.color = 'red';

    for (var i = lastPositionTerms - 1; i >= 0; i--) {
      const forlp = document.getElementById(i);

      forlp.style.color = 'black';
    };
  };


  const handleButtonRmTerm = (data) => {
    setTerms([]);
    setExistsTerm(false);
  };

  const handleButtonAddRenderTerm = data => {
    setTerms([...terms, { value: "" }]);
    setReload(reload + 1);
    setExistsTerm(true);
  };

  const handleButtonDownload = async () => {
    try {
      const { data } = await api.post('excel/generate', {
        name: contractSelected.name,
        documents: documentListRender
      });

      // console.log(data);
      window.location.href = `${host.ip}/${data}`

    } catch (err) {
      toast.error("Erro ao gerar o arquivo, contate um administrador!");
    };
  };

  const handleButtonTypeDocChange = data => {
    const { id, type } = data;
    
    switch (type) {
      case 'correspondencia':
        window.open(`${host.local}/edit-correspondence/${contractSelected.id}/${id}`);
        break;

      case 'ata':
        window.open(`${host.local}/edit-ata/${contractSelected.id}/${id}`);
        break;

      case 'proposta':
        window.open(`${host.local}/edit-prop/${id}`);
        break;

      default:
        window.open(`${host.local}/edit-anexo/${id}`);
        break;
    };
  };

  const handleClickFilterNameChange = (data) => {
    setNameFilterClick(true);
    setDateFilterClick(false);
    setCodFilterClick(false);
    setTypeFilterClick(false);
    setTypeImpactClick(false);
  };
  const handleClickFilterCodChange = (data) => {
    setNameFilterClick(false);
    setDateFilterClick(false);
    setCodFilterClick(true);
    setTypeFilterClick(false);
    setTypeImpactClick(false);
  };
  const handleClickFilterTypeChange = (data) => {
    setNameFilterClick(false);
    setDateFilterClick(false);
    setCodFilterClick(false);
    setTypeFilterClick(true);
    setTypeImpactClick(false);
  };
  const handleClickFilterDateChange = (data) => {
    setNameFilterClick(false);
    setDateFilterClick(true);
    setCodFilterClick(false);
    setTypeFilterClick(false);
    setTypeImpactClick(false);
  };

  const handleClickFilterImpactChange = (data) => {
    setNameFilterClick(false);
    setDateFilterClick(false);
    setCodFilterClick(false);
    setTypeFilterClick(false);
    setTypeImpactClick(true);
  };

  const handleSort = type => {
    var documentFilter;

    switch (type) {
      case 'date':
        if (dateOrder === 'asc') {
          documentFilter = sortArray(documentListRender, {
            by: 'formattedDate'
          });

          setDateOrder('desc');
        } else {
          documentFilter = sortArray(documentListRender, {
            by: 'formattedDate',
            order: 'desc'
          });

          setDateOrder('asc');
        }
        break;

      case 'type':
        if (typeOrder === 'asc') {
          documentFilter = sortArray(documentListRender, {
            by: 'type'
          });

          setTypeOrder('desc');
        } else {
          documentFilter = sortArray(documentListRender, {
            by: 'type',
            order: 'desc'
          });

          setTypeOrder('asc');
        }
        break;

      case 'code':
        if (codeOrder === 'asc') {
          documentFilter = sortArray(documentListRender, {
            by: 'code'
          });

          const documentCodeListWithContent = documentFilter.filter(index => index.code);
          const documentCodeListOutContent = documentFilter.filter(index => !index.code);

          documentFilter = [...documentCodeListWithContent, ...documentCodeListOutContent];

          setCodeOrder('desc');
        } else {
          documentFilter = sortArray(documentListRender, {
            by: 'code',
            order: 'desc'
          });

          const documentCodeListOutContent = documentFilter.filter(index => !index.code);
          const documentCodeListWithContent = documentFilter.filter(index => index.code);


          documentFilter = [...documentCodeListOutContent, ...documentCodeListWithContent];

          setCodeOrder('asc');
        }
        break;

      case 'name':
        if (documentOrder === 'asc') {
          documentFilter = sortArray(documentListRender, {
            by: 'name'
          });

          const documentNameListWithContent = documentFilter.filter(index => index.name);
          const documentNameListOutContent = documentFilter.filter(index => !index.name);

          documentFilter = [...documentNameListWithContent, ...documentNameListOutContent];

          setDocumentOrder('desc')
        } else {
          documentFilter = sortArray(documentListRender, {
            by: 'name',
            order: 'desc'
          });

          const documentNameListWithContent = documentFilter.filter(index => index.name);
          const documentNameListOutContent = documentFilter.filter(index => !index.name);

          documentFilter = [...documentNameListOutContent, ...documentNameListWithContent];

          setDocumentOrder('asc')
        }
        break;

      case 'impact':
        if (impactOrder == 'asc') {
          const documentWithImpact = documentListRender.filter(index => index.impact);
          const documentOutImpact = documentListRender.filter(index => !index.impact);

          documentFilter = [...documentWithImpact, ...documentOutImpact];

          setImpactOrder('desc');
        } else {
          const documentWithImpact = documentListRender.filter(index => index.impact);
          const documentOutImpact = documentListRender.filter(index => !index.impact);

          documentFilter = [...documentOutImpact, ...documentWithImpact];

          setImpactOrder('asc');
        }
        break;
    };

    setDocumentListRender([]);

    setTimeout(() => { setDocumentListRender(documentFilter) }, 20);
  };

  const handleFilter = async () => {
    var cachedDocumentList;

    async function callNewDocuments(id) {
      setLoading(true);
      const { data } = await api.get(`contract/list/subdocuments/${id}`);

      const formattedData = formatData(data);

      setDocumentList(formattedData);
      cachedDocumentList = formattedData;

      lastContractSelected = id;
    };


    if (contractSelected.id !== 0) {
      const { id } = contractSelected;

      await callNewDocuments(id);
    } else if (contractselected.selected === false) {
      return toast.error('É necessário selecionar previamente um contrato');
    } else if (contractselected.selected === true) {
      cachedDocumentList = documentList;
    };

    var documents;

    switch (typeSelected) {
      case "todos":
        documents = cachedDocumentList;
        break;

      case "contrato":
        documents = cachedDocumentList.filter((index) => index.type === "contrato");
        break;

      case "anexo":
        documents = cachedDocumentList.filter((index) => index.type === "anexo");
        break;

      case "aditivo":
        documents = cachedDocumentList.filter((index) => index.type === "aditivo");
        break;

      case "correspondencia":
        if (typeCorrespondenceSelected === "todos") {
          documents = cachedDocumentList.filter(
            (index) => index.type === "correspondencia"
          );
        } else if (typeCorrespondenceSelected === "email") {
          documents = cachedDocumentList.filter((index) => index.subtype === "email");
        } else if (typeCorrespondenceSelected === "enviada") {
          documents = cachedDocumentList.filter(
            (index) => index.subtype === "enviada"
          );
        } else if (typeCorrespondenceSelected === "recebida") {
          documents = cachedDocumentList.filter(
            (index) => index.subtype === "recebida"
          );
        }
        break;

      case "proposta":
        documents = cachedDocumentList.filter((index) => index.type === "proposta");
        break;

      case "ata":
        documents = cachedDocumentList.filter((index) => index.type === "ata");
        break;
    }

    if (dateInitial && dateFinal) {
      documents = filterDate(documents, dateInitial, dateFinal);
    }

    if (impact) {
      documents = filterImpact(documents);
    }

    if (existTheme) {
      documents = filterThemes(documents, themesSelected);
    }

    if (existsTerm) {
      documents = filterTerms(documents, terms);
      setRenderContent(true);
    } else {
      setRenderContent(false);
    }

    setDocumentListRender([]);

    console.log(documents);

    setTimeout(() => {
      setDocumentListRender(documents);
    }, 50);


    setLoading(false);
  };


  const [popInfo, setPopInfo] = useState('');
  const [popUp, setPopUp] = useState(false);


  const HandlerInfoOver = (data) => {
    setPopInfo(data);
  }

  return (
    <>
      <RowContent style={{ padding: "30px" }}>
        <Modal
          style={{
            overlay: {
              backgroundColor: "rgba(25,25,25, .5)",
            },
            content: {
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
              padding: "0",
              top: "0",
              bottom: "0",
              left: "0",
              right: "0",
              border: "none",
              backgroundColor: "rgba(25,25,25, .0)",
              margin: "0",
              borderRadius: "10px",
              height: "100vh",
              width: "100vw",
            },
          }}

          isOpen={loading}
        >
          <img src={LoadingGif} style={{ object: 'fill', height: '30vh' }} />
        </Modal>


        <Modal
          style={{
            overlay: {
              backgroundColor: "rgba(25,25,25, .5)",
            },
            content: {
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
              padding: "0",
              top: "0",
              bottom: "0",
              left: "0",
              right: "0",
              border: "none",
              backgroundColor: "rgba(25,25,25, .5)",
              margin: "0",
              borderRadius: "10px",
              height: "100vh",
              width: "100vw",
            },
          }}
          isOpen={openModalPreview}
          onRequestClose={() => {
            setOpenModalPreview(false);
          }}
        >
          <PreviewRow>
            <PreviewContainer className="col-md-6 col-12">
                <ClosedModalButton onClick={() => { setOpenModalPreview(false); }}>x</ClosedModalButton>
              <PreviewContainer className="col-md-12 col-12">
                <PreviewTextTitle>{previewContent.name}</PreviewTextTitle>
                <PreviewText id="preview_theme">{previewContent.preview ? previewContent.preview : previewContent.File.ocr}</PreviewText>
              </PreviewContainer>
            </PreviewContainer>
          </PreviewRow>
        </Modal>

        <ColContent2 className="col-md-12 col-sm-12 col-12">
          <TitleProgress>Minerar Documento:</TitleProgress>
          <ContainContent>
            <ContainContent>
              <FormGrup className="col-md-3 col-12">
                <FormLabel className="col-md-12 col-12">
                  Nome do contrato:
                </FormLabel>
                <SeachContractInput>
                  <SeachContractToogle
                    className="col-md-12 col-12"
                    id="dropdown-custom-components"
                  >
                    {contractSelected.name}
                  </SeachContractToogle>
                  <SeachContractMenu>
                    <FormControl
                      autoFocus
                      className="mx-3 my-2 w-auto"
                      placeholder="Consultar..."
                      onChange={handleContractNameFilterChange}
                      value={textFilterContract}
                    />

                    {/* {
                        textFilterContract && 
                      } */}

                    {contractListRender.map(index => (
                      <SeachContractItem className={index.name}
                        onClick={() => handleContractNameSelect(index)}
                      >
                        {index.name}
                      </SeachContractItem>
                    ))}
                  </SeachContractMenu>
                </SeachContractInput>
              </FormGrup>

              <FormGrup className="col-md-3 col-12">
                <FormLabel className="col-lg-12 col-md-12">De</FormLabel>
                <FormControl
                  required
                  className="col-lg-12 col-md-12 col-12"
                  type="date"
                  onChange={handleInputDateInitialChange}
                  value={dateInitial}
                />
              </FormGrup>
              <FormGrup className="col-md-3 col-12">
                <FormLabel className="col-lg-12 col-md-12">Ate</FormLabel>
                <FormControl
                  required
                  className="col-lg-12 col-md-12 col-12"
                  type="date"
                  onChange={handleInputDateFinalChange}
                  value={dateFinal}
                />
              </FormGrup>
              <FormGrup className="col-md-1 col-12">
                <FormLabel className="col-lg-12 col-md-12">Filtro data</FormLabel>
                <BtnRemoveTerm
                  className="m-0 col-md-12 col-12"
                  onClick={handleButtonRmDate}
                >
                  Limpar
              </BtnRemoveTerm>
              </FormGrup>
              <FormGrup className="col-md-2 col-12">
                {
                  documentListRender[0] &&
                  <>
                    <FormLabel className="col-lg-12 col-md-12">Gerador excel</FormLabel>
                    <RecordButton onClick={handleButtonDownload} >Download</RecordButton>
                  </>
                }
              </FormGrup>
              <FormGrup className="col-md-6 col-12 d-flex flex-column align-items-center justify-content-center">
                <FormCheck
                  type="switch"
                  id="custom-switch"
                  label="Ocorrência importante"
                  onChange={handleInputImpactChange}
                />
              </FormGrup>
              <FormGrup className="col-md-3 col-12">
                <FormLabel>Tipo do documento:</FormLabel>
                <FormControl
                  onChange={handleSelectTypeChange}
                  onClick={handleSelectType}
                  style={{
                    border: "solid #d1d1d1 1px",
                    padding: "5px 5px",
                    borderRadius: "5px",
                  }}
                  className="col-md-12 col-12"
                  as="select"
                >
                  <option value="todos">Todos</option>
                  <option value="contrato">Contrato</option>
                  <option value="anexo">Anexo do contrato</option>
                  <option value="aditivo">Aditivo do contrato</option>
                  <option value="proposta">Proposta Técnica</option>
                  <option value="correspondencia">Correspondencia</option>
                  <option value="ata">Ata de reunião</option>
                </FormControl>
              </FormGrup>
              {correspondenceSelected ? (
                <FormGrup className="col-md-3 col-12">
                  <FormLabel>Tipo da correspondencia:</FormLabel>
                  <FormControl
                    onChange={handleSelectTypeCorrespondenceChange}
                    style={{
                      border: "solid #d1d1d1 1px",
                      padding: "5px 5px",
                      borderRadius: "5px",
                    }}
                    value={typeCorrespondenceSelected}
                    className="col-md-12 col-12"
                    as="select"
                  >
                    <option value="todos">Todos</option>
                    <option value="email">E-mail</option>
                    <option value="enviada">Enviadas</option>
                    <option value="recebida">Recebidas</option>
                  </FormControl>
                </FormGrup>
              ) : (
                  <></>
                )}

              <FormGrup className="col-md-12 col-12">
                <FormLabel>Pesquisar por termos:</FormLabel>
                <DivScroll>
                  <RowContent>
                    {reload ? (
                      terms.map((document) => (
                        <>
                          <FormGrup className="col-md-3 col-12 d-flex my-2">
                            <input
                              id={handleReturnTermsPosition()}
                              className="col-lg-12 col-md-10 col-10"
                              type="text"
                              onChange={handleInputTermChange}
                              placeholder="insira aqui o termo"
                            />
                            {/* <BtnRemoveTerm 
                                  id={handleReturnTermsPosition()}
                                  onClick={handleButtonRmTerm} className="col-lg-2 col-md-2 col-2">X</BtnRemoveTerm> */}
                          </FormGrup>
                        </>
                      ))
                    ) : (
                        <></>
                      )}
                  </RowContent>
                </DivScroll>
              </FormGrup>

              {/* start component focus subject */}

              <FormGrup className="col-md-12 col-12">
                <FormLabel>
                  {renderThemesSelect.length > 0 && "Assunto Destaque:"}
                </FormLabel>
                <DivScrollSubject className="col-md-12 col-sm-12 col-12">
                  {renderThemesSelect.map((index) => (
                    <FormControl
                      onChange={handleSelectThemeSelectedChange}
                      style={{
                        border: "solid #d1d1d1 1px",
                        padding: "5px 5px",
                        borderRadius: "5px",
                        margin: "5px 0px",
                      }}
                      className="col-md-3 col-12"
                      as="select"
                    >
                      <option> </option>
                      {themes.map((data) => (
                        <option value={data.name}>{data.name}</option>
                      ))}
                    </FormControl>
                  ))}
                </DivScrollSubject>
              </FormGrup>

              {/* End component focus subject */}
            </ContainContent>
            <ContainContent className="col-md-12 col-12">
              <TermButton
                className="my-1 col-md-2 col-12"
                onClick={handleButtonAddRenderTerm}
              >
                Adicionar termo
              </TermButton>

              <BtnRemoveTerm
                className="my-1 col-md-2 col-12"
                onClick={handleButtonRmTerm}
              >
                Limpar termos
              </BtnRemoveTerm>

              <SendButton
                className="col-md-3 col-12"
                id="SubForm"
                name="btForm"
                type="button"
                onClick={handleFilter}
              >
                Minerar documento
              </SendButton>

              <BtnAddSubject
                className="my-1 col-md-2 col-12"
                onClick={handleButtonAddTheme}
              >
                Adicionar assunto destaque
              </BtnAddSubject>

              <BtnRemoveTerm
                className="my-1 col-md-2 col-12"
                onClick={handleButtonClearThemes}
              >
                Limpar assuntos destaque
              </BtnRemoveTerm>
            </ContainContent>
          </ContainContent>
        </ColContent2>
        <DivScrollListTable>
          <ColContentFilter className="col-lg-12 col-md-12 col-sm-12 col-12">
            <ColContent className="col-md-12">
              <ColContentInter className="col-md-12">
                <ColContent className="col-lg-2 col-md-2 col-sm-6 col-6">
                  <ButtonFilters onClick={() => { handleSort('type'); handleClickFilterTypeChange() }} style={{ fontWeight: typeFilterClick ? 'bold' : '400', color: typeFilterClick ? 'orange' : 'black', background: 'none', border: 'none' }}>Tipo:{typeFilterClick ? '^' : '-'}</ButtonFilters>
                </ColContent>
                <ColContent className="col-lg-1 col-md-1 col-sm-6 col-6 ">
                  <ButtonFilters onClick={() => { handleSort('name'); handleClickFilterNameChange() }} style={{ fontWeight: nameFilterClick ? 'bold' : '400', color: nameFilterClick ? 'orange' : 'black', background: 'none', border: 'none' }}>Documento:{nameFilterClick ? '^' : '-'}</ButtonFilters>
                </ColContent>
                <ColContent className="col-lg-1 col-md-1 col-sm-6 col-6">
                  <ButtonFilters onClick={() => { handleSort('date'); handleClickFilterDateChange() }} style={{ fontWeight: dateFilterClick ? 'bold' : '400', color: dateFilterClick ? 'orange' : 'black', background: 'none', border: 'none' }}>Data:{dateFilterClick ? '^' : '-'}</ButtonFilters>
                </ColContent>
                <ColContent className="col-lg-1 col-md-1 col-sm-6 col-6">
                  <ButtonFilters onClick={() => { handleSort('code'); handleClickFilterCodChange() }} style={{ fontWeight: codFilterClick ? 'bold' : '400', color: codFilterClick ? 'orange' : 'black', background: 'none', border: 'none' }}>Código:{codFilterClick ? '^' : '-'}</ButtonFilters>
                </ColContent>
                <ColContent className="col-lg-2 col-md-3 col-sm-2 col-6">
                  Assunto destaque:
              </ColContent>
                <ColContent className="col-lg-1 col-md-1 col-sm-6 col-6">
                  Remetente
              </ColContent>
                <ColContent className="col-lg-1 col-md-1 col-sm-6 col-6">
                  Destinatario
              </ColContent>
                <ColContent className="col-lg-1 col-md-1 col-sm-6 col-6">
                  Conteúdo
              </ColContent>
                <ColContent className="col-lg-1 col-md-1 col-sm-6 col-6">
                  <ButtonFilters onClick={() => { handleSort('impact'); handleClickFilterImpactChange() }} style={{ fontWeight: typeImpactClick ? 'bold' : '400', color: typeImpactClick ? 'orange' : 'black', background: 'none', border: 'none' }}>Impacto:{typeImpactClick ? '^' : '-'}</ButtonFilters>
                </ColContent>
                <ColContent className="col-lg-1 col-md-1 col-sm-6 col-6">
                  Editar
              </ColContent>
              </ColContentInter>

              {documentListRender.map((document) => (
                <ColContentInter className="col-md-12">
                  <ColContent className="col-lg-2 col-md-2 col-sm-6 col-6">
                    <TextContent>
                       <a href={`${host.ip}/files/${document.File.path}`} target="_blank">
                        {document.typeView ? document.typeView : document.type}</a> 
                    </TextContent>
                  </ColContent>
                  <ColContent className="col-lg-1 col-md-1 col-sm-6 col-6 ">
                    <TextContentDocument>{document.name}</TextContentDocument>
                  </ColContent>
                  <ColContent className="col-lg-1 col-md-1 col-sm-6 col-6">
                    <TextContent>{document.formattedDate}</TextContent>
                  </ColContent>
                  <ColContent className="col-lg-1 col-md-1 col-sm-6 col-6">
                    <TextContent> {document.code && document.code}</TextContent>
                  </ColContent>
                  <ColContent className="col-lg-2 col-md-2 col-sm-6 col-6">
                    <FucosTextContent>{document.themes}</FucosTextContent>
                  </ColContent>
                  <ColContent className="col-lg-1 col-md-1 col-sm-6 col-6">
                    <TextContent>{document.sender}</TextContent>
                  </ColContent>
                  <ColContent className="col-lg-1 col-md-1 col-sm-6 col-6">
                    <TextContent>{document.recipient}</TextContent>
                  </ColContent>

                  <ColContentPreview className="col-lg-1 col-md-1 col-sm-6 col-6">
                      <BoxPopUp onClick={() => handleChangePreview(document)}>
                      <PreviewIco
  
                      >
                        <PopUp>
                          <DivScrollPopUp>
                          <TextContentPopUp>
                            {document.preview ? document.preview : document.File.ocr}
                          </TextContentPopUp>
                          </DivScrollPopUp>
                        </PopUp>
                        <img width="30" height="30" src={IconContent} />
                      </PreviewIco>
                      </BoxPopUp>
                  </ColContentPreview>
                  <ColContent className="col-lg-1 col-md-1 col-sm-6 col-6">
                    <TextContent>
                      {document.impact ? (
                        <img width="20px" height="20px" src={impact_icotrue} />
                      ) : (
                          <img width="20px" height="20px" src={impact_icofalse} />
                        )}
                    </TextContent>
                  </ColContent>
                  <ColContent className="col-lg-1 col-md-1 col-sm-6 col-6">
                    <TextContent>
                      <EditDocButton
                        onClick={() => handleButtonTypeDocChange(document)}>
                        Editar
                    </EditDocButton>
                    </TextContent>
                  </ColContent>
                </ColContentInter>
              ))}
            </ColContent>
          </ColContentFilter>
        </DivScrollListTable>
      </RowContent>
    </>
  );
}

export default MinerarDoc;
